/**
 * Milestone Tracker Component
 * Visual progress indicator for user journeys
 */

import { MILESTONES } from '../core/widget-config.js';

export class MilestoneTracker {
  constructor(container, options = {}) {
    this.container = container;
    this.options = {
      onMilestoneClick: options.onMilestoneClick || (() => {}),
      showLabels: options.showLabels !== false,
      showProgress: options.showProgress !== false,
      compact: options.compact || false,
      interactive: options.interactive || false,
      ...options
    };
    
    this.currentMilestone = 0;
    this.journeyType = null;
    this.isVisible = false;
    this.milestones = [...MILESTONES];
    
    this.init();
  }
  
  /**
   * Initialize the milestone tracker
   */
  init() {
    this.render();
    this.attachEventListeners();
  }
  
  /**
   * Render the milestone tracker
   */
  render() {
    if (!this.container) return;
    
    this.container.innerHTML = `
      <div class="tlw-milestone-tracker ${this.options.compact ? 'tlw-milestone-tracker--compact' : ''}">
        ${this.options.showProgress ? this.renderProgressBar() : ''}
        <div class="tlw-milestone-steps">
          ${this.renderMilestones()}
        </div>
        ${this.renderJourneyIndicator()}
      </div>
    `;
  }
  
  /**
   * Render progress bar
   */
  renderProgressBar() {
    const progress = this.calculateProgress();
    
    return `
      <div class="tlw-milestone-progress">
        <div class="tlw-progress-bar">
          <div class="tlw-progress-fill" style="width: ${progress}%"></div>
        </div>
        <div class="tlw-progress-text">
          ${this.getProgressText()}
        </div>
      </div>
    `;
  }
  
  /**
   * Render individual milestones
   */
  renderMilestones() {
    return this.milestones.map((milestone, index) => {
      const status = this.getMilestoneStatus(index);
      const isClickable = this.options.interactive && status !== 'pending';
      
      return `
        <div class="tlw-milestone ${this.getMilestoneClasses(index, status)}"
             data-milestone="${milestone.id}"
             ${isClickable ? 'role="button" tabindex="0"' : ''}
             title="${milestone.description}">
          
          <div class="tlw-milestone-icon-container">
            <div class="tlw-milestone-icon">${milestone.icon}</div>
            ${status === 'active' ? '<div class="tlw-milestone-pulse"></div>' : ''}
            ${status === 'completed' ? '<div class="tlw-milestone-check"></div>' : ''}
          </div>
          
          ${this.options.showLabels ? `
            <div class="tlw-milestone-label">
              <span class="tlw-milestone-name">${milestone.name}</span>
              ${!this.options.compact ? `
                <span class="tlw-milestone-description">${milestone.description}</span>
              ` : ''}
            </div>
          ` : ''}
          
          ${index < this.milestones.length - 1 ? `
            <div class="tlw-milestone-connector ${status === 'completed' ? 'tlw-milestone-connector--completed' : ''}"></div>
          ` : ''}
        </div>
      `;
    }).join('');
  }
  
  /**
   * Render journey type indicator
   */
  renderJourneyIndicator() {
    if (!this.journeyType) return '';
    
    const journeyLabels = {
      'interactive': 'Interactive Journey',
      'template': 'Template Journey'
    };
    
    const journeyIcons = {
      'interactive': '',
      'template': ''
    };
    
    return `
      <div class="tlw-journey-indicator">
        <span class="tlw-journey-icon">${journeyIcons[this.journeyType] || ''}</span>
        <span class="tlw-journey-label">${journeyLabels[this.journeyType] || 'Journey'}</span>
      </div>
    `;
  }
  
  /**
   * Get milestone CSS classes
   */
  getMilestoneClasses(index, status) {
    const classes = ['tlw-milestone-item'];
    
    classes.push(`tlw-milestone--${status}`);
    
    if (this.options.interactive && status !== 'pending') {
      classes.push('tlw-milestone--clickable');
    }
    
    if (index === 0) {
      classes.push('tlw-milestone--first');
    }
    
    if (index === this.milestones.length - 1) {
      classes.push('tlw-milestone--last');
    }
    
    return classes.join(' ');
  }
  
  /**
   * Get milestone status
   */
  getMilestoneStatus(index) {
    if (index < this.currentMilestone) {
      return 'completed';
    } else if (index === this.currentMilestone) {
      return 'active';
    } else {
      return 'pending';
    }
  }
  
  /**
   * Calculate overall progress percentage
   */
  calculateProgress() {
    if (this.milestones.length === 0) return 0;
    return Math.round((this.currentMilestone / (this.milestones.length - 1)) * 100);
  }
  
  /**
   * Get progress text description
   */
  getProgressText() {
    const completed = this.currentMilestone;
    const total = this.milestones.length;
    const current = this.milestones[this.currentMilestone];
    
    if (completed === total - 1 && this.currentMilestone === total - 1) {
      return 'Journey Complete!';
    }
    
    return `Step ${completed + 1} of ${total}: ${current?.name || 'Unknown'}`;
  }
  
  /**
   * Attach event listeners
   */
  attachEventListeners() {
    if (!this.options.interactive) return;
    
    // Milestone click handlers
    this.container.querySelectorAll('.tlw-milestone--clickable').forEach(milestone => {
      milestone.addEventListener('click', (e) => {
        const milestoneId = parseInt(milestone.dataset.milestone);
        this.handleMilestoneClick(milestoneId, e);
      });
      
      // Keyboard support
      milestone.addEventListener('keydown', (e) => {
        if (e.key === 'Enter' || e.key === ' ') {
          e.preventDefault();
          const milestoneId = parseInt(milestone.dataset.milestone);
          this.handleMilestoneClick(milestoneId, e);
        }
      });
    });
  }
  
  /**
   * Handle milestone click
   */
  handleMilestoneClick(milestoneId, event) {
    const milestone = this.milestones.find(m => m.id === milestoneId);
    if (milestone) {
      this.options.onMilestoneClick(milestone, event);
    }
  }
  
  /**
   * Update current milestone
   */
  updateMilestone(milestone) {
    const newMilestone = typeof milestone === 'number' ? milestone : milestone.id;
    
    if (newMilestone !== this.currentMilestone) {
      this.currentMilestone = Math.max(0, Math.min(newMilestone, this.milestones.length - 1));
      this.render();
      this.attachEventListeners();
      
      // Animate milestone change
      this.animateMilestoneChange(newMilestone);
    }
  }
  
  /**
   * Set journey type
   */
  setJourneyType(journeyType) {
    if (this.journeyType !== journeyType) {
      this.journeyType = journeyType;
      this.render();
      this.attachEventListeners();
    }
  }
  
  /**
   * Animate milestone change
   */
  animateMilestoneChange(newMilestone) {
    const milestoneElements = this.container.querySelectorAll('.tlw-milestone-item');
    const targetElement = milestoneElements[newMilestone];
    
    if (targetElement) {
      // Add animation class
      targetElement.classList.add('tlw-milestone--just-completed');
      
      // Remove animation class after animation
      setTimeout(() => {
        targetElement.classList.remove('tlw-milestone--just-completed');
      }, 1000);
      
      // Scroll to milestone if needed
      if (this.options.autoScroll) {
        targetElement.scrollIntoView({ 
          behavior: 'smooth', 
          block: 'nearest',
          inline: 'center'
        });
      }
    }
  }
  
  /**
   * Show milestone tracker
   */
  show() {
    if (!this.isVisible) {
      this.container.style.display = 'block';
      this.container.classList.add('tlw-milestone-tracker--visible');
      this.isVisible = true;
    }
  }
  
  /**
   * Hide milestone tracker
   */
  hide() {
    if (this.isVisible) {
      this.container.classList.remove('tlw-milestone-tracker--visible');
      
      setTimeout(() => {
        this.container.style.display = 'none';
        this.isVisible = false;
      }, 300);
    }
  }
  
  /**
   * Reset to beginning
   */
  reset() {
    this.currentMilestone = 0;
    this.journeyType = null;
    this.render();
    this.attachEventListeners();
  }
  
  /**
   * Get current milestone info
   */
  getCurrentMilestone() {
    return {
      index: this.currentMilestone,
      milestone: this.milestones[this.currentMilestone],
      progress: this.calculateProgress(),
      journeyType: this.journeyType
    };
  }
  
  /**
   * Set custom milestones
   */
  setMilestones(customMilestones) {
    if (Array.isArray(customMilestones) && customMilestones.length > 0) {
      this.milestones = customMilestones;
      this.currentMilestone = Math.min(this.currentMilestone, this.milestones.length - 1);
      this.render();
      this.attachEventListeners();
    }
  }
  
  /**
   * Add milestone event listener
   */
  onMilestoneChange(callback) {
    if (typeof callback === 'function') {
      this.options.onMilestoneClick = callback;
    }
  }
  
  /**
   * Destroy the tracker
   */
  destroy() {
    if (this.container) {
      this.container.innerHTML = '';
    }
    this.isVisible = false;
  }
}

// CSS classes for styling (to be included in main stylesheet)
export const MILESTONE_TRACKER_STYLES = `
  .tlw-milestone-tracker {
    padding: 1rem;
    background: #f8f9fa;
    border-radius: 8px;
    margin-bottom: 1rem;
    opacity: 0;
    transform: translateY(-10px);
    transition: opacity 0.3s ease, transform 0.3s ease;
  }
  
  .tlw-milestone-tracker--visible {
    opacity: 1;
    transform: translateY(0);
  }
  
  .tlw-milestone-tracker--compact {
    padding: 0.5rem 1rem;
  }
  
  .tlw-milestone-progress {
    margin-bottom: 1rem;
  }
  
  .tlw-progress-bar {
    width: 100%;
    height: 4px;
    background: #e5e5e5;
    border-radius: 2px;
    overflow: hidden;
    margin-bottom: 0.5rem;
  }
  
  .tlw-progress-fill {
    height: 100%;
    background: linear-gradient(90deg, #007bff, #28a745);
    transition: width 0.5s ease;
    border-radius: 2px;
  }
  
  .tlw-progress-text {
    font-size: 0.9rem;
    color: #666;
    text-align: center;
  }
  
  .tlw-milestone-steps {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    position: relative;
    overflow-x: auto;
    padding: 0.5rem 0;
  }
  
  .tlw-milestone-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-width: 80px;
    position: relative;
    flex-shrink: 0;
    transition: all 0.3s ease;
  }
  
  .tlw-milestone--clickable {
    cursor: pointer;
  }
  
  .tlw-milestone--clickable:hover .tlw-milestone-icon-container {
    transform: scale(1.1);
  }
  
  .tlw-milestone-icon-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background: #e5e5e5;
    border: 3px solid #e5e5e5;
    transition: all 0.3s ease;
    margin-bottom: 0.5rem;
  }
  
  .tlw-milestone--pending .tlw-milestone-icon-container {
    background: #f8f9fa;
    border-color: #dee2e6;
    color: #adb5bd;
  }
  
  .tlw-milestone--active .tlw-milestone-icon-container {
    background: #007bff;
    border-color: #007bff;
    color: white;
    box-shadow: 0 0 0 4px rgba(0, 123, 255, 0.2);
  }
  
  .tlw-milestone--completed .tlw-milestone-icon-container {
    background: #28a745;
    border-color: #28a745;
    color: white;
  }
  
  .tlw-milestone-icon {
    font-size: 1.25rem;
    line-height: 1;
  }
  
  .tlw-milestone-pulse {
    position: absolute;
    top: -3px;
    left: -3px;
    right: -3px;
    bottom: -3px;
    border-radius: 50%;
    border: 2px solid #007bff;
    animation: tlw-pulse 2s infinite;
  }
  
  .tlw-milestone-check {
    position: absolute;
    top: -0.25rem;
    right: -0.25rem;
    width: 1.25rem;
    height: 1.25rem;
    background: #28a745;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.75rem;
    color: white;
    border: 2px solid white;
  }
  
  .tlw-milestone-label {
    text-align: center;
    max-width: 100px;
  }
  
  .tlw-milestone-name {
    display: block;
    font-size: 0.8rem;
    font-weight: 600;
    color: #333;
    margin-bottom: 0.25rem;
  }
  
  .tlw-milestone--pending .tlw-milestone-name {
    color: #adb5bd;
  }
  
  .tlw-milestone--active .tlw-milestone-name {
    color: #007bff;
  }
  
  .tlw-milestone--completed .tlw-milestone-name {
    color: #28a745;
  }
  
  .tlw-milestone-description {
    display: block;
    font-size: 0.7rem;
    color: #666;
    line-height: 1.2;
  }
  
  .tlw-milestone-connector {
    position: absolute;
    top: 1.5rem;
    left: calc(100% - 1rem);
    width: 2rem;
    height: 2px;
    background: #dee2e6;
    transition: background 0.3s ease;
  }
  
  .tlw-milestone-connector--completed {
    background: #28a745;
  }
  
  .tlw-journey-indicator {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    margin-top: 1rem;
    padding-top: 1rem;
    border-top: 1px solid #e5e5e5;
    font-size: 0.9rem;
    color: #666;
  }
  
  .tlw-journey-icon {
    font-size: 1.1rem;
  }
  
  .tlw-milestone--just-completed {
    animation: tlw-milestone-complete 1s ease;
  }
  
  @keyframes tlw-pulse {
    0% { transform: scale(1); opacity: 1; }
    50% { transform: scale(1.1); opacity: 0.7; }
    100% { transform: scale(1); opacity: 1; }
  }
  
  @keyframes tlw-milestone-complete {
    0% { transform: scale(1); }
    50% { transform: scale(1.2); }
    100% { transform: scale(1); }
  }
  
  /* Responsive adjustments */
  @media (max-width: 768px) {
    .tlw-milestone-steps {
      justify-content: flex-start;
      gap: 1rem;
    }
    
    .tlw-milestone-item {
      min-width: 60px;
    }
    
    .tlw-milestone-icon-container {
      width: 2.5rem;
      height: 2.5rem;
    }
    
    .tlw-milestone-icon {
      font-size: 1rem;
    }
    
    .tlw-milestone-name {
      font-size: 0.75rem;
    }
    
    .tlw-milestone-description {
      display: none;
    }
  }
`;

export default MilestoneTracker;