/**
 * Message Renderers
 * Specialized renderers for different message types
 */

/**
 * Base Message Renderer
 */
export class BaseMessageRenderer {
  constructor(message) {
    this.message = message;
    this.timestamp = new Date(message.timestamp).toLocaleTimeString();
  }
  
  render() {
    return `
      <div class="tlw-message tlw-message--${this.message.type}" data-message-id="${this.message.id}">
        ${this.renderContent()}
        ${this.renderFooter()}
      </div>
    `;
  }
  
  renderContent() {
    return `
      <div class="tlw-message-content">
        ${this.message.content || ''}
      </div>
    `;
  }
  
  renderFooter() {
    return `
      <div class="tlw-message-footer">
        <span class="tlw-message-timestamp">${this.timestamp}</span>
        ${this.renderActions()}
      </div>
    `;
  }
  
  renderActions() {
    return '';
  }
}

/**
 * User Message Renderer
 */
export class UserMessageRenderer extends BaseMessageRenderer {
  render() {
    return `
      <div class="tlw-message tlw-message--user" data-message-id="${this.message.id}">
        <div class="tlw-message-bubble">
          <div class="tlw-message-content">
            ${this.formatContent(this.message.content)}
          </div>
          <div class="tlw-message-timestamp">${this.timestamp}</div>
        </div>
      </div>
    `;
  }
  
  formatContent(content) {
    // Simple markdown-like formatting
    return content
      .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
      .replace(/\*(.*?)\*/g, '<em>$1</em>')
      .replace(/\n/g, '<br>');
  }
}

/**
 * AI Text Message Renderer
 */
export class TextMessageRenderer extends BaseMessageRenderer {
  render() {
    return `
      <div class="tlw-message tlw-message--assistant" data-message-id="${this.message.id}">
        <div class="tlw-message-header">
          <div class="tlw-message-avatar"></div>
          <div class="tlw-message-info">
            <span class="tlw-message-sender">TradrLab AI</span>
            <span class="tlw-message-timestamp">${this.timestamp}</span>
          </div>
        </div>
        <div class="tlw-message-content">
          ${this.formatContent(this.message.content)}
        </div>
        ${this.renderActions()}
      </div>
    `;
  }
  
  formatContent(content) {
    // Enhanced markdown formatting
    return content
      .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
      .replace(/\*(.*?)\*/g, '<em>$1</em>')
      .replace(/`(.*?)`/g, '<code>$1</code>')
      .replace(/\n\n/g, '</p><p>')
      .replace(/\n/g, '<br>')
      .replace(/^(.+)$/, '<p>$1</p>');
  }
  
  renderActions() {
    return `
      <div class="tlw-message-actions">
        <button class="tlw-action-btn tlw-copy-btn" data-action="copy" title="Copy message">
          
        </button>
        <button class="tlw-action-btn tlw-feedback-btn" data-action="feedback-good" title="Helpful">
          
        </button>
        <button class="tlw-action-btn tlw-feedback-btn" data-action="feedback-bad" title="Not helpful">
          
        </button>
      </div>
    `;
  }
}

/**
 * Steps Message Renderer (for step-by-step guidance)
 */
export class StepsMessageRenderer extends BaseMessageRenderer {
  render() {
    const steps = this.parseSteps(this.message.content);
    
    return `
      <div class="tlw-message tlw-message--steps" data-message-id="${this.message.id}">
        <div class="tlw-message-header">
          <div class="tlw-message-avatar"></div>
          <div class="tlw-message-info">
            <span class="tlw-message-sender">Step-by-Step Guide</span>
            <span class="tlw-message-timestamp">${this.timestamp}</span>
          </div>
        </div>
        <div class="tlw-steps-content">
          ${this.renderSteps(steps)}
        </div>
        ${this.renderActions()}
      </div>
    `;
  }
  
  parseSteps(content) {
    // Try to parse numbered steps from content
    const lines = content.split('\n');
    const steps = [];
    
    lines.forEach(line => {
      const stepMatch = line.match(/^(\d+)\.\s*(.+)/);
      if (stepMatch) {
        steps.push({
          number: parseInt(stepMatch[1]),
          text: stepMatch[2]
        });
      }
    });
    
    return steps.length > 0 ? steps : [{ number: 1, text: content }];
  }
  
  renderSteps(steps) {
    return `
      <ol class="tlw-steps-list">
        ${steps.map(step => `
          <li class="tlw-step-item">
            <div class="tlw-step-number">${step.number}</div>
            <div class="tlw-step-text">${step.text}</div>
          </li>
        `).join('')}
      </ol>
    `;
  }
}

/**
 * Template Message Renderer
 */
export class TemplateMessageRenderer extends BaseMessageRenderer {
  render() {
    return `
      <div class="tlw-message tlw-message--template" data-message-id="${this.message.id}">
        <div class="tlw-template-header">
          <div class="tlw-template-icon"></div>
          <div class="tlw-template-info">
            <h4>Scenario Template Ready</h4>
            <span class="tlw-template-name">${this.message.templateName || 'Custom Template'}</span>
          </div>
        </div>
        <div class="tlw-template-content">
          ${this.formatContent(this.message.content)}
        </div>
        <div class="tlw-template-actions">
          <button class="tlw-btn tlw-btn--primary" data-action="accept-template">
            Generate Model
          </button>
          <button class="tlw-btn tlw-btn--secondary" data-action="modify-template">
            Modify Template
          </button>
        </div>
        <div class="tlw-message-timestamp">${this.timestamp}</div>
      </div>
    `;
  }
  
  formatContent(content) {
    return content
      .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
      .replace(/\*(.*?)\*/g, '<em>$1</em>')
      .replace(/\n/g, '<br>');
  }
}

/**
 * Model Message Renderer (DAG Generated)
 */
export class ModelMessageRenderer extends BaseMessageRenderer {
  render() {
    const scenarioId = this.message.scenarioId;
    const hasError = this.message.statusCode && this.message.statusCode !== '200';
    
    return `
      <div class="tlw-message tlw-message--model" data-message-id="${this.message.id}">
        <div class="tlw-model-header">
          <div class="tlw-model-icon">${hasError ? '' : ''}</div>
          <div class="tlw-model-info">
            <h4>${hasError ? 'Model Generation Failed' : 'Model Generated Successfully'}</h4>
            ${scenarioId ? `<span class="tlw-scenario-id">Scenario ID: ${scenarioId}</span>` : ''}
          </div>
        </div>
        
        ${hasError ? this.renderError() : this.renderSuccess()}
        
        <div class="tlw-message-timestamp">${this.timestamp}</div>
      </div>
    `;
  }
  
  renderSuccess() {
    return `
      <div class="tlw-model-content">
        <p> Your trading scenario has been created and is being evaluated against market data...</p>
        <div class="tlw-model-status">
          <div class="tlw-progress-indicator">
            <div class="tlw-spinner"></div>
            <span>Running evaluation...</span>
          </div>
        </div>
      </div>
      <div class="tlw-model-actions">
        <button class="tlw-btn tlw-btn--secondary" data-action="view-model">
          View Model Details
        </button>
      </div>
    `;
  }
  
  renderError() {
    return `
      <div class="tlw-model-content tlw-error-content">
        <p>There was an issue generating your trading model. Please try again or modify your request.</p>
        ${this.message.content ? `<div class="tlw-error-details">${this.message.content}</div>` : ''}
      </div>
      <div class="tlw-model-actions">
        <button class="tlw-btn tlw-btn--primary" data-action="retry-model">
          Try Again
        </button>
      </div>
    `;
  }
}

/**
 * Analysis Message Renderer (with tables)
 */
export class AnalysisMessageRenderer extends BaseMessageRenderer {
  render() {
    const hasTable = this.message.tableJson || this.message.tableMarkdown;
    
    return `
      <div class="tlw-message tlw-message--analysis" data-message-id="${this.message.id}">
        <div class="tlw-message-header">
          <div class="tlw-message-avatar"></div>
          <div class="tlw-message-info">
            <span class="tlw-message-sender">Analysis Results</span>
            <span class="tlw-message-timestamp">${this.timestamp}</span>
          </div>
        </div>
        
        <div class="tlw-analysis-content">
          ${this.formatContent(this.message.content)}
        </div>
        
        ${hasTable ? this.renderTable() : ''}
        
        ${this.renderActions()}
      </div>
    `;
  }
  
  renderTable() {
    if (this.message.tableJson) {
      return this.renderJsonTable();
    } else if (this.message.tableMarkdown) {
      return this.renderMarkdownTable();
    }
    return '';
  }
  
  renderJsonTable() {
    try {
      const tableData = JSON.parse(this.message.tableJson);
      
      if (!Array.isArray(tableData) || tableData.length === 0) {
        return '<div class="tlw-table-empty">No data available</div>';
      }
      
      const headers = Object.keys(tableData[0]);
      const maxRows = 10;
      const displayData = tableData.slice(0, maxRows);
      const hasMoreRows = tableData.length > maxRows;
      
      return `
        <div class="tlw-table-container">
          <table class="tlw-data-table">
            <thead>
              <tr>
                ${headers.map(header => `<th>${header}</th>`).join('')}
              </tr>
            </thead>
            <tbody>
              ${displayData.map(row => `
                <tr>
                  ${headers.map(header => `<td>${this.formatCellValue(row[header])}</td>`).join('')}
                </tr>
              `).join('')}
            </tbody>
          </table>
          
          <div class="tlw-table-controls">
            ${hasMoreRows ? `
              <button class="tlw-btn tlw-btn--secondary" data-action="expand-table">
                Show All (${tableData.length} rows)
              </button>
            ` : ''}
            <button class="tlw-btn tlw-btn--secondary" data-action="export-csv">
              Export CSV
            </button>
            <button class="tlw-btn tlw-btn--secondary" data-action="open-full-screen">
              View in Main App
            </button>
          </div>
        </div>
      `;
    } catch (error) {
      return '<div class="tlw-table-error">Error parsing table data</div>';
    }
  }
  
  renderMarkdownTable() {
    // Simple markdown table rendering
    return `
      <div class="tlw-markdown-table">
        ${this.message.tableMarkdown}
      </div>
    `;
  }
  
  formatCellValue(value) {
    if (value === null || value === undefined) {
      return '-';
    }
    
    // Format numbers
    if (typeof value === 'number') {
      if (value % 1 === 0) {
        return value.toLocaleString();
      } else {
        return value.toLocaleString(undefined, { maximumFractionDigits: 4 });
      }
    }
    
    // Format percentages
    if (typeof value === 'string' && value.includes('%')) {
      return value;
    }
    
    return String(value);
  }
  
  formatContent(content) {
    return content
      .replace(/\*\*(.*?)\*\*/g, '<strong>$1</strong>')
      .replace(/\*(.*?)\*/g, '<em>$1</em>')
      .replace(/`(.*?)`/g, '<code>$1</code>')
      .replace(/\n\n/g, '</p><p>')
      .replace(/\n/g, '<br>')
      .replace(/^(.+)$/, '<p>$1</p>');
  }
  
  renderActions() {
    return `
      <div class="tlw-message-actions">
        <button class="tlw-action-btn" data-action="ask-follow-up" title="Ask follow-up question">
           Ask More
        </button>
        <button class="tlw-action-btn" data-action="copy" title="Copy results">
          
        </button>
        <button class="tlw-action-btn tlw-feedback-btn" data-action="feedback-good" title="Helpful">
          
        </button>
        <button class="tlw-action-btn tlw-feedback-btn" data-action="feedback-bad" title="Not helpful">
          
        </button>
      </div>
    `;
  }
}

