/**
 * System Message Renderer
 */
export class SystemMessageRenderer extends BaseMessageRenderer {
  render() {
    return `
      <div class="tlw-message tlw-message--system" data-message-id="${this.message.id}">
        <div class="tlw-system-content">
          <span class="tlw-system-icon"></span>
          ${this.message.content}
        </div>
        <div class="tlw-message-timestamp">${this.timestamp}</div>
      </div>
    `;
  }
}

/**
 * Message Renderer Factory
 */
export class MessageRenderer {
  static create(message) {
    const messageType = message.messageType || message.type;
    
    switch (messageType) {
      case 'user':
        return new UserMessageRenderer(message);
      
      case 'Text':
      case 'assistant':
        return new TextMessageRenderer(message);
      
      case 'Steps':
        return new StepsMessageRenderer(message);
      
      case 'Template':
      case 'TemplateSelection':
        return new TemplateMessageRenderer(message);
      
      case 'Model':
        return new ModelMessageRenderer(message);
      
      case 'Analysis':
        return new AnalysisMessageRenderer(message);
      
      case 'Scenario':
        return new ScenarioMessageRenderer(message);
      
      case 'Explanation':
        return new ExplanationMessageRenderer(message);
      
      case 'error':
        return new ErrorMessageRenderer(message);
      
      case 'ExecutionError':
        return new ExecutionErrorMessageRenderer(message);
      
      case 'system':
        return new SystemMessageRenderer(message);
      
      default:
        // Fallback to text renderer
        return new TextMessageRenderer(message);
    }
  }
}

export default MessageRenderer;