/**
 * Action Manager
 * Manages available actions and button visibility based on conversation state
 */

export class ActionManager {
  constructor(messageStore, apiClient, thinkingManager) {
    this.messageStore = messageStore;
    this.apiClient = apiClient;
    this.thinkingManager = thinkingManager;
    this.listeners = [];
    this.pendingRequests = new Map(); // Track pending DAG generation requests
  }
  
  /**
   * Get all currently available actions based on message history and state
   */
  getAvailableActions() {
    const actions = [];
    const currentActions = this.messageStore.getCurrentAvailableActions();
    const milestoneStatus = this.messageStore.getMilestoneStatus();
    
    // Generate Scenario button
    if (currentActions.showGenerateButton) {
      actions.push({
        id: 'generate-scenario',
        label: ' Generate Scenario',
        description: 'Build your trading model',
        action: () => this.handleGenerateScenario(),
        primary: true,
        visible: true,
        disabled: false
      });
    }
    
    // Redraw button (for existing scenarios)
    if (currentActions.showRedrawButton) {
      actions.push({
        id: 'redraw-scenario',
        label: ' Redraw Scenario',
        description: 'Regenerate with same prompt',
        action: () => this.handleRedrawScenario(),
        primary: false,
        visible: true,
        disabled: false
      });
    }
    
    // Run Backtest button (after evaluation)
    if (currentActions.showBacktestButton) {
      actions.push({
        id: 'run-backtest',
        label: ' Run Backtest',
        description: 'Test strategy performance',
        action: () => this.handleRunBacktest(),
        primary: true,
        visible: true,
        disabled: false
      });
    }
    
    // Analysis options (after evaluation)
    if (currentActions.enableAnalysis) {
      actions.push({
        id: 'ask-question',
        label: ' Ask Question',
        description: 'Analyze your results',
        action: () => this.handleAskQuestion(),
        primary: false,
        visible: true,
        disabled: false
      });
    }
    
    
    return actions;
  }
  
  /**
   * Handle Generate Scenario action
   * Pass the message object directly like the original app
   */
  async handleGenerateScenario(message) {
    console.log('ActionManager: Generating scenario');
    
    if (!message) {
      throw new Error('ActionManager: No message provided for scenario generation');
    }
    
    // Start thinking indicator for model creation with timeout handling
    const requestId = `query-${Date.now()}`;
    console.log(' ActionManager: Starting thinking indicator for query with timeout', { requestId });
    
    // Define timeout handler for DAG generation
    const handleDagTimeout = (reqId, shouldRetry) => {
      console.warn(` ActionManager: DAG generation request ${reqId} timed out`);
      
      // Add timeout error message
      this.messageStore.addMessage({
        id: `dag-timeout-${Date.now()}`,
        type: 'system',
        messageType: 'Error',
        content: ` **Model Generation Timed Out**\n\nThe AI engine did not respond within the expected time.\n\n${shouldRetry ? 'Retrying...' : 'Please try generating the model again or simplify your request.'}`,
        timestamp: Date.now()
      });
      
      // If retry was requested, trigger retry
      if (shouldRetry) {
        console.log(' ActionManager: Retrying DAG generation after timeout');
        setTimeout(() => {
          this.handleGenerateScenario();
        }, 1000);
      }
    };
    
    // Start thinking with 90 second timeout (DAG generation can take longer)
    this.thinkingManager.startThinking(requestId, '/query', null, handleDagTimeout, 90000);
    
    try {
      // Extract content from message exactly like original app does
      // Original app uses message.message which contains the raw aiResponse
      let prompt = message.aiResponse || message.message || message.content;
      
      if (!prompt || !prompt.trim()) {
        throw new Error('ActionManager: Message has no content for scenario generation');
      }
      
      // NO CLEANING - send raw content exactly like original app
      console.log('ActionManager: Using raw message content for DAG generation:', prompt.substring(0, 100) + '...');
      
      // Call the generateScenario method exactly like original app
      // Use the current session ID from previous AI responses (like original app)
      const sessionId = this.messageStore.getCurrentSessionId();
      
      // CRITICAL: Let apiClient handle ScenarioId - it MUST exist from previous chat
      // This ensures GetScenarioLastSessionId() can find the saved messages
      try {
        const response = await this.apiClient.generateScenario({
          conversationContext: prompt,
          // No scenarioId passed - apiClient will use stored one or throw error
          sessionId: sessionId, // Use conversation session ID for context continuity
          socketId: this.getSocketId()
        });
        
        console.log('ActionManager: Scenario generation initiated', {
          requestId: response.requestId,
          scenarioId: response.scenarioId
        });
        
        return response;
        
      } catch (error) {
        this.thinkingManager.stopThinking(requestId);
        
        // Provide user-friendly error message
        if (error.message.includes('ScenarioId is required')) {
          throw new Error('Please send at least one chat message before generating a scenario. The system needs conversation context to create your trading model.');
        } else if (error.message.includes('SessionId')) {
          throw new Error('Please wait for the previous message to complete before generating a scenario.');
        } else {
          throw error; // Re-throw other errors as-is
        }
      }
      
    } catch (error) {
      console.error('ActionManager: Generate scenario failed', error);
      this.thinkingManager.stopThinking(requestId);
      throw error;
    }
  }
  
  /**
   * Handle Redraw Scenario action
   */
  async handleRedrawScenario() {
    console.log('ActionManager: Redrawing scenario');
    
    const lastRelevantMessage = this.messageStore.getLastRelevantMessage();
    if (!lastRelevantMessage?.userDagRequestId) {
      throw new Error('No DAG request ID found for redraw');
    }
    
    try {
      const requestId = `redraw-${Date.now()}`;
      this.thinkingManager.startThinking(requestId, '/query');
      
      const response = await this.apiClient.get(
        `/api/widget/dag/redraw/${lastRelevantMessage.userDagRequestId}/${this.getSocketId()}/${lastRelevantMessage.scenarioId}`
      );
      
      console.log('ActionManager: Scenario redraw initiated', response);
      
      return response;
      
    } catch (error) {
      console.error('ActionManager: Redraw scenario failed', error);
      throw error;
    }
  }
  
  /**
   * Handle Run Backtest action
   */
  async handleRunBacktest() {
    console.log('ActionManager: Running backtest');
    
    const currentScenario = this.getCurrentScenario();
    if (!currentScenario) {
      throw new Error('No active scenario for backtest');
    }
    
    try {
      const requestId = `backtest-${Date.now()}`;
      this.thinkingManager.startThinking(requestId, 'backtest', ' Starting backtest...');
      
      const response = await this.apiClient.post('/api/widget/backtest', {
        scenarioId: currentScenario.id,
        externalUserId: this.getExternalUserId(),
        socketId: this.getSocketId()
      });
      
      console.log('ActionManager: Backtest initiated', response);
      
      // Update milestone
      this.messageStore.updateMilestone('backtest');
      
      return response;
      
    } catch (error) {
      console.error('ActionManager: Backtest failed', error);
      throw error;
    }
  }
  
  /**
   * Handle Ask Question action (opens analysis input)
   */
  handleAskQuestion() {
    console.log('ActionManager: Opening analysis question input');
    
    // This could trigger a modal or inline input
    // For now, we'll emit an event that the UI can listen to
    this.notifyListeners('action:ask-question', {
      scenario: this.getCurrentScenario()
    });
  }
  
  /**
   * Send analysis question
   */
  async sendAnalysisQuestion(question) {
    console.log('ActionManager: Sending analysis question', question);
    
    const currentScenario = this.getCurrentScenario();
    if (!currentScenario) {
      throw new Error('No active scenario for analysis');
    }
    
    try {
      const requestId = `analysis-${Date.now()}`;
      this.thinkingManager.startThinking(requestId, 'analysis');
      
      // SessionId MUST come from current conversation for context continuity
      const sessionId = this.messageStore.getCurrentSessionId();
      if (!sessionId) {
        throw new Error('ActionManager: No SessionId available for analysis - requires previous chat messages');
      }
      
      const response = await this.apiClient.post('/api/widget/analysis', {
        scenarioId: currentScenario.id,
        question: question,
        externalUserId: this.getExternalUserId(),
        sessionId: sessionId,
        socketId: this.getSocketId()
      });
      
      console.log('ActionManager: Analysis question sent', response);
      
      return response;
      
    } catch (error) {
      console.error('ActionManager: Analysis question failed', error);
      this.thinkingManager.stopThinking(requestId);
      throw error;
    }
  }
  
  
  /**
   * Get current active scenario
   */
  getCurrentScenario() {
    const scenarios = Array.from(this.messageStore.scenarios.values());
    return scenarios.find(s => s.id === this.messageStore.activeScenarioId) || 
           scenarios[scenarios.length - 1]; // Most recent
  }
  
  /**
   * Generate unique ID
   */
  generateId() {
    // Generate MongoDB ObjectId-compatible 24-character hex string
    const timestamp = Math.floor(Date.now() / 1000).toString(16).padStart(8, '0');
    const randomHex = Array.from({length: 16}, () => Math.floor(Math.random() * 16).toString(16)).join('');
    return timestamp + randomHex;
  }
  
  /**
   * Get external user ID from config
   */
  getExternalUserId() {
    // This would come from widget configuration
    return 'widget-user-' + Date.now();
  }
  
  /**
   * Get SignalR socket ID
   */
  getSocketId() {
    // This would come from SignalR client
    return 'socket-' + Date.now();
  }
  
  // REMOVED: getScenarioId() method - no fallbacks, ApiClient handles ScenarioId consistency
  
  /**
   * Add event listener
   */
  on(event, callback) {
    this.listeners.push({ event, callback });
  }
  
  /**
   * Notify listeners of events
   */
  notifyListeners(event, data) {
    this.listeners
      .filter(listener => listener.event === event)
      .forEach(listener => listener.callback(data));
  }
  
  /**
   * Check if any actions are currently disabled (waiting for responses)
   */
  areActionsDisabled() {
    return this.thinkingManager.getActiveCount() > 0;
  }
  
  /**
   * Get action by ID
   */
  getAction(actionId) {
    return this.getAvailableActions().find(action => action.id === actionId);
  }
  
  /**
   * Execute action by ID
   */
  async executeAction(actionId, ...args) {
    const action = this.getAction(actionId);
    
    if (!action) {
      throw new Error(`Action ${actionId} not found`);
    }
    
    if (action.disabled) {
      throw new Error(`Action ${actionId} is disabled`);
    }
    
    return await action.action(...args);
  }
}