/**
 * Configuration Manager
 * Handles merging and validation of widget configuration
 */

export class ConfigManager {
  constructor(globalConfig = {}, widgetConfig = {}) {
    this.config = this.mergeConfigs(globalConfig, widgetConfig);
    this.validateConfig();
  }
  
  /**
   * Merge global and widget-specific configurations
   */
  mergeConfigs(global, widget) {
    // Use production URLs if not in development mode
    const isDev = window.location.hostname === 'localhost' || window.location.hostname === '127.0.0.1';
    
    const defaults = {
      // API Configuration - will be overridden by user config
      apiBaseUrl: isDev ? 'https://localhost:44358' : 'https://localhost:44358', // Default to localhost for testing
      signalRUrl: isDev ? 'https://localhost:44358/hubs/message' : 'https://localhost:44358/hubs/message', // Default to localhost for testing
      locale: 'en-US',
      debug: false,
      
      // Appearance
      theme: 'light',
      width: '400px',
      height: '600px',
      fontFamily: '-apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, sans-serif',
      primaryColor: '#1976d2',
      enableFontSizeControl: true,
      
      // Features
      placeholder: 'Type your message...',
      welcomeMessage: null,
      allowImageUpload: true,
      allowImagePaste: true,
      allowReply: true,
      allowFeedback: true,
      showTables: true,
      enableTableResize: true,
      enableTableModal: true,
      autoExpand: false,
      
      // Message Types
      supportedMessageTypes: ['Text', 'Steps', 'Template', 'Scenario', 'Analysis', 'Explanation'],
      enableTemplateAutoTrigger: false,
      showEvaluationButtons: false,
      
      // Rate Limiting
      showRateLimitWarnings: true,
      rateLimitBehavior: 'warn', // 'queue' | 'reject' | 'warn'
      rateLimits: {
        maxDagRequests: 10,
        maxChatRequests: 100
      },
      
      // Integration
      scenarioId: null,
      userId: null,
      sessionId: null,
      
      // Widget User Configuration
      externalUserId: null, // Required: Third-party's user ID
      userDisplayName: null, // Optional: Display name for the user
      userMetadata: {}, // Optional: Additional user metadata
      jwtToken: null, // Set automatically during initialization
      tokenExpires: null, // Set automatically during initialization
      
      // Events (callbacks)
      onMessageSent: null,
      onMessageReceived: null,
      onImageUploaded: null,
      onFeedbackProvided: null,
      onTableExpanded: null,
      onRateLimitReached: null,
      onWidgetReady: null,
      onError: null
    };
    
    return {
      ...defaults,
      ...global,
      ...widget,
      // Ensure rateLimits are properly merged
      rateLimits: {
        ...defaults.rateLimits,
        ...(global.rateLimits || {}),
        ...(widget.rateLimits || {})
      }
    };
  }
  
  /**
   * Validate configuration
   */
  validateConfig() {
    // Required fields
    if (!this.config.apikey) {
      throw new Error('ConfigManager: API key is required');
    }
    
    // Validate theme
    const validThemes = ['light', 'dark', 'custom'];
    if (!validThemes.includes(this.config.theme)) {
      console.warn(`ConfigManager: Invalid theme "${this.config.theme}". Using "light".`);
      this.config.theme = 'light';
    }
    
    // Validate rate limit behavior
    const validBehaviors = ['queue', 'reject', 'warn'];
    if (!validBehaviors.includes(this.config.rateLimitBehavior)) {
      console.warn(`ConfigManager: Invalid rateLimitBehavior "${this.config.rateLimitBehavior}". Using "warn".`);
      this.config.rateLimitBehavior = 'warn';
    }
    
    // Validate callbacks
    const callbacks = [
      'onMessageSent', 'onMessageReceived', 'onImageUploaded',
      'onFeedbackProvided', 'onTableExpanded', 'onRateLimitReached',
      'onWidgetReady', 'onError'
    ];
    
    callbacks.forEach(callback => {
      if (this.config[callback] && typeof this.config[callback] !== 'function') {
        console.warn(`ConfigManager: ${callback} must be a function. Ignoring.`);
        this.config[callback] = null;
      }
    });
    
    // Validate dimensions
    if (this.config.width && !this.isValidDimension(this.config.width)) {
      console.warn(`ConfigManager: Invalid width "${this.config.width}". Using default.`);
      this.config.width = '400px';
    }
    
    if (this.config.height && !this.isValidDimension(this.config.height)) {
      console.warn(`ConfigManager: Invalid height "${this.config.height}". Using default.`);
      this.config.height = '600px';
    }
  }
  
  /**
   * Check if dimension value is valid
   */
  isValidDimension(value) {
    return /^\d+(px|%|em|rem|vh|vw)$/.test(value) || value === 'auto';
  }
  
  /**
   * Get configuration value
   */
  get(key) {
    return this.config[key];
  }
  
  /**
   * Set configuration value
   */
  set(key, value) {
    this.config[key] = value;
  }
  
  /**
   * Get all configuration
   */
  getAll() {
    return { ...this.config };
  }
  
  /**
   * Check if feature is enabled
   */
  isFeatureEnabled(feature) {
    const featureMap = {
      images: this.config.allowImageUpload || this.config.allowImagePaste,
      tables: this.config.showTables,
      replies: this.config.allowReply,
      feedback: this.config.allowFeedback,
      fontControl: this.config.enableFontSizeControl,
      templates: this.config.supportedMessageTypes.includes('Template'),
      evaluation: this.config.showEvaluationButtons
    };
    
    return featureMap[feature] || false;
  }
}