/**
 * Template Selection Modal
 * Shows available trading scenario templates for quick selection
 */

import { TEMPLATE_CATEGORIES } from '../core/widget-config.js';

export class TemplateModal {
  constructor(options = {}) {
    this.options = {
      templates: options.templates || [],
      onSelect: options.onSelect || (() => {}),
      onClose: options.onClose || (() => {}),
      apiClient: options.apiClient,
      showCategories: options.showCategories !== false,
      ...options
    };
    
    this.templates = this.options.templates;
    this.filteredTemplates = [...this.templates];
    this.selectedCategory = 'all';
    this.searchQuery = '';
    this.isVisible = false;
    this.isLoading = false;
    
    this.modal = null;
    this.backdrop = null;
    
    this.init();
  }
  
  /**
   * Initialize the modal
   */
  init() {
    this.createModal();
    this.loadTemplates();
  }
  
  /**
   * Create modal DOM structure
   */
  createModal() {
    // Create backdrop
    this.backdrop = document.createElement('div');
    this.backdrop.className = 'tlw-modal-backdrop';
    this.backdrop.addEventListener('click', (e) => {
      if (e.target === this.backdrop) {
        this.hide();
      }
    });
    
    // Create modal
    this.modal = document.createElement('div');
    this.modal.className = 'tlw-template-modal';
    this.modal.innerHTML = this.render();
    
    this.backdrop.appendChild(this.modal);
    document.body.appendChild(this.backdrop);
    
    this.attachEventListeners();
  }
  
  /**
   * Render modal HTML
   */
  render() {
    return `
      <div class="tlw-modal-header">
        <h2 class="tlw-modal-title">
          <span class="tlw-modal-icon"></span>
          Trading Scenario Templates
        </h2>
        <button class="tlw-modal-close" data-action="close" aria-label="Close">
          <span>&times;</span>
        </button>
      </div>
      
      <div class="tlw-modal-body">
        ${this.renderFilters()}
        ${this.renderTemplateGrid()}
      </div>
      
      <div class="tlw-modal-footer">
        <div class="tlw-template-count">
          <span id="template-count">${this.filteredTemplates.length}</span> templates available
        </div>
        <div class="tlw-modal-actions">
          <button class="tlw-btn tlw-btn--secondary" data-action="close">
            Cancel
          </button>
        </div>
      </div>
    `;
  }
  
  /**
   * Render filter section
   */
  renderFilters() {
    if (!this.options.showCategories && !this.options.showSearch) {
      return '';
    }
    
    return `
      <div class="tlw-filters">
        ${this.options.showSearch !== false ? `
          <div class="tlw-search-box">
            <input 
              type="text" 
              class="tlw-search-input" 
              placeholder="Search templates..." 
              value="${this.searchQuery}"
              data-action="search"
            >
            <span class="tlw-search-icon"></span>
          </div>
        ` : ''}
        
        ${this.options.showCategories ? `
          <div class="tlw-category-filters">
            ${this.renderCategoryFilters()}
          </div>
        ` : ''}
      </div>
    `;
  }
  
  /**
   * Render category filter buttons
   */
  renderCategoryFilters() {
    const categories = ['all', ...this.extractCategories()];
    
    return categories.map(category => `
      <button 
        class="tlw-category-btn ${this.selectedCategory === category ? 'active' : ''}"
        data-action="filter-category"
        data-category="${category}"
      >
        ${category === 'all' ? 'All' : category}
        ${category !== 'all' ? `<span class="tlw-category-count">${this.getTemplateCountByCategory(category)}</span>` : ''}
      </button>
    `).join('');
  }
  
  /**
   * Render template grid
   */
  renderTemplateGrid() {
    if (this.isLoading) {
      return `
        <div class="tlw-loading-state">
          <div class="tlw-spinner"></div>
          <p>Loading templates...</p>
        </div>
      `;
    }
    
    if (this.filteredTemplates.length === 0) {
      return this.renderEmptyState();
    }
    
    return `
      <div class="tlw-template-grid" id="template-grid">
        ${this.filteredTemplates.map(template => this.renderTemplateCard(template)).join('')}
      </div>
    `;
  }
  
  /**
   * Render individual template card
   */
  renderTemplateCard(template) {
    const difficulty = template.difficulty || 'intermediate';
    const category = template.category || 'Custom';
    
    return `
      <div class="tlw-template-card" data-template-id="${template.id}" data-action="select-template">
        <div class="tlw-template-header">
          <h3 class="tlw-template-title">${template.name}</h3>
          <div class="tlw-template-badges">
            <span class="tlw-template-category">${category}</span>
            <span class="tlw-template-difficulty tlw-template-difficulty--${difficulty}">
              ${difficulty}
            </span>
          </div>
        </div>
        
        <div class="tlw-template-content">
          <p class="tlw-template-description">
            ${template.description || 'No description available'}
          </p>
          
          ${template.parameters ? `
            <div class="tlw-template-parameters">
              <strong>Key Parameters:</strong>
              <ul>
                ${template.parameters.slice(0, 3).map(param => 
                  `<li>${param.name}: ${param.description}</li>`
                ).join('')}
                ${template.parameters.length > 3 ? `<li>+${template.parameters.length - 3} more...</li>` : ''}
              </ul>
            </div>
          ` : ''}
          
          ${template.tags && template.tags.length > 0 ? `
            <div class="tlw-template-tags">
              ${template.tags.slice(0, 4).map(tag => `<span class="tlw-tag">${tag}</span>`).join('')}
            </div>
          ` : ''}
        </div>
        
        <div class="tlw-template-footer">
          <div class="tlw-template-stats">
            ${template.popularity ? `
              <span class="tlw-template-stat">
                <span class="tlw-stat-icon"></span>
                ${template.popularity}
              </span>
            ` : ''}
            ${template.successRate ? `
              <span class="tlw-template-stat">
                <span class="tlw-stat-icon"></span>
                ${template.successRate}% success
              </span>
            ` : ''}
          </div>
          <button class="tlw-btn tlw-btn--primary tlw-template-select" data-template-id="${template.id}">
            Select Template
          </button>
        </div>
      </div>
    `;
  }
  
  /**
   * Render empty state
   */
  renderEmptyState() {
    const message = this.searchQuery 
      ? `No templates found matching "${this.searchQuery}"`
      : this.selectedCategory !== 'all' 
        ? `No templates found in "${this.selectedCategory}" category`
        : 'No templates available';
    
    return `
      <div class="tlw-empty-state">
        <div class="tlw-empty-icon"></div>
        <h3>No Templates Found</h3>
        <p>${message}</p>
        ${this.searchQuery || this.selectedCategory !== 'all' ? `
          <button class="tlw-btn tlw-btn--secondary" data-action="clear-filters">
            Clear Filters
          </button>
        ` : ''}
      </div>
    `;
  }
  
  /**
   * Attach event listeners
   */
  attachEventListeners() {
    // Close buttons
    this.modal.querySelectorAll('[data-action="close"]').forEach(btn => {
      btn.addEventListener('click', () => this.hide());
    });
    
    // Search input
    const searchInput = this.modal.querySelector('[data-action="search"]');
    if (searchInput) {
      searchInput.addEventListener('input', (e) => {
        this.handleSearch(e.target.value);
      });
    }
    
    // Category filters
    this.modal.querySelectorAll('[data-action="filter-category"]').forEach(btn => {
      btn.addEventListener('click', (e) => {
        const category = e.target.dataset.category;
        this.handleCategoryFilter(category);
      });
    });
    
    // Template selection
    this.modal.addEventListener('click', (e) => {
      if (e.target.matches('[data-action="select-template"], [data-action="select-template"] *')) {
        const card = e.target.closest('.tlw-template-card');
        if (card) {
          const templateId = card.dataset.templateId;
          this.handleTemplateSelect(templateId);
        }
      }
    });
    
    // Clear filters
    this.modal.addEventListener('click', (e) => {
      if (e.target.matches('[data-action="clear-filters"]')) {
        this.clearFilters();
      }
    });
    
    // Keyboard navigation
    this.modal.addEventListener('keydown', (e) => this.handleKeyNavigation(e));
    
    // Prevent modal from closing when clicking inside
    this.modal.addEventListener('click', (e) => {
      e.stopPropagation();
    });
  }
  
  /**
   * Handle search input
   */
  handleSearch(query) {
    this.searchQuery = query.toLowerCase();
    this.filterTemplates();
    this.updateTemplateGrid();
  }
  
  /**
   * Handle category filter
   */
  handleCategoryFilter(category) {
    this.selectedCategory = category;
    
    // Update button states
    this.modal.querySelectorAll('.tlw-category-btn').forEach(btn => {
      btn.classList.toggle('active', btn.dataset.category === category);
    });
    
    this.filterTemplates();
    this.updateTemplateGrid();
  }
  
  /**
   * Handle template selection
   */
  handleTemplateSelect(templateId) {
    const template = this.templates.find(t => t.id === templateId);
    
    if (template) {
      // Add selection animation
      const card = this.modal.querySelector(`[data-template-id="${templateId}"]`);
      if (card) {
        card.classList.add('tlw-template-card--selected');
      }
      
      setTimeout(() => {
        this.options.onSelect(template);
        this.hide();
      }, 300);
    }
  }
  
  /**
   * Clear all filters
   */
  clearFilters() {
    this.searchQuery = '';
    this.selectedCategory = 'all';
    
    // Reset search input
    const searchInput = this.modal.querySelector('[data-action="search"]');
    if (searchInput) {
      searchInput.value = '';
    }
    
    // Reset category buttons
    this.modal.querySelectorAll('.tlw-category-btn').forEach(btn => {
      btn.classList.toggle('active', btn.dataset.category === 'all');
    });
    
    this.filterTemplates();
    this.updateTemplateGrid();
  }
  
  /**
   * Filter templates based on current filters
   */
  filterTemplates() {
    this.filteredTemplates = this.templates.filter(template => {
      // Category filter
      if (this.selectedCategory !== 'all' && template.category !== this.selectedCategory) {
        return false;
      }
      
      // Search filter
      if (this.searchQuery && !this.matchesSearch(template)) {
        return false;
      }
      
      return true;
    });
  }
  
  /**
   * Check if template matches search query
   */
  matchesSearch(template) {
    const searchFields = [
      template.name,
      template.description,
      template.category,
      ...(template.tags || []),
      ...(template.parameters || []).map(p => p.name + ' ' + p.description)
    ].join(' ').toLowerCase();
    
    return searchFields.includes(this.searchQuery);
  }
  
  /**
   * Update template grid display
   */
  updateTemplateGrid() {
    const container = this.modal.querySelector('.tlw-modal-body');
    if (container) {
      // Update the entire body to refresh filters and grid
      container.innerHTML = this.renderFilters() + this.renderTemplateGrid();
      
      // Re-attach event listeners for new elements
      this.attachEventListeners();
    }
    
    // Update count
    const countElement = this.modal.querySelector('#template-count');
    if (countElement) {
      countElement.textContent = this.filteredTemplates.length;
    }
  }
  
  /**
   * Handle keyboard navigation
   */
  handleKeyNavigation(e) {
    switch(e.key) {
      case 'Escape':
        this.hide();
        break;
        
      case 'Enter':
        if (e.target.matches('.tlw-template-card')) {
          const templateId = e.target.dataset.templateId;
          this.handleTemplateSelect(templateId);
        }
        break;
    }
  }
  
  /**
   * Extract unique categories from templates
   */
  extractCategories() {
    const categories = new Set(this.templates.map(t => t.category).filter(Boolean));
    return Array.from(categories).sort();
  }
  
  /**
   * Get template count by category
   */
  getTemplateCountByCategory(category) {
    return this.templates.filter(t => t.category === category).length;
  }
  
  /**
   * Load templates from API or use provided templates
   */
  async loadTemplates() {
    if (this.templates.length > 0) {
      this.filterTemplates();
      return;
    }
    
    if (!this.options.apiClient) {
      // Use default templates
      this.templates = this.getDefaultTemplates();
      this.filterTemplates();
      this.updateTemplateGrid();
      return;
    }
    
    try {
      this.isLoading = true;
      this.updateTemplateGrid();
      
      const response = await this.options.apiClient.get('/api/widget/templates');
      this.templates = response || [];
      
      this.filterTemplates();
    } catch (error) {
      console.warn('Failed to load templates from API, using defaults', error);
      this.templates = this.getDefaultTemplates();
      this.filterTemplates();
    } finally {
      this.isLoading = false;
      this.updateTemplateGrid();
    }
  }
  
  /**
   * Get default fallback templates
   */
  getDefaultTemplates() {
    return [
      {
        id: 'breakout-volume',
        name: 'Breakout with Volume',
        description: 'Identify stocks breaking out of consolidation patterns with volume confirmation',
        category: 'Technical Analysis',
        difficulty: 'beginner',
        tags: ['breakout', 'volume', 'momentum'],
        popularity: 4.5,
        successRate: 68,
        parameters: [
          { name: 'Volume Threshold', description: 'Minimum volume increase percentage' },
          { name: 'Breakout Period', description: 'Number of days for pattern analysis' },
          { name: 'Price Change', description: 'Minimum price movement percentage' }
        ]
      },
      {
        id: 'rsi-oversold',
        name: 'RSI Oversold Recovery',
        description: 'Find oversold stocks showing signs of reversal using RSI indicator',
        category: 'Technical Analysis',
        difficulty: 'intermediate',
        tags: ['RSI', 'oversold', 'reversal'],
        popularity: 4.2,
        successRate: 72,
        parameters: [
          { name: 'RSI Threshold', description: 'Oversold RSI level (typically 30)' },
          { name: 'Recovery Confirmation', description: 'RSI bounce back requirement' }
        ]
      },
      {
        id: 'gap-up-follow-through',
        name: 'Gap Up Follow-Through',
        description: 'Stocks gapping up with strong follow-through momentum',
        category: 'Price Action',
        difficulty: 'intermediate',
        tags: ['gap', 'momentum', 'follow-through'],
        popularity: 3.8,
        successRate: 65,
        parameters: [
          { name: 'Gap Size', description: 'Minimum gap percentage' },
          { name: 'Volume Confirmation', description: 'Required volume increase' }
        ]
      },
      {
        id: 'earnings-surprise',
        name: 'Earnings Surprise Momentum',
        description: 'Companies beating earnings expectations with price momentum',
        category: 'Market Structure',
        difficulty: 'advanced',
        tags: ['earnings', 'surprise', 'fundamental'],
        popularity: 4.0,
        successRate: 75,
        parameters: [
          { name: 'Surprise Threshold', description: 'Minimum earnings beat percentage' },
          { name: 'Price Response', description: 'Required price movement post-earnings' }
        ]
      }
    ];
  }
  
  /**
   * Show the modal
   */
  show() {
    if (!this.isVisible) {
      this.backdrop.style.display = 'flex';
      document.body.classList.add('tlw-modal-open');
      
      // Animate in
      setTimeout(() => {
        this.backdrop.classList.add('tlw-modal-backdrop--visible');
        this.modal.classList.add('tlw-template-modal--visible');
      }, 10);
      
      this.isVisible = true;
      
      // Focus first template or search input
      setTimeout(() => {
        const searchInput = this.modal.querySelector('[data-action="search"]');
        const firstTemplate = this.modal.querySelector('.tlw-template-card');
        
        if (searchInput) {
          searchInput.focus();
        } else if (firstTemplate) {
          firstTemplate.focus();
        }
      }, 100);
    }
  }
  
  /**
   * Hide the modal
   */
  hide() {
    if (this.isVisible) {
      this.backdrop.classList.remove('tlw-modal-backdrop--visible');
      this.modal.classList.remove('tlw-template-modal--visible');
      
      setTimeout(() => {
        this.backdrop.style.display = 'none';
        document.body.classList.remove('tlw-modal-open');
        this.isVisible = false;
        
        this.options.onClose();
      }, 300);
    }
  }
  
  /**
   * Update templates
   */
  updateTemplates(templates) {
    this.templates = templates || [];
    this.filterTemplates();
    this.updateTemplateGrid();
  }
  
  /**
   * Destroy the modal
   */
  destroy() {
    if (this.backdrop && this.backdrop.parentNode) {
      this.backdrop.parentNode.removeChild(this.backdrop);
    }
    
    document.body.classList.remove('tlw-modal-open');
    this.isVisible = false;
  }
}

export default TemplateModal;