/**
 * Widget AI Flow Parser
 * Adapts the aiFlowParser logic from the main app to work in the widget context
 * Builds proper Nodes/Edges with data connections from AI DAG blocks
 */

export class WidgetAiFlowParser {
  constructor() {
    this.nodeWidth = 400;
    this.nodeHeight = 400;
    this.flowDirection = 'LR';
  }

  /**
   * Parse AI DAG blocks into Nodes/Edges format with proper data connections
   * This replicates the critical logic from web-client aiFlowParser.js
   * 
   * @param {Array} dagBlocks - The functions_list from AI response
   * @param {Array} blockDefinitions - The function definitions (from blocks API)
   * @param {Object} options - Additional options like scenarioId
   * @returns {Object} - { nodes, edges } suitable for backend ScenarioTreeBuilder
   */
  parseAiDag(dagBlocks, blockDefinitions, options = {}) {
    console.log('WidgetAiFlowParser: Parsing DAG blocks', {
      blockCount: dagBlocks?.length || 0,
      definitionCount: blockDefinitions?.length || 0
    });

    if (!dagBlocks || !Array.isArray(dagBlocks) || dagBlocks.length === 0) {
      throw new Error('Invalid or empty DAG blocks');
    }

    // Create a map of function definitions for quick lookup
    const functionsMap = this.createFunctionsMap(blockDefinitions);
    
    // Process nodes and build edges
    const nodes = [];
    const edges = [];
    
    // First pass: create all nodes
    dagBlocks.forEach((aiBlock, index) => {
      const node = this.createNode(aiBlock, functionsMap);
      nodes.push(node);
    });
    
    // Second pass: create edges based on data connections
    dagBlocks.forEach((aiBlock, index) => {
      const blockEdges = this.buildDataConnectionEdges(aiBlock, dagBlocks, functionsMap, nodes);
      edges.push(...blockEdges);
    });

    console.log('WidgetAiFlowParser: Parsed result', {
      nodeCount: nodes.length,
      edgeCount: edges.length,
      hasDataConnections: edges.length > 0
    });

    return {
      nodes,
      edges,
      scenarioId: options.scenarioId,
      version: options.version || 1.0
    };
  }

  /**
   * Create a map of function definitions for quick lookup
   */
  createFunctionsMap(blockDefinitions) {
    const map = {};
    
    if (blockDefinitions && Array.isArray(blockDefinitions)) {
      blockDefinitions.forEach(def => {
        if (def.name) {
          map[def.name] = def;
        }
        // Also map by function_name if different
        if (def.function_name && def.function_name !== def.name) {
          map[def.function_name] = def;
        }
      });
    }
    
    return map;
  }

  /**
   * Create a node from AI block with proper inputs/outputs
   */
  createNode(aiBlock, functionsMap) {
    const fnDef = functionsMap[aiBlock.function_name] || functionsMap[aiBlock.function];
    
    // Extract formatted inputs and outputs from function definition AND AI block data
    const formattedInputs = this.extractInputs(fnDef, aiBlock);
    const formattedOutputs = this.extractOutputs(fnDef, aiBlock);
    const formattedOptions = this.extractOptions(fnDef, aiBlock);
    
    // Build node data structure (EXACTLY like working AiFlowParser)
    const candleOptions = [
      { value: 'bearish', label: 'Bearish' },
      { value: 'bullish', label: 'Bullish' }
    ];

    const priceOptions = [
      { value: 'open', label: 'Open' },
      { value: 'high', label: 'High' },
      { value: 'low', label: 'Low' },
      { value: 'close', label: 'Close' }
    ];

    const nodeData = {
      mainType: "function",
      type: "function",
      group: fnDef.grouping,
      name: fnDef.name,
      description: fnDef.description,
      inputs: formattedInputs,
      outputs: formattedOutputs,
      options: formattedOptions,
      blockId: fnDef.bid_,
      candleOptions: candleOptions,
      priceOptions: priceOptions,
      values: this.extractValues(aiBlock, fnDef),
      version: fnDef.version_
    };

    // Create the node with position (simplified since widget doesn't visualize)
    return {
      id: `node${aiBlock.BlockID}`,
      type: 'customNode',
      data: nodeData,
      position: { x: 0, y: 0 }, // Position will be handled by backend if needed
      targetPosition: 'left',
      sourcePosition: 'right'
    };
  }

  /**
   * Extract inputs from function definition (EXACTLY like working AiFlowParser)
   */
  extractInputs(fnDef, aiBlock) {
    const inputs = [];
    
    // Match working AiFlowParser logic exactly (lines 53-61)
    if (fnDef?.inputs?.properties) {
      for (const inputName in fnDef.inputs.properties) {
        if (fnDef.inputs.properties.hasOwnProperty(inputName)) {
          inputs.push({ name: inputName });
        }
      }
    }
    
    console.log(`WidgetAiFlowParser: Block ${aiBlock.BlockID} inputs`, {
      fromFnDef: fnDef?.inputs ? Object.keys(fnDef.inputs.properties || {}) : [],
      finalInputs: inputs.map(i => i.name)
    });
    
    return inputs;
  }

  /**
   * Extract outputs from function definition and AI block
   */
  extractOutputs(fnDef, aiBlock) {
    const outputs = [];
    
    // First try to use outputs from AI block if provided
    if (aiBlock.outputs && Array.isArray(aiBlock.outputs)) {
      aiBlock.outputs.forEach(output => {
        if (output) {
          outputs.push({ name: output });
        }
      });
    }
    // Fallback to function definition
    else if (fnDef?.outputs?.properties) {
      for (const outputName in fnDef.outputs.properties) {
        outputs.push({ name: outputName });
      }
    }
    
    return outputs;
  }

  /**
   * Extract options/parameters from function definition
   */
  extractOptions(fnDef, aiBlock) {
    const options = [];
    
    if (fnDef?.parameters?.properties) {
      for (const paramName in fnDef.parameters.properties) {
        const param = fnDef.parameters.properties[paramName];
        const value = aiBlock.parameters?.[paramName] || param.default;
        
        let fieldType = 'input';
        let optionValues = [];
        
        if (param.enum) {
          fieldType = 'select';
          optionValues = param.enum.map(val => ({ value: val, label: val }));
        }
        
        options.push({
          field: paramName,
          value: fieldType === 'select' ? value : value,
          type: fieldType,
          enumOutputs: optionValues.map(o => o.value),
          isValid: true
        });
      }
    }
    
    return options;
  }

  /**
   * Extract parameter values from AI block
   */
  extractValues(aiBlock, fnDef) {
    const values = [];
    
    if (aiBlock.parameters) {
      for (const paramName in aiBlock.parameters) {
        values.push({
          field: paramName,
          value: aiBlock.parameters[paramName]
        });
      }
    }
    
    return values;
  }

  /**
   * Build edges from data connections - THE CRITICAL LOGIC
   * This replicates lines 147-168 from aiFlowParser.js
   */
  buildDataConnectionEdges(aiBlock, allBlocks, functionsMap, nodes) {
    const edges = [];
    
    console.log(`WidgetAiFlowParser: Building edges for Block ${aiBlock.BlockID}`, {
      hasData: !!aiBlock.data,
      dataKeys: aiBlock.data ? Object.keys(aiBlock.data) : [],
      dataValues: aiBlock.data || {}
    });
    
    // This is the key part - process the data connections
    if (aiBlock.data) {
      for (const dataProp in aiBlock.data) {
        const dataValue = aiBlock.data[dataProp];
        
        if (!dataValue) continue;
        
        // EXACT LOGIC from working AiFlowParser (lines 149-164)
        const sourceLinkValues = dataValue.toString().split('.');
        
        if (sourceLinkValues.length > 1) {
          const sourceId = sourceLinkValues[0].replace('Block_', '');
          const sourceBlock = allBlocks.find(d => d.BlockID == sourceId);
          
          if (sourceBlock) {
            // Find target node from created nodes 
            const targetNode = nodes.find(node => node.id === `node${aiBlock.BlockID}`);
            if (!targetNode) {
              console.warn(`WidgetAiFlowParser: Target node not found for BlockID ${aiBlock.BlockID}`);
              continue;
            }
            
            // EXACT logic from working version
            const sourceOutputIndex = sourceBlock.outputs.indexOf(sourceLinkValues[1]);
            const targetInputIndex = targetNode.data.inputs.indexOf(targetNode.data.inputs.find(f => f.name == dataProp));
            
            if (sourceOutputIndex >= 0 && targetInputIndex >= 0) {
              const edge = {
                id: `reactflow__edge-node${sourceId}node${sourceId}-output-${sourceOutputIndex}-node${aiBlock.BlockID}node${aiBlock.BlockID}-input-${targetInputIndex}`,
                source: `node${sourceId}`,
                target: `node${aiBlock.BlockID}`,
                sourceHandle: `node${sourceId}-output-${sourceOutputIndex < 0 ? 0 : sourceOutputIndex}`,
                targetHandle: `node${aiBlock.BlockID}-input-${targetInputIndex < 0 ? 0 : targetInputIndex}`
              };
              
              edges.push(edge);
              
              console.log('WidgetAiFlowParser: Created data connection edge', {
                from: `Block_${sourceId}.${sourceLinkValues[1]}`,
                to: `Block_${aiBlock.BlockID}.${dataProp}`,
                edge: edge.id,
                sourceHandle: edge.sourceHandle,
                targetHandle: edge.targetHandle
              });
            } else {
              console.warn('WidgetAiFlowParser: Invalid indices for edge creation', {
                sourceOutputIndex,
                targetInputIndex,
                sourceOutputName: sourceLinkValues[1],
                targetFieldName: dataProp,
                dataValue,
                sourceBlockOutputs: sourceBlock.outputs,
                targetInputs: targetNode.data.inputs.map(i => i.name)
              });
            }
          }
        }
      }
    }
    
    // REMOVED: Hierarchy edges with null handles cause NullReferenceException in ScenarioTreeBuilder.cs:260
    // The backend only needs data connection edges with proper sourceHandle/targetHandle
    // Execution order is determined by the data connections, not hierarchy relationships
    
    return edges;
  }

  /**
   * Transform parsed nodes/edges to match backend ScenarioDto format
   */
  transformToScenarioDto(parsedData, options = {}) {
    const { nodes, edges } = parsedData;
    
    return {
      Id: options.scenarioId,
      Name: options.name || `Widget Scenario ${options.scenarioId}`,
      UserId: options.userId,
      IsFromAiGenerator: true,
      Nodes: nodes,
      Edges: edges,
      Viewport: { X: 0, Y: 0, Zoom: 1 },
      CrossFilters: {},
      Version: parsedData.version || 1.0,
      IsActive: true,
      ManuallyUpdated: false,
      IsFromEditor: false,
      GeneratingAiFlow: false
    };
  }
}