/**
 * Thinking Manager
 * Manages progressive loading indicators with contextual messages
 */

export class ThinkingManager {
  constructor() {
    this.activeIndicators = new Map();
    this.progressiveMessages = {
      '/chat': {
        0: ' Thinking...',
        5000: ' Analyzing your request...',
        10000: ' Crafting response...',
        30000: ' Almost done...',
        45000: ' This is taking a bit longer...'
      },
      '/query': {
        0: ' Creating your model...',
        3000: ' Constructing DAG structure...',
        8000: ' Optimizing parameters...',
        15000: ' Fine-tuning logic...',
        30000: ' Complex model taking time...',
        60000: ' Timeout - Please try again'
      },
      '/execute': {
        0: ' Running the model...',
        1500: ' Processing market data...',
        4000: ' Finding trading opportunities...',
        8000: ' Analyzing results...',
        15000: ' Large dataset processing...'
      },
      'template': {
        0: ' Processing template...',
        3000: ' Customizing parameters...',
        8000: ' Generating scenario...',
        15000: ' Applying configurations...',
        30000: ' Template taking time...'
      },
      'analysis': {
        0: ' Analyzing data...',
        3000: ' Processing your question...',
        8000: ' Generating insights...',
        15000: ' Deep diving into data...',
        30000: ' Complex analysis in progress...'
      }
    };
  }
  
  /**
   * Start showing thinking indicator
   * @param {string} requestId - Unique ID for this request
   * @param {string} operationType - Type of operation ('/query', '/execute', etc)
   * @param {string|null} customMessage - Optional custom message
   * @param {Function|null} onTimeout - Callback when timeout is reached
   * @param {number} timeoutMs - Timeout in milliseconds (default: 60000)
   */
  startThinking(requestId, operationType, customMessage = null, onTimeout = null, timeoutMs = 60000) {
    console.log(' ThinkingManager: Starting thinking indicator', {
      requestId,
      operationType,
      customMessage,
      activeCount: this.activeIndicators.size,
      timeoutMs
    });
    
    // Stop any existing indicator for this request
    this.stopThinking(requestId);
    
    const indicator = {
      id: requestId,
      type: operationType,
      startTime: Date.now(),
      element: null,
      timers: [],
      customMessage,
      onTimeout,
      timeoutMs,
      timedOut: false
    };
    
    this.activeIndicators.set(requestId, indicator);
    
    // Create and show the indicator
    this.createIndicatorElement(indicator);
    
    // Schedule progressive message updates
    this.scheduleProgressiveUpdates(indicator);
    
    // Set up timeout handler
    if (onTimeout && timeoutMs > 0) {
      this.setupTimeoutHandler(indicator);
    }
    
    // Start progress bar animation for chat messages
    if (indicator.type === '/chat') {
      this.startProgressBar(indicator);
    }
    
    return indicator;
  }
  
  /**
   * Stop thinking indicator
   */
  stopThinking(requestId) {
    const indicator = this.activeIndicators.get(requestId);
    
    console.log(' ThinkingManager: Stopping thinking indicator', {
      requestId,
      found: !!indicator,
      hasElement: !!indicator?.element
    });
    
    if (indicator) {
      // Clear all timers
      indicator.timers.forEach(timer => clearTimeout(timer));
      
      // Clear progress timer if it exists
      if (indicator.progressTimer) {
        clearTimeout(indicator.progressTimer);
      }
      
      // Remove element with fade out
      if (indicator.element) {
        indicator.element.style.opacity = '0';
        indicator.element.style.transform = 'translateY(-10px)';
        
        setTimeout(() => {
          if (indicator.element && indicator.element.parentNode) {
            indicator.element.parentNode.removeChild(indicator.element);
            console.log(' ThinkingManager: Indicator element removed from DOM');
          }
        }, 300);
      }
      
      this.activeIndicators.delete(requestId);
      console.log(' ThinkingManager: Indicator stopped successfully');
      
      return true;
    }
    
    console.log(' ThinkingManager: No indicator found to stop');
    return false;
  }
  
  /**
   * Create the visual thinking indicator element
   */
  createIndicatorElement(indicator) {
    const messages = this.progressiveMessages[indicator.type] || this.progressiveMessages['/chat'];
    const initialMessage = indicator.customMessage || messages[0];
    
    // Create indicator HTML
    const element = document.createElement('div');
    element.className = 'tlw-thinking-indicator';
    element.setAttribute('data-type', indicator.type);
    element.innerHTML = `
      <div class="tlw-thinking-content">
        <div class="tlw-thinking-animation">
          <span class="tlw-thinking-dot"></span>
          <span class="tlw-thinking-dot"></span>
          <span class="tlw-thinking-dot"></span>
        </div>
        <div class="tlw-thinking-message">
          ${initialMessage}
        </div>
        <div class="tlw-thinking-timer" style="display: none;">
          <span class="tlw-timer-text">0s</span>
        </div>
      </div>
      <div class="tlw-progress-bar">
        <div class="tlw-progress-fill"></div>
      </div>
    `;
    
    // Add to DOM - find chat container (including custom test containers)
    const chatContainer = document.getElementById('chatContainer') ||  // Custom test container
                         document.querySelector('.tlw-messages-container') || 
                         document.querySelector('.tlw-chat-container') ||
                         document.querySelector('[data-widget-messages]') ||
                         document.querySelector('.tradr-widget');
    
    console.log(' ThinkingManager: Chat container found:', {
      found: !!chatContainer,
      containerType: chatContainer?.id || chatContainer?.className || 'unknown',
      elementCreated: !!element
    });
    
    if (chatContainer) {
      chatContainer.appendChild(element);
      console.log(' ThinkingManager: Indicator added to DOM');
      
      // Scroll to show indicator
      setTimeout(() => {
        chatContainer.scrollTop = chatContainer.scrollHeight;
      }, 100);
    } else {
      console.warn(' ThinkingManager: No chat container found to add indicator!');
    }
    
    indicator.element = element;
    
    return element;
  }
  
  /**
   * Schedule progressive message updates
   */
  scheduleProgressiveUpdates(indicator) {
    const messages = this.progressiveMessages[indicator.type] || this.progressiveMessages['/chat'];
    
    // Schedule each message update
    Object.entries(messages).forEach(([delay, message]) => {
      const timeoutId = setTimeout(() => {
        if (this.activeIndicators.has(indicator.id)) {
          this.updateMessage(indicator, message);
        }
      }, parseInt(delay));
      
      indicator.timers.push(timeoutId);
    });
    
    // Start timer display after 2 seconds
    const timerTimeout = setTimeout(() => {
      if (this.activeIndicators.has(indicator.id)) {
        this.showTimer(indicator);
      }
    }, 2000);
    
    indicator.timers.push(timerTimeout);
  }
  
  /**
   * Set up timeout handler for the request
   */
  setupTimeoutHandler(indicator) {
    const timeoutId = setTimeout(() => {
      if (this.activeIndicators.has(indicator.id) && !indicator.timedOut) {
        console.warn(` ThinkingManager: Request ${indicator.id} timed out after ${indicator.timeoutMs}ms`);
        
        indicator.timedOut = true;
        
        // Update message to show timeout
        this.updateMessage(indicator, ' Request timed out - Click to retry');
        
        // Add retry button to the indicator element
        if (indicator.element) {
          this.addRetryButton(indicator);
        }
        
        // Call the timeout callback if provided
        if (indicator.onTimeout) {
          indicator.onTimeout(indicator.id);
        }
      }
    }, indicator.timeoutMs);
    
    // Store timeout ID for cleanup
    indicator.timeoutHandler = timeoutId;
    indicator.timers.push(timeoutId);
  }
  
  /**
   * Add retry button to timed out indicator
   */
  addRetryButton(indicator) {
    const retryBtn = document.createElement('button');
    retryBtn.className = 'tlw-thinking-retry-btn';
    retryBtn.innerHTML = ' Retry';
    retryBtn.style.cssText = `
      margin-left: 10px;
      padding: 4px 12px;
      background: #4CAF50;
      color: white;
      border: none;
      border-radius: 4px;
      cursor: pointer;
      font-size: 14px;
    `;
    
    retryBtn.addEventListener('click', () => {
      console.log(' Retry button clicked for:', indicator.id);
      
      // Remove the timeout indicator
      this.stopThinking(indicator.id);
      
      // Trigger retry through callback
      if (indicator.onTimeout) {
        indicator.onTimeout(indicator.id, true); // true = retry requested
      }
    });
    
    // Add button to indicator element
    const messageElement = indicator.element?.querySelector('.tlw-thinking-message');
    if (messageElement) {
      messageElement.appendChild(retryBtn);
    }
  }
  
  /**
   * Update the thinking message with smooth transition
   */
  updateMessage(indicator, newMessage) {
    if (!indicator.element) return;
    
    const messageElement = indicator.element.querySelector('.tlw-thinking-message');
    if (!messageElement) return;
    
    // Fade out
    messageElement.style.opacity = '0.5';
    
    setTimeout(() => {
      messageElement.textContent = newMessage;
      // Fade in
      messageElement.style.opacity = '1';
    }, 200);
  }
  
  /**
   * Show elapsed time timer
   */
  showTimer(indicator) {
    if (!indicator.element) return;
    
    const timerElement = indicator.element.querySelector('.tlw-thinking-timer');
    if (!timerElement) return;
    
    // Don't show timer for chat messages (they have progress bar)
    if (indicator.type === '/chat') return;
    
    timerElement.style.display = 'flex';
    
    const updateTimer = () => {
      if (!this.activeIndicators.has(indicator.id)) return;
      
      const elapsed = Math.floor((Date.now() - indicator.startTime) / 1000);
      const timerText = timerElement.querySelector('.tlw-timer-text');
      
      if (timerText) {
        timerText.textContent = `${elapsed}s`;
      }
      
      // Continue updating if still active
      if (this.activeIndicators.has(indicator.id)) {
        setTimeout(updateTimer, 1000);
      }
    };
    
    updateTimer();
  }
  
  /**
   * Start 30-second progress bar animation
   */
  startProgressBar(indicator) {
    if (!indicator.element) return;
    
    const progressFill = indicator.element.querySelector('.tlw-progress-fill');
    if (!progressFill) return;
    
    // Start the 30-second animation
    progressFill.style.transition = 'width 30s linear';
    progressFill.style.width = '100%';
    
    // After 30 seconds, pulse to indicate still working
    const progressTimer = setTimeout(() => {
      if (this.activeIndicators.has(indicator.id)) {
        progressFill.style.transition = 'opacity 0.5s ease-in-out';
        progressFill.style.animation = 'pulse 1s ease-in-out infinite';
      }
    }, 30000);
    
    // Store timer so it can be cleared
    indicator.timers.push(progressTimer);
  }
  
  /**
   * Get contextual message for operation type
   */
  static getContextualMessage(operationType, stage = 'start') {
    const contextualMessages = {
      'sendMessage': {
        'start': ' Sending message...',
        'processing': ' AI is thinking...'
      },
      'generateScenario': {
        'start': ' Initiating scenario generation...',
        'processing': ' Building your trading model...'
      },
      'executeTemplate': {
        'start': ' Loading template...',
        'processing': ' Generating from template...'
      },
      'runEvaluation': {
        'start': ' Starting evaluation...',
        'processing': ' Processing market data...'
      },
      'runBacktest': {
        'start': ' Initializing backtest...',
        'processing': ' Calculating performance...'
      }
    };
    
    return contextualMessages[operationType]?.[stage] || ' Processing...';
  }
  
  /**
   * Stop all active thinking indicators
   */
  stopAll() {
    const indicatorIds = Array.from(this.activeIndicators.keys());
    indicatorIds.forEach(id => this.stopThinking(id));
  }
  
  /**
   * Get active indicators count
   */
  getActiveCount() {
    return this.activeIndicators.size;
  }
  
  /**
   * Check if specific indicator is active
   */
  isActive(requestId) {
    return this.activeIndicators.has(requestId);
  }
}

/**
 * CSS styles for thinking indicators (to be injected)
 */
export const thinkingIndicatorStyles = `
  .tlw-thinking-indicator {
    padding: 12px 16px;
    background: linear-gradient(90deg, #f0f9ff 0%, #e0f2fe 100%);
    border-left: 3px solid #0ea5e9;
    border-radius: 8px;
    margin: 8px 0;
    animation: slideIn 0.3s ease-out;
    transition: all 0.3s ease;
    font-size: 14px;
  }
  
  .tlw-thinking-content {
    display: flex;
    align-items: center;
    gap: 12px;
  }
  
  .tlw-thinking-animation {
    display: flex;
    gap: 4px;
    align-items: center;
  }
  
  .tlw-thinking-dot {
    width: 8px;
    height: 8px;
    background: #0ea5e9;
    border-radius: 50%;
    animation: bounce 1.4s ease-in-out infinite;
  }
  
  .tlw-thinking-dot:nth-child(1) { animation-delay: -0.32s; }
  .tlw-thinking-dot:nth-child(2) { animation-delay: -0.16s; }
  .tlw-thinking-dot:nth-child(3) { animation-delay: 0s; }
  
  .tlw-thinking-message {
    color: #0c4a6e;
    font-weight: 500;
    transition: opacity 0.2s ease;
    flex: 1;
    line-height: 1.4;
  }
  
  .tlw-thinking-timer {
    display: none;
    align-items: center;
    padding: 2px 8px;
    background: rgba(14, 165, 233, 0.1);
    border-radius: 12px;
    margin-left: auto;
  }
  
  .tlw-timer-text {
    color: #0369a1;
    font-size: 12px;
    font-weight: 600;
    font-family: 'SF Mono', 'Monaco', 'Inconsolata', monospace;
  }
  
  @keyframes bounce {
    0%, 80%, 100% {
      transform: scale(0);
      opacity: 0.5;
    }
    40% {
      transform: scale(1);
      opacity: 1;
    }
  }
  
  @keyframes slideIn {
    from {
      transform: translateY(-10px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  
  /* Different colors for different operation types */
  .tlw-thinking-indicator[data-type="/query"] {
    background: linear-gradient(90deg, #f0fdf4 0%, #dcfce7 100%);
    border-left-color: #22c55e;
  }
  
  .tlw-thinking-indicator[data-type="/query"] .tlw-thinking-dot {
    background: #22c55e;
  }
  
  .tlw-thinking-indicator[data-type="/evaluate"] {
    background: linear-gradient(90deg, #fefce8 0%, #fef3c7 100%);
    border-left-color: #eab308;
  }
  
  .tlw-thinking-indicator[data-type="/evaluate"] .tlw-thinking-dot {
    background: #eab308;
  }
  
  .tlw-thinking-indicator[data-type="template"] {
    background: linear-gradient(90deg, #fdf4ff 0%, #fae8ff 100%);
    border-left-color: #a855f7;
  }
  
  .tlw-thinking-indicator[data-type="template"] .tlw-thinking-dot {
    background: #a855f7;
  }
  
  /* Progress bar for chat messages */
  .tlw-progress-bar {
    width: 100%;
    height: 3px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    margin-top: 8px;
    overflow: hidden;
    position: relative;
  }
  
  .tlw-thinking-indicator[data-type="/chat"] .tlw-progress-bar {
    display: block;
  }
  
  .tlw-thinking-indicator:not([data-type="/chat"]) .tlw-progress-bar {
    display: none;
  }
  
  .tlw-progress-fill {
    height: 100%;
    width: 0%;
    background: linear-gradient(90deg, #0ea5e9 0%, #0284c7 100%);
    border-radius: 3px;
    box-shadow: 0 0 8px rgba(14, 165, 233, 0.5);
    position: relative;
    overflow: hidden;
  }
  
  .tlw-progress-fill::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: linear-gradient(
      90deg,
      transparent,
      rgba(255, 255, 255, 0.3),
      transparent
    );
    animation: shimmer 2s linear infinite;
  }
  
  @keyframes shimmer {
    0% { transform: translateX(-100%); }
    100% { transform: translateX(100%); }
  }
  
  @keyframes pulse {
    0%, 100% { opacity: 1; }
    50% { opacity: 0.6; }
  }
`;