/**
 * TradrLab Chat Widget
 * Main entry point for the embeddable chat widget
 */

import { WidgetFactory } from './core/widget-factory';
import { ConfigManager } from './core/config-manager';
import './styles/widget-base.css';

// Debug logging
console.log(' TradrLab Widget: Loading v2.1.0 - DEBUG BUILD');

// Create global TradrLabWidget object
const TradrLabWidget = {
  version: '1.0.0',
  _globalConfig: {},
  _widgets: new Map(),
  
  /**
   * Set global configuration for all widgets
   * @param {Object} config - Global configuration object
   */
  globals: function(config) {
    this._globalConfig = {
      ...this._globalConfig,
      ...config
    };
    
    // Validate required global config
    if (!this._globalConfig.apikey) {
      console.warn('TradrLabWidget: API key not set. Call TradrLabWidget.globals({ apikey: "your-key" })');
    }
    
    return this;
  },
  
  /**
   * Create a new widget instance
   * @param {string} type - Widget type (currently only 'chat' is supported)
   * @param {HTMLElement} targetElement - DOM element to render widget into
   * @param {Object} options - Widget-specific configuration
   * @returns {Object} Widget instance
   */
  CreateWidget: function(type, targetElement, options = {}) {
    if (!targetElement) {
      throw new Error('TradrLabWidget: Target element is required');
    }
    
    if (!this._globalConfig.apikey) {
      throw new Error('TradrLabWidget: API key is required. Call TradrLabWidget.globals() first.');
    }
    
    // Create config manager instance
    const configManager = new ConfigManager(this._globalConfig, options);
    
    // Create widget using factory
    const widget = WidgetFactory.create(type, targetElement, configManager);
    
    // Store widget reference
    const widgetId = `widget-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
    widget.id = widgetId;
    this._widgets.set(widgetId, widget);
    
    // Initialize widget
    widget.init();
    
    return widget;
  },
  
  /**
   * Get widget by ID
   * @param {string} widgetId - Widget ID
   * @returns {Object|null} Widget instance or null
   */
  getWidget: function(widgetId) {
    return this._widgets.get(widgetId) || null;
  },
  
  /**
   * Destroy widget by ID
   * @param {string} widgetId - Widget ID
   */
  destroyWidget: function(widgetId) {
    const widget = this._widgets.get(widgetId);
    if (widget) {
      widget.destroy();
      this._widgets.delete(widgetId);
    }
  },
  
  /**
   * Destroy all widgets
   */
  destroyAll: function() {
    this._widgets.forEach(widget => widget.destroy());
    this._widgets.clear();
  }
};

// Expose to global scope
if (typeof window !== 'undefined') {
  window.TradrLabWidget = TradrLabWidget;
}

export default TradrLabWidget;