/**
 * Enhanced SignalR Client
 * Extended to handle all message types for Interactive and Template journeys
 * Includes auto-execution after DAG generation (Step 4 of user journey)  
 */

import * as signalR from '@microsoft/signalr';

export class EnhancedSignalRClient {
  constructor(configManager, messageStore, apiClient = null, thinkingManager = null) {
    this.config = configManager;
    this.messageStore = messageStore;
    this.apiClient = apiClient;
    this.thinkingManager = thinkingManager;
    this.connection = null;
    this.listeners = new Map();
    this.connected = false;
    this.reconnectAttempts = 0;
    this.maxReconnectAttempts = 5;
    this.socketId = null;
    this.waitingForResponse = false;
  }
  
  /**
   * Connect to SignalR hub
   */
  async connect() {
    console.log(' SignalRClient: Starting connection process...');
    
    if (this.connected) {
      console.warn('SignalRClient: Already connected');
      return;
    }
    
    const hubUrl = this.config.get('signalRUrl');
    console.log('SignalRClient: Hub URL:', hubUrl);
    
    if (!hubUrl) {
      throw new Error('SignalRClient: SignalR URL not configured');
    }
    
    const jwtToken = this.config.get('jwtToken');
    if (!jwtToken) {
      console.warn('SignalRClient: No JWT token available, SignalR connection may fail');
    }
    
    try {
      this.connection = new signalR.HubConnectionBuilder()
        .withUrl(hubUrl, {
          accessTokenFactory: () => jwtToken || this.config.get('apikey')
        })
        .withAutomaticReconnect({
          nextRetryDelayInMilliseconds: (retryContext) => {
            if (retryContext.previousRetryCount >= this.maxReconnectAttempts) {
              return null;
            }
            return Math.min(1000 * Math.pow(2, retryContext.previousRetryCount), 30000);
          }
        })
        .configureLogging(
          this.config.get('debug') 
            ? signalR.LogLevel.Debug 
            : signalR.LogLevel.Warning
        )
        .build();
      
      this.setupEventHandlers();
      
      await this.connection.start();
      this.connected = true;
      this.reconnectAttempts = 0;
      this.socketId = this.connection.connectionId;
      
      console.log('SignalRClient: Connected successfully with ID:', this.socketId);
      
      await this.registerForMessages();
      
    } catch (error) {
      console.error('SignalRClient: Connection failed', error);
      throw error;
    }
  }
  
  /**
   * Setup comprehensive SignalR event handlers
   */
  setupEventHandlers() {
    console.log('SignalRClient: Setting up event handlers...');
    
    // Primary AI response handler - matches backend UnifiedSignalRService
    this.connection.on('AiResponseReceived', (message) => {
      console.log(' SignalRClient: AiResponseReceived received!', {
        messageType: message.messageType,
        originalEndpoint: message.originalEndpoint,
        scenarioId: message.scenarioId,
        sessionId: message.sessionId,
        aiResponseLength: message.aiResponse?.length,
        aiResponsePreview: message.aiResponse?.substring(0, 50) + '...'
      });
      
      // DEBUG: Call test harness callback if it exists
      const onSignalRMessage = this.config.get('onSignalRMessage');
      if (onSignalRMessage) {
        onSignalRMessage(message);
      }
      
      this.handleUnifiedMessage(message);
    });
    
    // Catch-all debug handler
    this.connection.onmessage = (message) => {
      console.log(' SignalRClient: Raw message received:', message);
      
      // DEBUG: Log all SignalR message types we receive
      if (message && message.type === 1 && message.arguments) {
        console.log(' SIGNALR MESSAGE DEBUG:', {
          target: message.target,
          arguments: message.arguments,
          firstArgType: typeof message.arguments[0],
          firstArgKeys: message.arguments[0] ? Object.keys(message.arguments[0]) : 'none'
        });
      }
    };
    
    // DEBUG: Listen for ScenarioQueryMessage specifically
    this.connection.on('ScenarioQueryMessage', (message) => {
      console.log(' DEBUG: ScenarioQueryMessage received (this might be our DAG!):', message);
    });
    
    // Listen for ALL possible events for debugging
    ['AiResponseReceived', 'UnifiedAiMessage', 'ScenarioGenerated', 'ScenarioQueryMessage', 'BackTestExecuted'].forEach(eventName => {
      this.connection.on(eventName, (message) => {
        console.log(` SignalRClient: ${eventName} event received:`, message);
        
        // ENHANCED LOGGING FOR DAG DEBUGGING
        if (eventName === 'ScenarioGenerated') {
          console.log(' CRITICAL: ScenarioGenerated Event Received! ');
          console.log('Full message structure:', JSON.stringify(message, null, 2));
          console.log('Message properties:', {
            hasAiResponse: !!(message.aiResponse || message.AiResponse),
            aiResponseLength: (message.aiResponse || message.AiResponse)?.length,
            messageType: message.messageType || message.MessageType,
            originalEndpoint: message.originalEndpoint || message.OriginalEndpoint,
            scenarioId: message.scenarioId || message.ScenarioId,
            dagRequestId: message.dagRequestId || message.DagRequestId,
            requestId: message.requestId || message.RequestId,
            sessionId: message.sessionId || message.SessionId,
            statusCode: message.statusCode || message.StatusCode
          });
          
          // Try to parse DAG data if present
          const aiResponse = message.aiResponse || message.AiResponse;
          if (aiResponse) {
            try {
              const dagData = typeof aiResponse === 'string' ? JSON.parse(aiResponse) : aiResponse;
              console.log(' DAG Data successfully parsed:', {
                isDagArray: Array.isArray(dagData),
                dagBlockCount: Array.isArray(dagData) ? dagData.length : 'N/A',
                firstBlock: Array.isArray(dagData) && dagData.length > 0 ? dagData[0] : 'No blocks'
              });
            } catch (error) {
              console.error(' Failed to parse DAG data:', error);
            }
          }
          
          console.log(' NOTE: This ScenarioGenerated event is being received but NOT processed!');
          console.log(' It needs a proper handler to route it to handleUnifiedMessage()');
        }
        
        if (eventName === 'ScenarioQueryMessage') {
          console.log(' ScenarioQueryMessage Event Received!');
          console.log('Full message:', JSON.stringify(message, null, 2));
        }
      });
    });
    
    // Legacy handlers for backward compatibility
    this.connection.on('UnifiedAiMessage', (message) => {
      console.log('SignalRClient: UnifiedAiMessage (legacy)', message);
      this.handleUnifiedMessage(message);
    });
    
    // CRITICAL: Handle ScenarioGenerated event for DAG messages from /query endpoint
    this.connection.on('ScenarioGenerated', (message) => {
      console.log(' SignalRClient: ScenarioGenerated received (DAG from /query endpoint)', {
        messageType: message.messageType || message.MessageType,
        originalEndpoint: message.originalEndpoint || message.OriginalEndpoint,
        scenarioId: message.scenarioId || message.ScenarioId,
        hasAiResponse: !!(message.aiResponse || message.AiResponse),
        dagRequestId: message.dagRequestId || message.DagRequestId
      });
      
      // Transform to unified format and handle
      const unifiedMessage = {
        messageType: message.messageType || message.MessageType || 'Model',
        originalEndpoint: message.originalEndpoint || message.OriginalEndpoint || '/query',
        scenarioId: message.scenarioId || message.ScenarioId,
        sessionId: message.sessionId || message.SessionId,
        aiResponse: message.aiResponse || message.AiResponse,
        dagRequestId: message.dagRequestId || message.DagRequestId,
        relatedUserMessageId: message.relatedUserMessageId || message.RelatedUserMessageId,
        requestId: message.requestId || message.RequestId,
        statusCode: message.statusCode || message.StatusCode || 200
      };
      
      console.log(' SignalRClient: Processing ScenarioGenerated as unified message', unifiedMessage);
      this.handleUnifiedMessage(unifiedMessage);
    });
    
    // Scenario DAG messages
    this.connection.on('ScenarioQueryMessage', (message) => {
      console.log('SignalRClient: ScenarioQueryMessage received', message);
      this.handleScenarioQueryMessage(message);
    });
    
    // Scenario updates
    this.connection.on('ScenarioUpdated', (scenario) => {
      this.emit('scenarioUpdated', scenario);
    });
    
    // Error handling
    this.connection.on('Error', (error) => {
      this.emit('error', error);
    });
    
    // Connection lifecycle
    this.connection.onreconnecting((error) => {
      this.connected = false;
      this.reconnectAttempts++;
      console.warn('SignalRClient: Reconnecting...', error);
      this.emit('reconnecting', { attempt: this.reconnectAttempts, error });
    });
    
    this.connection.onreconnected((connectionId) => {
      this.connected = true;
      this.reconnectAttempts = 0;
      this.socketId = connectionId;
      console.log('SignalRClient: Reconnected', connectionId);
      this.emit('reconnected', connectionId);
      
      this.registerForMessages().catch(console.error);
    });
    
    this.connection.onclose((error) => {
      this.connected = false;
      console.log('SignalRClient: Connection closed', error);
      this.emit('disconnected', error);
    });
  }
  
  /**
   * Handle unified message routing based on message type
   */
  handleUnifiedMessage(message) {
    const { messageType, originalEndpoint, sessionId, scenarioId } = message;
    
    console.log(' SignalRClient: Processing message with messageType analysis:', {
      messageType,
      messageTypeType: typeof messageType,
      originalEndpoint,
      hasStepContent: message.aiResponse?.includes('Step 1'),
      aiResponsePreview: message.aiResponse?.substring(0, 100) + '...',
      aiResponseType: typeof message.aiResponse,
      fullMessage: JSON.stringify(message, null, 2)
    });
    
    // Stop thinking indicators based on endpoint
    if (this.thinkingManager) {
      const activeIndicators = Array.from(this.thinkingManager.activeIndicators.keys());
      console.log(' SignalR: Active indicators before stopping:', activeIndicators);
      
      if (originalEndpoint === '/chat') {
        console.log(' SignalR: Stopping chat thinking indicators for response from /chat endpoint');
        activeIndicators.forEach(id => {
          if (id.startsWith('chat-') || id.startsWith('template-')) {
            console.log(' SignalR: Stopping thinking indicator:', id);
            this.thinkingManager.stopThinking(id);
          }
        });
      } else if (originalEndpoint === '/query') {
        console.log(' SignalR: Stopping query thinking indicators for response from /query endpoint');
        activeIndicators.forEach(id => {
          if (id.startsWith('query-')) {
            console.log(' SignalR: Stopping thinking indicator:', id);
            this.thinkingManager.stopThinking(id);
          }
        });
      }
      
      const remainingIndicators = Array.from(this.thinkingManager.activeIndicators.keys());
      console.log(' SignalR: Remaining active indicators:', remainingIndicators);
    }
    
    // CRITICAL: Store SessionId from AI responses for conversation continuity
    // This matches main app behavior where SessionId comes from AI service
    if (sessionId) {
      console.log('SignalRClient: Storing SessionId from AI response:', sessionId);
      this.config.set('sessionId', sessionId);
      // Also update the message store's current session
      if (this.messageStore) {
        this.messageStore.currentSessionId = sessionId;
      }
    }
    
    // Track session lifecycle for Template journey
    if (originalEndpoint === '/chat') {
      if (messageType === 'Template') {
        // Template message starts a session
        this.messageStore.startSession(sessionId, message.templateName || 'Template');
      } else if (messageType === 'Model' && sessionId) {
        // Model message ends the template session
        this.messageStore.endSession(sessionId);
      }
    }
    
    // Route message based on type
    switch(messageType) {
      case 'Text':
      case 'Steps':
        this.handleChatMessage(message);
        break;
        
      case 'Template':
        this.handleTemplateMessage(message);
        break;
        
      case 'Model':
        this.handleModelMessage(message);
        break;
        
      case 'Analysis':
        this.handleAnalysisMessage(message);
        break;
        
      case 'Scenario':
      case 'Explanation':
        this.handleSpecialMessage(message);
        break;
        
      case 'Evaluation':
      case 'EvaluationComplete':
        this.handleEvaluationMessage(message);
        break;
        
      default:
        console.warn('SignalRClient: Unknown message type', messageType);
        this.handleChatMessage(message); // Fallback to chat
    }
    
    // Clear waiting state
    if (this.waitingForResponse) {
      this.waitingForResponse = false;
      this.emit('responseReceived', message);
    }
  }
  
  /**
   * Handle chat messages (Text, Steps)
   */
  handleChatMessage(message) {
    console.log(' SignalR: Handling chat message', {
      messageType: message.messageType,
      hasThinkingManager: !!this.thinkingManager
    });
    
    // Note: Thinking indicators are stopped at the top level in handleUnifiedMessage
    
    const chatMessage = {
      id: message.aiMessageId || message.id,
      content: message.aiResponse || message.message,
      messageType: message.messageType,
      type: 'ai',
      isAiResponse: true,
      scenarioId: message.scenarioId,
      sessionId: message.sessionId,
      timestamp: new Date().toISOString(),
      originalEndpoint: message.originalEndpoint,
      parentMessageId: message.relatedUserMessageId
    };
    
    this.messageStore.addMessage(chatMessage);
    this.emit('chatMessage', chatMessage);
  }
  
  /**
   * Handle template messages
   */
  handleTemplateMessage(message) {
    const templateMessage = {
      id: message.aiMessageId || message.id,
      content: message.aiResponse || message.templateContent,
      messageType: 'Template',
      type: 'ai',
      isAiResponse: true,
      templateName: message.templateName,
      scenarioId: message.scenarioId,
      sessionId: message.sessionId,
      timestamp: new Date().toISOString(),
      originalEndpoint: message.originalEndpoint
    };
    
    this.messageStore.addMessage(templateMessage);
    this.emit('templateMessage', templateMessage);
    
    // Template messages indicate we should expect a Model message next
    this.emit('templateReady', templateMessage);
  }
  
  /**
   * Handle model/DAG messages
   */
  handleModelMessage(message) {
    let dagData = null;
    
    try {
      if (typeof message.aiResponse === 'string') {
        dagData = JSON.parse(message.aiResponse);
      } else {
        dagData = message.aiResponse;
      }
    } catch (error) {
      console.error('SignalRClient: Failed to parse DAG data', error);
    }
    
    const modelMessage = {
      id: message.aiMessageId || message.dagRequestId,
      messageType: 'Model',
      type: 'ai',
      isAiResponse: true,
      scenarioId: message.scenarioId,
      sessionId: message.sessionId,
      dagRequestId: message.dagRequestId,
      dagData: dagData,
      aiResponse: message.aiResponse, // REQUIRED: DagProcessor expects this field
      timestamp: new Date().toISOString(),
      originalEndpoint: message.originalEndpoint,
      parentMessageId: message.relatedUserMessageId
    };
    
    this.messageStore.addMessage(modelMessage);
    
    // Emit different events based on the endpoint
    if (message.originalEndpoint === '/query') {
      // Interactive journey - manual DAG generation
      this.emit('dagGenerated', modelMessage);
    } else if (message.originalEndpoint === '/chat') {
      // Template journey - automated DAG generation
      this.emit('templateDagGenerated', modelMessage);
    }
    
    // Always emit a generic model event
    this.emit('modelMessage', modelMessage);
    
    // AUTO-EXECUTE: Step 4 of user journey - execute DAG after generation
    this.autoExecuteAfterDagGeneration(modelMessage);
  }
  
  /**
   * Auto-execute DAG after generation completes (Step 4 of user journey)
   * NOTE: Execution is now handled by DagProcessor, not here
   */
  async autoExecuteAfterDagGeneration(modelMessage) {
    // Check if auto-execution is enabled
    const autoExecute = this.config.get('autoExecuteAfterDag');
    if (autoExecute === false) {
      console.log('SignalRClient: Auto-execution disabled, skipping execution');
      return;
    }
    
    console.log('SignalRClient: DAG generation complete, execution will be handled by DagProcessor', {
      scenarioId: modelMessage.scenarioId,
      hasDagData: !!modelMessage.dagData
    });
    
    // The DagProcessor will handle the actual execution automatically
    // We just emit an event to notify that DAG generation is complete
    this.emit('dagGenerationComplete', {
      scenarioId: modelMessage.scenarioId,
      dagData: modelMessage.dagData,
      messageType: 'Model'
    });
  }
  
  /**
   * Handle analysis messages
   */
  handleAnalysisMessage(message) {
    let analysisData = null;
    let tableMarkdown = null;
    let tableJson = null;
    
    try {
      if (typeof message.aiResponse === 'string') {
        try {
          const parsed = JSON.parse(message.aiResponse);
          let textContent = parsed.text || parsed.message || message.aiResponse;
          
          // Handle double-encoded JSON in the text field
          if (typeof textContent === 'string' && textContent.startsWith('{')) {
            try {
              const innerParsed = JSON.parse(textContent);
              analysisData = innerParsed.text || innerParsed.message || textContent;
            } catch {
              analysisData = textContent; // Use as-is if can't parse
            }
          } else {
            analysisData = textContent;
          }
          
          tableMarkdown = parsed.tableMarkdown;
          tableJson = parsed.tableJson;
        } catch {
          analysisData = message.aiResponse;
        }
      } else {
        let textContent = message.aiResponse?.text || message.aiResponse?.message || '';
        
        // Handle double-encoded JSON in the text field  
        if (typeof textContent === 'string' && textContent.startsWith('{')) {
          try {
            const innerParsed = JSON.parse(textContent);
            analysisData = innerParsed.text || innerParsed.message || textContent;
          } catch {
            analysisData = textContent; // Use as-is if can't parse
          }
        } else {
          analysisData = textContent;
        }
        
        tableMarkdown = message.aiResponse?.tableMarkdown;
        tableJson = message.aiResponse?.tableJson;
      }
    } catch (error) {
      analysisData = message.aiResponse || '';
    }
    
    const analysisMessage = {
      id: message.aiMessageId || message.id,
      content: analysisData,
      messageType: 'Analysis',
      type: 'ai',
      isAiResponse: true,
      scenarioId: message.scenarioId,
      sessionId: message.sessionId,
      tableMarkdown,
      tableJson,
      timestamp: new Date().toISOString(),
      originalEndpoint: message.originalEndpoint,
      parentMessageId: message.relatedUserMessageId
    };
    
    this.messageStore.addMessage(analysisMessage);
    this.emit('analysisMessage', analysisMessage);
  }
  
  /**
   * Handle special messages (Scenario, Explanation)
   */
  handleSpecialMessage(message) {
    const specialMessage = {
      id: message.aiMessageId || message.id,
      content: message.aiResponse || message.message,
      messageType: message.messageType,
      type: 'ai',
      isAiResponse: true,
      scenarioId: message.scenarioId,
      sessionId: message.sessionId,
      timestamp: new Date().toISOString(),
      originalEndpoint: message.originalEndpoint,
      parentMessageId: message.relatedUserMessageId,
      fixedDag: message.fixedDag,
      fixedScenario: message.fixedScenario
    };
    
    this.messageStore.addMessage(specialMessage);
    this.emit(`${message.messageType.toLowerCase()}Message`, specialMessage);
  }
  
  /**
   * Handle evaluation messages
   */
  handleEvaluationMessage(message) {
    const evaluationMessage = {
      id: message.id,
      messageType: message.messageType,
      type: 'system',
      scenarioId: message.scenarioId,
      sessionId: message.sessionId,
      status: message.status,
      hasResults: message.hasResults,
      totalRecords: message.totalRecords,
      timestamp: new Date().toISOString()
    };
    
    this.messageStore.addMessage(evaluationMessage);
    this.emit('evaluationMessage', evaluationMessage);
    
    // Update scenario status
    if (message.scenarioId) {
      this.messageStore.updateScenarioStatus(message.scenarioId, message.status);
    }
  }
  
  /**
   * Handle scenario query messages (DAG generation)
   */
  handleScenarioQueryMessage(message) {
    console.log('SignalRClient: Processing ScenarioQueryMessage', message);
    
    // Transform to unified format and handle
    const unifiedMessage = {
      messageType: 'Model',
      originalEndpoint: '/query',
      scenarioId: message.scenarioId,
      dagRequestId: message.dagRequestId,
      aiResponse: message.aiData,
      relatedUserMessageId: message.relatedUserMessageId,
      statusCode: message.statusCode
    };
    
    this.handleUnifiedMessage(unifiedMessage);
  }
  
  /**
   * Handle legacy message format
   */
  handleLegacyMessage(message) {
    // Transform legacy format to unified format
    const unifiedMessage = {
      messageType: message.type || 'Text',
      originalEndpoint: '/chat',
      aiResponse: message.content || message.message,
      aiMessageId: message.id,
      scenarioId: message.scenarioId,
      timestamp: message.timestamp
    };
    
    this.handleUnifiedMessage(unifiedMessage);
  }
  
  /**
   * Register for message updates
   */
  async registerForMessages() {
    const scenarioId = this.config.get('scenarioId');
    if (scenarioId && this.connection) {
      try {
        await this.connection.invoke('JoinScenario', scenarioId);
        console.log(`SignalRClient: Joined scenario ${scenarioId}`);
      } catch (error) {
        console.error('SignalRClient: Failed to join scenario', error);
      }
    }
    
    // Also register for widget-specific channel if available
    const widgetId = this.config.get('widgetId');
    if (widgetId && this.connection) {
      try {
        await this.connection.invoke('JoinWidget', widgetId);
        console.log(`SignalRClient: Joined widget channel ${widgetId}`);
      } catch (error) {
        console.warn('SignalRClient: Widget channel not available', error);
      }
    }
  }
  
  /**
   * Set waiting state
   */
  setWaitingForResponse(waiting) {
    this.waitingForResponse = waiting;
    this.emit('waitingStateChanged', waiting);
  }
  
  /**
   * Get connection ID
   */
  getConnectionId() {
    return this.socketId || this.connection?.connectionId;
  }
  
  /**
   * Disconnect from SignalR hub
   */
  async disconnect() {
    if (this.connection) {
      try {
        await this.connection.stop();
        this.connected = false;
        this.connection = null;
        this.socketId = null;
        console.log('SignalRClient: Disconnected');
      } catch (error) {
        console.error('SignalRClient: Error during disconnect', error);
      }
    }
  }
  
  /**
   * Send a message via SignalR
   */
  async send(method, ...args) {
    if (!this.connected || !this.connection) {
      throw new Error('SignalRClient: Not connected');
    }
    
    try {
      return await this.connection.invoke(method, ...args);
    } catch (error) {
      console.error(`SignalRClient: Failed to invoke ${method}`, error);
      throw error;
    }
  }
  
  /**
   * Add event listener
   */
  on(event, callback) {
    if (!this.listeners.has(event)) {
      this.listeners.set(event, []);
    }
    
    this.listeners.get(event).push(callback);
    
    return () => {
      const callbacks = this.listeners.get(event);
      if (callbacks) {
        const index = callbacks.indexOf(callback);
        if (index !== -1) {
          callbacks.splice(index, 1);
        }
      }
    };
  }
  
  /**
   * Remove event listener
   */
  off(event, callback) {
    const callbacks = this.listeners.get(event);
    if (callbacks) {
      const index = callbacks.indexOf(callback);
      if (index !== -1) {
        callbacks.splice(index, 1);
      }
    }
  }
  
  /**
   * Emit event to listeners
   */
  emit(event, data) {
    const callbacks = this.listeners.get(event);
    if (callbacks) {
      callbacks.forEach(callback => {
        try {
          callback(data);
        } catch (error) {
          console.error(`SignalRClient: Error in ${event} listener`, error);
        }
      });
    }
  }
  
  /**
   * Get connection state
   */
  getState() {
    if (!this.connection) {
      return 'Disconnected';
    }
    
    switch (this.connection.state) {
      case signalR.HubConnectionState.Connected:
        return 'Connected';
      case signalR.HubConnectionState.Connecting:
        return 'Connecting';
      case signalR.HubConnectionState.Reconnecting:
        return 'Reconnecting';
      case signalR.HubConnectionState.Disconnected:
        return 'Disconnected';
      case signalR.HubConnectionState.Disconnecting:
        return 'Disconnecting';
      default:
        return 'Unknown';
    }
  }
  
  /**
   * Check if connected
   */
  isConnected() {
    return this.connected && this.connection?.state === signalR.HubConnectionState.Connected;
  }
}