/**
 * Enhanced Message Store
 * Extended state management with session tracking and milestone support
 */

// Use browser crypto API instead of uuid package for direct ES6 module loading
function uuidv4() {
  // Use modern crypto.randomUUID if available (most browsers support it now)
  if (crypto && crypto.randomUUID) {
    return crypto.randomUUID();
  }
  
  // Fallback for older browsers
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
    var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  });
}

export class EnhancedMessageStore {
  constructor() {
    this.messages = [];
    this.sessions = new Map(); // Track template sessions
    this.scenarios = new Map(); // Track scenario metadata
    this.currentMilestone = 0;
    this.journeyType = null; // 'interactive' | 'template'
    this.listeners = [];
    this.eventListeners = new Map(); // Event emitter functionality
    this.activeScenarioId = null;
    this.activeSessionId = null;
    this.currentSessionId = null; // Track ongoing conversation session for AI context
  }
  
  // ===== Event Emitter Methods =====
  
  /**
   * Add event listener
   */
  on(event, callback) {
    if (!this.eventListeners.has(event)) {
      this.eventListeners.set(event, []);
    }
    this.eventListeners.get(event).push(callback);
  }
  
  /**
   * Remove event listener
   */
  off(event, callback) {
    if (!this.eventListeners.has(event)) return;
    const listeners = this.eventListeners.get(event);
    const index = listeners.indexOf(callback);
    if (index > -1) {
      listeners.splice(index, 1);
    }
  }
  
  /**
   * Emit event to all listeners
   */
  emit(event, ...args) {
    if (!this.eventListeners.has(event)) return;
    const listeners = this.eventListeners.get(event);
    listeners.forEach(callback => {
      try {
        callback(...args);
      } catch (error) {
        console.error(`MessageStore: Error in event listener for ${event}:`, error);
      }
    });
  }
  
  // ===== Session Management =====
  
  /**
   * Start a new template session
   */
  startSession(sessionId, templateName, metadata = {}) {
    const session = {
      id: sessionId,
      templateName,
      startTime: Date.now(),
      endTime: null,
      status: 'active',
      milestones: {
        template: true,
        model: false,
        evaluation: false,
        analysis: false
      },
      metadata
    };
    
    this.sessions.set(sessionId, session);
    this.activeSessionId = sessionId;
    this.journeyType = 'template';
    
    this.notifyListeners('session:start', session);
    
    return session;
  }
  
  /**
   * End an active session
   */
  endSession(sessionId) {
    const session = this.sessions.get(sessionId);
    
    if (session) {
      session.endTime = Date.now();
      session.status = 'completed';
      
      if (this.activeSessionId === sessionId) {
        this.activeSessionId = null;
      }
      
      this.notifyListeners('session:end', session);
      
      return session;
    }
    
    return null;
  }
  
  /**
   * Get session by ID
   */
  getSession(sessionId) {
    return this.sessions.get(sessionId) || null;
  }
  
  /**
   * Get active session
   */
  getActiveSession() {
    return this.activeSessionId ? this.sessions.get(this.activeSessionId) : null;
  }
  
  // ===== Milestone Tracking =====
  
  /**
   * Update current milestone
   */
  updateMilestone(milestone) {
    const milestoneMap = {
      'explore': 0,
      'template': 1,
      'model': 2,
      'evaluate': 3,
      'analyze': 4,
      'backtest': 5
    };
    
    const milestoneValue = typeof milestone === 'string' 
      ? milestoneMap[milestone.toLowerCase()] 
      : milestone;
    
    this.currentMilestone = milestoneValue;
    
    // Update session milestones if in template journey
    if (this.activeSessionId) {
      const session = this.sessions.get(this.activeSessionId);
      if (session) {
        switch(milestoneValue) {
          case 1:
            session.milestones.template = true;
            break;
          case 2:
            session.milestones.model = true;
            break;
          case 3:
            session.milestones.evaluation = true;
            break;
          case 4:
            session.milestones.analysis = true;
            break;
        }
      }
    }
    
    this.notifyListeners('milestone:update', {
      milestone: milestoneValue,
      journeyType: this.journeyType
    });
    
    // Emit event for new event system
    this.emit('milestone:update', {
      milestone: milestoneValue,
      journeyType: this.journeyType
    });
    
    return milestoneValue;
  }
  
  /**
   * Get current milestone status
   */
  getMilestoneStatus() {
    return {
      current: this.currentMilestone,
      journeyType: this.journeyType,
      session: this.getActiveSession()
    };
  }
  
  // ===== Message Intelligence =====
  
  /**
   * Analyze message for required actions and UI state changes
   * This is the BRAIN of the widget - determines what buttons to show, when to trigger actions
   */
  analyzeMessageForActions(message) {
    const actions = {
      showGenerateButton: false,
      showRedrawButton: false,
      triggerEvaluation: false,
      startSession: false,
      endSession: false,
      updateMilestone: null,
      showApplyFix: false,
      enableAnalysis: false
    };
    
    if (!message || !message.messageType) {
      return actions;
    }
    
    switch(message.messageType) {
      case 'Steps':
        // Steps with "Step 1"  Show Generate button (unless already has DAG)
        if (message.message && message.message.includes('Step 1')) {
          actions.showGenerateButton = !message.userDagRequestId;
          actions.showRedrawButton = !!message.userDagRequestId;
          actions.updateMilestone = 'model'; // Ready to build model
          
          // Set journey type to interactive
          if (!this.journeyType) {
            this.journeyType = 'interactive';
          }
        }
        break;
        
      case 'Scenario':
        // Scenario message  Generate or Redraw based on DAG existence
        actions.showGenerateButton = !message.userDagRequestId;
        actions.showRedrawButton = !!message.userDagRequestId;
        actions.updateMilestone = 'model'; // Ready to build model
        
        // Set journey type to interactive
        if (!this.journeyType) {
          this.journeyType = 'interactive';
        }
        break;
        
      case 'Template':
        // Template message  Start automated session
        actions.startSession = true;
        actions.updateMilestone = 'template';
        this.journeyType = 'template';
        break;
        
      case 'Model':
        // Model message  Auto-trigger evaluation
        actions.triggerEvaluation = true;
        actions.updateMilestone = 'evaluate';
        
        // End session if from template journey
        if (message.originalEndpoint === '/chat' && this.journeyType === 'template') {
          actions.endSession = true;
        }
        break;
        
      case 'Analysis':
        // Analysis message  Enable further analysis options
        actions.enableAnalysis = true;
        actions.updateMilestone = 'analyze';
        break;
        
      case 'Explanation':
        // Explanation with fixes  Show Apply Fix button
        if (message.fixedDag || message.fixedScenario) {
          actions.showApplyFix = true;
        }
        break;
        
      case 'Text':
      default:
        // Regular text messages don't trigger special actions
        // But they do indicate conversation has started
        if (!this.journeyType) {
          this.journeyType = 'interactive';
        }
        break;
    }
    
    return actions;
  }
  
  /**
   * Find the most recent message that affects button visibility
   */
  getLastRelevantMessage() {
    return this.messages.findLast(msg => 
      msg.messageType === 'Steps' || 
      msg.messageType === 'Scenario' ||
      msg.messageType === 'Template'
    );
  }
  
  /**
   * Get current available actions based on message history
   */
  getCurrentAvailableActions() {
    const lastRelevantMessage = this.getLastRelevantMessage();
    
    if (!lastRelevantMessage) {
      return {
        showGenerateButton: false,
        showRedrawButton: false,
        showBacktestButton: false,
        enableAnalysis: false
      };
    }
    
    const actions = this.analyzeMessageForActions(lastRelevantMessage);
    
    // Add backtest button if we've completed evaluation
    actions.showBacktestButton = this.currentMilestone >= 3; // After evaluate
    
    return actions;
  }
  
  /**
   * Check if conversation is ready for scenario generation
   */
  isReadyForGeneration() {
    const actions = this.getCurrentAvailableActions();
    return actions.showGenerateButton;
  }
  
  // ===== DAG Metadata Storage =====
  
  /**
   * Store DAG metadata without visualization (we don't need ReactFlow)
   */
  storeDagMetadata(dagMetadata) {
    const { scenarioId, dagRequestId } = dagMetadata;
    
    // Store in scenario
    if (scenarioId) {
      const scenario = this.scenarios.get(scenarioId) || {};
      scenario.dagMetadata = dagMetadata;
      scenario.status = 'model_generated';
      this.scenarios.set(scenarioId, scenario);
    }
    
    // Create system message about DAG creation
    const dagSummaryMessage = {
      id: uuidv4(),
      type: 'system',
      messageType: 'DAGGenerated',
      content: this.formatDagSummary(dagMetadata),
      timestamp: Date.now(),
      scenarioId,
      dagRequestId,
      metadata: dagMetadata
    };
    
    this.addMessage(dagSummaryMessage);
    
    this.notifyListeners('dag:generated', dagMetadata);
    
    // Emit event for new event system
    this.emit('dag:generated', dagMetadata);
    
    return dagMetadata;
  }
  
  /**
   * Format DAG metadata into user-friendly summary
   */
  formatDagSummary(dagMetadata) {
    const { nodeCount, indicators = [], conditions = [], triggers = [] } = dagMetadata;
    
    let summary = ` **Model Created Successfully!**\n\n`;
    summary += ` **${nodeCount} components** generated\n\n`;
    
    if (indicators.length > 0) {
      summary += `**Indicators:**\n${indicators.map(i => ` ${i}`).join('\n')}\n\n`;
    }
    
    if (conditions.length > 0) {
      summary += `**Conditions:**\n${conditions.map(c => ` ${c}`).join('\n')}\n\n`;
    }
    
    if (triggers.length > 0) {
      summary += `**Triggers:**\n${triggers.map(t => ` ${t}`).join('\n')}\n\n`;
    }
    
    summary += ` **Starting evaluation...**`;
    
    return summary;
  }
  
  // ===== Scenario Management =====
  
  /**
   * Add or update scenario metadata
   */
  addScenario(scenarioId, metadata) {
    // Just store the scenario ID and any metadata we actually need
    // Backend MongoDB is the source of truth
    const scenario = {
      id: scenarioId,
      ...metadata
    };
    
    this.scenarios.set(scenarioId, scenario);
    this.activeScenarioId = scenarioId;
    
    this.notifyListeners('scenario:update', scenario);
    
    return scenario;
  }
  
  /**
   * Update scenario status
   */
  updateScenarioStatus(scenarioId, status) {
    const scenario = this.scenarios.get(scenarioId);
    
    if (scenario) {
      scenario.status = status;
      scenario.updatedAt = Date.now();
      
      this.notifyListeners('scenario:status', {
        scenarioId,
        status
      });
      
      return scenario;
    }
    
    return null;
  }
  
  /**
   * Get scenario by ID
   */
  getScenario(scenarioId) {
    return this.scenarios.get(scenarioId) || null;
  }
  
  // ===== Enhanced Message Management =====
  
  /**
   * Add a message with enhanced metadata
   */
  addMessage(message) {
    // Validate required fields - no fallbacks
    if (!message.id) {
      throw new Error('EnhancedMessageStore: Message ID is required');
    }
    
    const fullMessage = {
      id: message.id,
      timestamp: message.timestamp || new Date().toISOString(),
      scenarioId: message.scenarioId, // Don't fallback - should be explicit
      sessionId: message.sessionId, // Don't fallback - should be explicit
      journeyType: this.journeyType,
      milestone: this.currentMilestone,
      ...message
    };
    
    // Update current session ID from AI responses (for conversation continuity)
    if (message.isAiResponse && message.sessionId) {
      this.updateSessionId(message.sessionId);
    }
    
    this.messages.push(fullMessage);
    
    // Auto-detect milestone transitions
    this.detectMilestoneTransition(fullMessage);
    
    this.notifyListeners('message:add', fullMessage);
    
    // Emit event for new event system
    this.emit('message:added', fullMessage);
    
    return fullMessage;
  }
  
  /**
   * Detect and handle milestone transitions based on message type
   */
  detectMilestoneTransition(message) {
    switch(message.messageType) {
      case 'Template':
        this.updateMilestone('template');
        if (message.sessionId && !this.sessions.has(message.sessionId)) {
          this.startSession(message.sessionId, message.templateName || 'Unknown Template');
        }
        break;
        
      case 'Model':
      case 'Scenario':  // Also handle Scenario messages as they contain rephrased prompts
        this.updateMilestone('model');
        if (message.originalEndpoint === '/chat' && this.activeSessionId) {
          // Template journey - model marks session end
          setTimeout(() => this.endSession(this.activeSessionId), 100);
        }
        break;
        
      case 'Evaluation':
      case 'EvaluationComplete':
        this.updateMilestone('evaluate');
        break;
        
      case 'Analysis':
        this.updateMilestone('analyze');
        break;
    }
  }
  
  /**
   * Update a message in the store
   */
  updateMessage(messageId, updates) {
    const index = this.messages.findIndex(m => m.id === messageId);
    
    if (index !== -1) {
      this.messages[index] = {
        ...this.messages[index],
        ...updates,
        updatedAt: new Date().toISOString()
      };
      
      this.notifyListeners('message:update', this.messages[index]);
      
      return this.messages[index];
    }
    
    return null;
  }
  
  /**
   * Get conversation summary for scenario generation
   */
  getConversationSummary() {
    const recentMessages = this.messages
      .filter(m => m.type === 'user' || (m.type === 'ai' && m.messageType === 'Text'))
      .slice(-10); // Last 10 messages
    
    return recentMessages
      .map(m => `${m.type === 'user' ? 'User' : 'AI'}: ${m.content || m.message}`)
      .join('\n');
  }
  
  // ===== Journey Management =====
  
  /**
   * Set journey type
   */
  setJourneyType(type) {
    this.journeyType = type;
    this.notifyListeners('journey:change', type);
  }
  
  /**
   * Reset journey state
   */
  resetJourney() {
    this.currentMilestone = 0;
    this.journeyType = null;
    this.activeSessionId = null;
    
    this.notifyListeners('journey:reset', null);
  }
  
  // ===== Base Store Methods (inherited) =====
  
  /**
   * Remove a message from the store
   */
  removeMessage(messageId) {
    const index = this.messages.findIndex(m => m.id === messageId);
    
    if (index !== -1) {
      const removed = this.messages.splice(index, 1)[0];
      this.notifyListeners('message:remove', removed);
      return removed;
    }
    
    return null;
  }
  
  /**
   * Get all messages
   */
  getMessages() {
    return [...this.messages];
  }
  
  /**
   * Get message by ID
   */
  getMessage(messageId) {
    return this.messages.find(m => m.id === messageId) || null;
  }
  
  /**
   * Clear all messages
   */
  clear() {
    this.messages = [];
    this.sessions.clear();
    this.scenarios.clear();
    this.resetJourney();
    
    this.notifyListeners('store:clear', null);
  }
  
  /**
   * Subscribe to store changes
   */
  subscribe(listener) {
    if (typeof listener !== 'function') {
      throw new Error('MessageStore: Listener must be a function');
    }
    
    this.listeners.push(listener);
    
    // Return unsubscribe function
    return () => {
      const index = this.listeners.indexOf(listener);
      if (index !== -1) {
        this.listeners.splice(index, 1);
      }
    };
  }
  
  /**
   * Notify all listeners of changes
   */
  notifyListeners(action, data) {
    this.listeners.forEach(listener => {
      try {
        listener({
          action,
          data,
          messages: this.getMessages(),
          milestone: this.currentMilestone,
          journeyType: this.journeyType,
          activeSession: this.getActiveSession()
        });
      } catch (error) {
        console.error('MessageStore: Error in listener', error);
      }
    });
  }
  
  /**
   * Get messages by type
   */
  getMessagesByType(type) {
    return this.messages.filter(m => m.type === type || m.messageType === type);
  }
  
  /**
   * Get last message
   */
  getLastMessage() {
    return this.messages[this.messages.length - 1] || null;
  }
  
  /**
   * Get messages for active scenario
   */
  getScenarioMessages(scenarioId = null) {
    const targetScenarioId = scenarioId || this.activeScenarioId;
    
    if (!targetScenarioId) return [];
    
    return this.messages.filter(m => m.scenarioId === targetScenarioId);
  }
  
  /**
   * Get messages for session
   */
  getSessionMessages(sessionId = null) {
    const targetSessionId = sessionId || this.activeSessionId;
    
    if (!targetSessionId) return [];
    
    return this.messages.filter(m => m.sessionId === targetSessionId);
  }
  
  /**
   * Update current session ID from AI responses
   * This maintains conversation context like the original app
   */
  updateSessionId(sessionId) {
    if (sessionId && sessionId !== this.currentSessionId) {
      console.log('EnhancedMessageStore: Updating session ID:', {
        from: this.currentSessionId,
        to: sessionId
      });
      this.currentSessionId = sessionId;
      this.emit('session:updated', { sessionId: sessionId });
    }
  }
  
  /**
   * Get current session ID for new requests
   */
  getCurrentSessionId() {
    return this.currentSessionId;
  }
  
}