/**
 * Chat Container
 * Main chat interface component
 */

export class ChatContainer {
  constructor(targetElement, configManager, messageStore, apiClient, signalRClient) {
    this.element = targetElement;
    this.config = configManager;
    this.messageStore = messageStore;
    this.apiClient = apiClient;
    this.signalRClient = signalRClient;
    
    // Debug logging for messageStore
    console.log('ChatContainer constructor:', {
      messageStore: messageStore,
      messageStoreType: typeof messageStore,
      hasSubscribe: messageStore && typeof messageStore.subscribe === 'function',
      messageStoreKeys: messageStore ? Object.getOwnPropertyNames(messageStore) : 'null'
    });
    
    this.messagesContainer = null;
    this.inputContainer = null;
    this.inputField = null;
    this.sendButton = null;
    
    this.unsubscribe = null;
  }
  
  /**
   * Render the chat container
   */
  render() {
    console.log('ChatContainer: render() called, target element:', this.element);
    
    // Create HTML structure
    this.element.innerHTML = `
      <div class="tradr-widget-container">
        <div class="tradr-widget-header">
          <h3>TradrLab Assistant</h3>
          ${this.config.get('enableFontSizeControl') ? `
            <div class="tradr-widget-controls">
              <button class="tradr-font-size-btn" data-action="decrease">A-</button>
              <button class="tradr-font-size-btn" data-action="increase">A+</button>
            </div>
          ` : ''}
        </div>
        
        <div class="tradr-widget-messages" id="messages-container">
          <!-- Messages will be rendered here -->
        </div>
        
        <div class="tradr-widget-input">
          ${this.config.get('allowImageUpload') ? `
            <button class="tradr-attach-btn" title="Attach image">
              <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor">
                <path d="M21.44 11.05l-9.19 9.19a6 6 0 0 1-8.49-8.49l9.19-9.19a4 4 0 0 1 5.66 5.66l-9.2 9.19a2 2 0 0 1-2.83-2.83l8.49-8.48"/>
              </svg>
              <input type="file" accept="image/*" style="display: none;" />
            </button>
          ` : ''}
          
          <textarea 
            class="tradr-widget-input-field" 
            placeholder="${this.config.get('placeholder')}"
            rows="1"
          ></textarea>
          
          <button class="tradr-send-btn" title="Send message">
            <svg width="20" height="20" viewBox="0 0 24 24" fill="none" stroke="currentColor">
              <line x1="22" y1="2" x2="11" y2="13"></line>
              <polygon points="22 2 15 22 11 13 2 9 22 2"></polygon>
            </svg>
          </button>
        </div>
      </div>
    `;
    
    // Get references to elements
    console.log('ChatContainer: Looking for #messages-container in:', this.element);
    this.messagesContainer = this.element.querySelector('#messages-container');
    console.log('ChatContainer: Found messagesContainer:', this.messagesContainer);
    
    // FORCE VISIBILITY OF ENTIRE CHAT CONTAINER
    this.element.style.cssText = 'display: block !important; visibility: visible !important; position: relative !important; width: 100% !important; height: 400px !important; background: pink !important; border: 5px solid green !important;';
    
    if (this.messagesContainer) {
      this.messagesContainer.style.cssText = 'display: block !important; visibility: visible !important; height: 300px !important; background: lightblue !important; border: 3px solid purple !important; overflow-y: auto !important;';
    }
    
    this.inputField = this.element.querySelector('.tradr-widget-input-field');
    this.sendButton = this.element.querySelector('.tradr-send-btn');
    
    // Set up event listeners
    this.setupEventListeners();
    
    // Subscribe to message store updates
    if (this.messageStore && typeof this.messageStore.subscribe === 'function') {
      this.unsubscribe = this.messageStore.subscribe((update) => {
        this.handleStoreUpdate(update);
      });
    } else {
      console.error('ChatContainer: messageStore is null or does not have subscribe method', {
        messageStore: this.messageStore,
        messageStoreType: typeof this.messageStore,
        messageStoreConstructor: this.messageStore?.constructor?.name
      });
    }
    
    // Render initial messages
    this.renderMessages();
  }
  
  /**
   * Setup event listeners
   */
  setupEventListeners() {
    // Send button click
    this.sendButton.addEventListener('click', () => this.sendMessage());
    
    // Enter key in input field
    this.inputField.addEventListener('keydown', (e) => {
      if (e.key === 'Enter' && !e.shiftKey) {
        e.preventDefault();
        this.sendMessage();
      }
    });
    
    // Auto-resize textarea
    this.inputField.addEventListener('input', () => {
      this.autoResizeInput();
    });
    
    // Image upload
    if (this.config.get('allowImageUpload')) {
      const fileInput = this.element.querySelector('input[type="file"]');
      const attachBtn = this.element.querySelector('.tradr-attach-btn');
      
      attachBtn.addEventListener('click', () => fileInput.click());
      fileInput.addEventListener('change', (e) => this.handleImageUpload(e));
    }
    
    // Image paste
    if (this.config.get('allowImagePaste')) {
      this.inputField.addEventListener('paste', (e) => this.handlePaste(e));
    }
    
    // Font size controls
    if (this.config.get('enableFontSizeControl')) {
      this.element.querySelectorAll('.tradr-font-size-btn').forEach(btn => {
        btn.addEventListener('click', (e) => {
          const action = e.currentTarget.dataset.action;
          this.adjustFontSize(action);
        });
      });
    }
    
    // Message action buttons (retry, ask question, etc.)
    this.messagesContainer.addEventListener('click', (e) => {
      const button = e.target.closest('[data-action]');
      if (button) {
        const action = button.dataset.action;
        const scenarioId = button.dataset.scenarioId;
        this.handleMessageAction(action, scenarioId, button);
      }
    });
  }
  
  /**
   * Handle store updates
   */
  handleStoreUpdate(update) {
    console.log('ChatContainer: Store update received:', {
      action: update.action,
      messageType: update.data?.messageType || update.data?.type,
      content: update.data?.content?.substring(0, 50) + '...'
    });
    
    switch (update.action) {
      case 'add':
      case 'message:add':
        this.addMessageToUI(update.data);
        break;
      case 'update':
      case 'message:update':
        this.updateMessageInUI(update.data);
        break;
      case 'remove':
      case 'message:remove':
        this.removeMessageFromUI(update.data.id);
        break;
      case 'clear':
      case 'store:clear':
        this.clearMessages();
        break;
    }
  }
  
  /**
   * Handle message action button clicks
   */
  async handleMessageAction(action, scenarioId, button) {
    console.log('ChatContainer: Handling message action:', { action, scenarioId });
    
    try {
      // Disable button during action
      button.disabled = true;
      const originalText = button.textContent;
      button.textContent = 'Processing...';
      
      switch (action) {
        case 'retry-execution':
          await this.handleRetryExecution(scenarioId);
          break;
        case 'ask-question':
          this.handleAskQuestion(scenarioId);
          break;
        case 'retry':
          // Generic retry action
          await this.handleGenericRetry();
          break;
        default:
          console.warn('Unknown message action:', action);
      }
      
      // Re-enable button
      button.disabled = false;
      button.textContent = originalText;
      
    } catch (error) {
      console.error('Message action failed:', error);
      
      // Re-enable button and show error
      button.disabled = false;
      button.textContent = originalText;
      
      this.showActionError(action, error);
    }
  }
  
  /**
   * Handle retry execution action
   */
  async handleRetryExecution(scenarioId) {
    console.log('ChatContainer: Retrying execution for scenario:', scenarioId);
    
    // Get the enhanced chat widget to trigger re-execution
    const widget = this.element.closest('.tradr-widget')?.widgetInstance;
    if (widget && widget.retryExecution) {
      await widget.retryExecution(scenarioId);
    } else {
      throw new Error('Widget instance not found or retry method not available');
    }
  }
  
  /**
   * Handle ask question action
   */
  handleAskQuestion(scenarioId) {
    console.log('ChatContainer: Opening question input for scenario:', scenarioId);
    
    // Get the enhanced chat widget to show analysis input
    const widget = this.element.closest('.tradr-widget')?.widgetInstance;
    if (widget && widget.showAnalysisInput) {
      widget.showAnalysisInput({ id: scenarioId });
    } else {
      console.warn('Widget instance not found or showAnalysisInput method not available');
    }
  }
  
  /**
   * Handle generic retry action
   */
  async handleGenericRetry() {
    console.log('ChatContainer: Generic retry action triggered');
    // For generic retry, you might want to retry the last failed operation
    // Implementation depends on what should be retried
    throw new Error('Generic retry not implemented yet');
  }
  
  /**
   * Show error when action fails
   */
  showActionError(action, error) {
    // Add error message to chat
    this.messageStore.addMessage({
      id: `action-error-${Date.now()}`,
      type: 'error',
      messageType: 'Error',
      content: ` **${action} Failed**\n\n${error.message}\n\nPlease try again or contact support.`,
      timestamp: Date.now()
    });
  }
  
  /**
   * Render all messages
   */
  renderMessages() {
    const messages = this.messageStore.getMessages();
    this.messagesContainer.innerHTML = '';
    
    messages.forEach(message => {
      this.addMessageToUI(message, false);
    });
    
    this.scrollToBottom();
  }
  
  /**
   * Add message to UI - SIMPLIFIED
   */
  addMessageToUI(message, shouldScroll = true) {
    console.log('ChatContainer: Adding SIMPLE message:', message.content);
    console.log('ChatContainer: messagesContainer is:', this.messagesContainer);
    console.log('ChatContainer: messagesContainer parent:', this.messagesContainer?.parentElement);
    console.log('ChatContainer: messagesContainer is visible:', this.messagesContainer?.offsetParent !== null);
    
    // Create the simplest possible element
    const messageEl = document.createElement('div');
    messageEl.style.cssText = 'border: 3px solid red; padding: 20px; margin: 10px; background: yellow; color: black; font-size: 16px; width: 100%;';
    messageEl.textContent = `${message.type}: ${message.content}`;
    
    console.log('ChatContainer: Appending simple element to:', this.messagesContainer);
    this.messagesContainer.appendChild(messageEl);
    
    console.log('ChatContainer: Container now has children:', this.messagesContainer.children.length);
    console.log('ChatContainer: Last child is:', this.messagesContainer.lastElementChild);
  }
  
  /**
   * Render message content (with markdown support in future)
   */
  renderMessageContent(message) {
    // For now, just escape HTML
    // TODO: Add markdown rendering
    return this.escapeHtml(message.content || '');
  }
  
  /**
   * Update message in UI
   */
  updateMessageInUI(message) {
    const messageEl = this.messagesContainer.querySelector(`[data-message-id="${message.id}"]`);
    if (messageEl) {
      // Re-render the message
      // This is simplified - would need to maintain type-specific rendering
      messageEl.querySelector('.tradr-message-bubble').innerHTML = this.renderMessageContent(message);
    }
  }
  
  /**
   * Remove message from UI
   */
  removeMessageFromUI(messageId) {
    const messageEl = this.messagesContainer.querySelector(`[data-message-id="${messageId}"]`);
    if (messageEl) {
      messageEl.remove();
    }
  }
  
  /**
   * Clear all messages
   */
  clearMessages() {
    this.messagesContainer.innerHTML = '';
  }
  
  /**
   * Send message
   */
  async sendMessage() {
    const content = this.inputField.value.trim();
    
    if (!content) {
      return;
    }
    
    // Clear input
    this.inputField.value = '';
    this.autoResizeInput();
    
    // Add user message
    const userMessage = {
      type: 'user',
      content
    };
    
    this.messageStore.addMessage(userMessage);
    
    // Show loading
    const loadingMessage = {
      type: 'loading',
      content: ''
    };
    
    const loadingId = this.messageStore.addMessage(loadingMessage).id;
    
    try {
      // Send to API
      const response = await this.apiClient.sendMessage(content);
      
      // Remove loading
      this.messageStore.removeMessage(loadingId);
      
      // Add response
      if (response && response.length > 0) {
        response.forEach(msg => {
          this.messageStore.addMessage({
            type: 'assistant',
            ...msg
          });
        });
      }
      
    } catch (error) {
      // Remove loading
      this.messageStore.removeMessage(loadingId);
      
      // Show error
      this.messageStore.addMessage({
        type: 'error',
        content: 'Failed to send message. Please try again.'
      });
    }
  }
  
  /**
   * Handle image upload
   */
  async handleImageUpload(event) {
    const file = event.target.files[0];
    if (!file) return;
    
    try {
      const imageData = await this.apiClient.uploadImage(file);
      
      // Trigger callback
      const onImageUploaded = this.config.get('onImageUploaded');
      if (onImageUploaded) {
        onImageUploaded(imageData);
      }
      
      // Could add image to message or send immediately
      // This depends on UX requirements
      
    } catch (error) {
      console.error('Failed to upload image', error);
    }
  }
  
  /**
   * Handle paste event
   */
  handlePaste(event) {
    const items = event.clipboardData?.items;
    if (!items) return;
    
    for (const item of items) {
      if (item.type.indexOf('image') !== -1) {
        const file = item.getAsFile();
        if (file) {
          event.preventDefault();
          this.handleImageUpload({ target: { files: [file] } });
        }
      }
    }
  }
  
  /**
   * Auto-resize input field
   */
  autoResizeInput() {
    this.inputField.style.height = 'auto';
    this.inputField.style.height = Math.min(this.inputField.scrollHeight, 120) + 'px';
  }
  
  /**
   * Adjust font size
   */
  adjustFontSize(action) {
    const currentSize = parseInt(getComputedStyle(this.messagesContainer).fontSize);
    const newSize = action === 'increase' ? currentSize + 2 : currentSize - 2;
    
    if (newSize >= 12 && newSize <= 24) {
      this.messagesContainer.style.fontSize = newSize + 'px';
    }
  }
  
  /**
   * Scroll to bottom of messages
   */
  scrollToBottom() {
    this.messagesContainer.scrollTop = this.messagesContainer.scrollHeight;
  }
  
  /**
   * Escape HTML
   */
  escapeHtml(text) {
    const div = document.createElement('div');
    div.textContent = text;
    return div.innerHTML;
  }
  
  /**
   * Destroy the container
   */
  destroy() {
    // Unsubscribe from store
    if (this.unsubscribe) {
      this.unsubscribe();
    }
    
    // Clear element
    this.element.innerHTML = '';
  }
}