/**
 * Enhanced Chat Widget
 * The world's most intelligent embeddable trading widget!
 * Extended chat widget with full message intelligence and automatic workflows
 */

import { EnhancedMessageStore } from '../services/enhanced-message-store.js';
import { EnhancedSignalRClient } from '../services/enhanced-signalr-client.js';
import { ApiClient } from '../services/api-client.js';
import { ThinkingManager, thinkingIndicatorStyles } from '../services/thinking-manager.js';
import { DagProcessor } from '../services/dag-processor.js';
import { ActionManager } from '../services/action-manager.js';
import { ChatContainer } from './chat-container.js';
import { LandingPage } from './landing-page.js';
import { TemplateModal } from './template-modal.js';
import { ScenarioWizard } from './wizard-modal.js';
import { MessageRenderer } from './message-renderers.js';
import { MilestoneTracker } from './milestone-tracker.js';
import { withApiFlags, WIDGET_STATES } from '../core/widget-config.js';

export class EnhancedChatWidget {
  constructor(targetElement, configManager) {
    this.element = targetElement;
    this.config = configManager;
    this.id = null;
    this.initialized = false;
    this.currentState = WIDGET_STATES.INITIALIZING;
    
    // Core Services (The Widget's Brain!)
    this.messageStore = null;
    this.apiClient = null;
    this.signalRClient = null;
    this.thinkingManager = null;
    this.dagProcessor = null;
    this.actionManager = null;
    
    // Processing flags
    this.processingTemplate = false;
    
    // UI Components
    this.chatContainer = null;
    this.landingPage = null;
    this.templateModal = null;
    this.scenarioWizard = null;
    this.milestoneTracker = null;
    
    // Widget State
    this.waitingForResponse = false;
    this.showLandingPage = true;
    this.hasConversation = false;
    this.generatedScenarios = [];
    this.stylesInjected = false;
  }
  
  /**
   * Initialize the enhanced widget
   */
  async init() {
    try {
      if (this.initialized) {
        console.warn('EnhancedChatWidget: Already initialized');
        return;
      }
      
      this.setupTargetElement();
      this.initializeServices();
      await this.initializeUser();
      
      this.createComponents();
      await this.connectSignalR();
      
      this.initialized = true;
      this.currentState = WIDGET_STATES.LANDING;
      
      const onReady = this.config.get('onWidgetReady');
      if (onReady) {
        onReady(this);
      }
      
      this.showInitialView();
      
    } catch (error) {
      console.error('EnhancedChatWidget: Initialization failed', error);
      this.currentState = WIDGET_STATES.ERROR;
      this.handleError(error);
    }
  }
  
  /**
   * Setup target element
   */
  setupTargetElement() {
    this.element.classList.add('tradr-widget', 'tradr-widget--enhanced');
    this.element.classList.add(`tradr-widget-${this.config.get('theme')}`);
    
    this.element.style.width = this.config.get('width');
    this.element.style.height = this.config.get('height');
    
    // Store widget instance reference for action handling
    this.element.widgetInstance = this;
    
    this.element.style.setProperty('--tradr-primary-color', this.config.get('primaryColor'));
    this.element.style.setProperty('--tradr-font-family', this.config.get('fontFamily'));
  }
  
  /**
   * Initialize the widget's intelligent services (THE BRAIN!)
   */
  initializeServices() {
    console.log(' Initializing widget intelligence...');
    
    // Core message store with intelligence
    this.messageStore = new EnhancedMessageStore();
    
    // API client for backend communication
    this.apiClient = new ApiClient(this.config);
    
    // Thinking manager for progressive loading states
    this.thinkingManager = new ThinkingManager();
    
    // DAG processor for handling Model messages without visualization
    this.dagProcessor = new DagProcessor(this.messageStore, this.apiClient, this.thinkingManager, this.config);
    
    // Action manager for smart button logic
    this.actionManager = new ActionManager(this.messageStore, this.apiClient, this.thinkingManager);
    
    // Enhanced SignalR client with API client for auto-execution and thinking manager
    this.signalRClient = new EnhancedSignalRClient(this.config, this.messageStore, this.apiClient, this.thinkingManager);
    
    // Load blocks data for DAG transformation (async but don't wait)
    this.loadBlocksData();
    
    // Load currencies for symbol picker (async but don't wait)
    this.loadCurrencies();
    
    // Inject CSS styles for thinking indicators
    this.injectStyles();
    
    // Wire up the intelligence network
    this.wireIntelligenceEvents();
  }
  
  /**
   * Inject CSS styles for widget components
   */
  injectStyles() {
    if (this.stylesInjected) return;
    
    const styleSheet = document.createElement('style');
    styleSheet.textContent = thinkingIndicatorStyles;
    document.head.appendChild(styleSheet);
    
    this.stylesInjected = true;
    console.log(' Widget styles injected');
  }
  
  /**
   * Wire up all the intelligent event handling
   */
  wireIntelligenceEvents() {
    console.log(' Wiring intelligence events...');
    
    // === Message Store Intelligence Events ===
    this.messageStore.on('message:added', (message) => {
      console.log(' New message analyzed:', message.messageType);
      this.handleIntelligentMessage(message);
    });
    
    this.messageStore.on('milestone:update', (data) => {
      console.log(' Milestone updated:', data.milestone);
      this.handleMilestoneUpdate(data);
    });
    
    this.messageStore.on('dag:generated', (dagMetadata) => {
      console.log(' DAG generated:', dagMetadata);
      this.handleDagGenerated(dagMetadata);
    });
    
    // === SignalR Events ===
    // Listen for different message types from SignalR client
    this.signalRClient.on('chatMessage', (message) => {
      console.log(' SignalR chatMessage received:', message.messageType);
      this.handleSignalRMessage(message);
    });
    
    this.signalRClient.on('templateMessage', (message) => {
      console.log(' SignalR templateMessage received:', message.messageType);
      this.handleSignalRMessage(message);
    });
    
    this.signalRClient.on('modelMessage', (message) => {
      console.log(' SignalR modelMessage received:', message.messageType);
      this.handleSignalRMessage(message);
    });
    
    // Fallback: listen for generic responseReceived event
    this.signalRClient.on('responseReceived', (message) => {
      console.log(' SignalR responseReceived:', message.messageType);
      this.handleSignalRMessage(message);
    });
    
    this.signalRClient.on('connected', () => {
      console.log(' SignalR connected');
      this.onSignalRConnected();
    });
    
    this.signalRClient.on('disconnected', () => {
      console.log(' SignalR disconnected');
      this.onSignalRDisconnected();
    });
    
    // === Execution Events ===
    this.signalRClient.on('executionStarting', (data) => {
      console.log(' Execution starting:', data.scenarioId);
      this.handleExecutionStarting(data);
    });
    
    this.signalRClient.on('executionCompleted', (message) => {
      console.log(' Execution completed:', message.executionResult.Success);
      this.handleExecutionCompleted(message);
    });
    
    this.signalRClient.on('executionError', (message) => {
      console.log(' Execution failed:', message.error);
      this.handleExecutionError(message);
    });
    
    // === Action Manager Events ===
    this.actionManager.on('action:ask-question', (data) => {
      this.showAnalysisInput(data.scenario);
    });
    
    console.log(' Intelligence network online!');
  }
  
  /**
   * Load blocks data for DAG transformation and template selection
   */
  async loadBlocksData() {
    try {
      console.log(' Loading blocks data for widget...');
      
      const blocksData = await this.apiClient.loadBlocksData();
      
      console.log(' Blocks data loaded successfully', {
        totalBlocks: blocksData.totalBlocks,
        totalGroups: blocksData.totalGroups,
        totalTemplates: blocksData.totalTemplates
      });
      
      // Enable auto-execution by default
      this.config.set('autoExecuteAfterDag', true);
      
    } catch (error) {
      console.warn(' Failed to load blocks data - DAG execution may fail', error);
      
      // Disable auto-execution if blocks data failed to load
      this.config.set('autoExecuteAfterDag', false);
    }
  }

  /**
   * Load currencies for symbol picker
   */
  async loadCurrencies() {
    console.log(' Loading currencies for symbol picker...');
    
    try {
      const currencies = await this.apiClient.loadCurrencies();
      console.log(' Currencies loaded successfully', {
        totalCurrencies: currencies.length,
        groups: [...new Set(currencies.map(c => c.group))].length + ' groups'
      });
      
      // Set default symbol if not already set
      if (!this.config.get('defaultSymbol')) {
        this.config.set('defaultSymbol', 'EURUSD');
      }
      
      // Set default timeframe if not already set
      if (!this.config.get('defaultTimeframe')) {
        this.config.set('defaultTimeframe', '1H');
      }
      
    } catch (error) {
      console.warn(' Failed to load currencies - using default symbol', error);
      
      // Set fallback defaults
      this.config.set('defaultSymbol', 'EURUSD');
      this.config.set('defaultTimeframe', '1H');
    }
  }
  
  // ===== INTELLIGENT EVENT HANDLERS =====
  
  /**
   * Handle new messages with intelligence analysis
   */
  handleIntelligentMessage(message) {
    // Analyze message for required actions
    const actions = this.messageStore.analyzeMessageForActions(message);
    
    // Update milestone if needed
    if (actions.updateMilestone) {
      this.messageStore.updateMilestone(actions.updateMilestone);
    }
    
    // Start session if Template message
    if (actions.startSession && message.sessionId) {
      this.messageStore.startSession(message.sessionId, message.templateName || 'Template');
    }
    
    // End session if needed
    if (actions.endSession && message.sessionId) {
      this.messageStore.endSession(message.sessionId);
    }
    
    // Update journey type
    if (this.messageStore.journeyType && !this.hasConversation) {
      this.hasConversation = true;
      this.showLandingPage = false;
      this.updateMainUI();
    }
    
    // Update action buttons based on new message
    this.updateActionButtons();
    
    // Trigger evaluation if Model message
    if (actions.triggerEvaluation) {
      this.dagProcessor.processModelMessage(message);
    }
    
    // Reset template processing flag when we receive Template or Model response
    if (message.messageType === 'Template' || message.messageType === 'Model') {
      this.processingTemplate = false;
      console.log(' Template processing completed, allowing new template selections');
    }
    
    // Call external callbacks for appropriate message types
    this.handleMessageCallback(message);
  }
  
  /**
   * Handle milestone updates
   */
  handleMilestoneUpdate(data) {
    // Update milestone tracker UI
    if (this.milestoneTracker) {
      this.milestoneTracker.updateMilestone(data.milestone);
    }
    
    // Emit milestone event for external listeners
    const onMilestone = this.config.get('onMilestone');
    if (onMilestone) {
      onMilestone({
        milestone: data.milestone,
        journeyType: data.journeyType
      });
    }
  }
  
  /**
   * Handle DAG generation completion
   */
  handleDagGenerated(dagMetadata) {
    console.log(' DAG generated successfully!', dagMetadata);
    
    // Update UI to show DAG summary (already handled by storeDagMetadata)
    this.updateMainUI();
    
    // Update action buttons (backtest might now be available)
    this.updateActionButtons();
  }
  
  /**
   * Handle SignalR messages with intelligence
   */
  handleSignalRMessage(message) {
    console.log(' handleSignalRMessage called with:', {
      messageType: message.messageType,
      hasAiResponse: !!message.aiResponse,
      currentWaitingState: this.waitingForResponse
    });
    
    // Stop any thinking indicators for this request
    if (message.requestId) {
      this.thinkingManager.stopThinking(message.requestId);
    }
    
    if (message.scenarioId) {
      this.thinkingManager.stopThinking(`eval-${message.scenarioId}`);
    }
    
    // CRITICAL: Reset waiting state when content messages arrive
    // This matches the main app behavior - reset on each content message
    if (message.messageType && message.aiResponse) {
      console.log(' CONDITION MET: Resetting waiting state - AI response received:', message.messageType);
      console.log(' Before setWaitingState(false):', this.waitingForResponse);
      this.setWaitingState(false);
      console.log(' After setWaitingState(false):', this.waitingForResponse);
    } else {
      console.log(' CONDITION NOT MET:', {
        hasMessageType: !!message.messageType,
        hasAiResponse: !!message.aiResponse,
        messageType: message.messageType
      });
    }
    
    // Route based on message type
    // Note: SignalR client already added these messages to store in its handlers
    switch (message.messageType) {
      case 'Model':
        // Model messages are handled via actions.triggerEvaluation logic above
        // to avoid duplicate processing
        console.log(' Model message will be processed by triggerEvaluation logic');
        break;
        
      case 'Text':
      case 'Steps':
      case 'Scenario':
      case 'Template':
      case 'Analysis':
      case 'Explanation':
        // Message already added to store by SignalR client handlers
        console.log(' Message processed by SignalR client:', message.messageType);
        break;
        
      default:
        console.log(' Unknown message type received:', message.messageType);
        break;
    }
  }
  
  /**
   * Handle execution starting event
   */
  handleExecutionStarting(data) {
    // Show execution progress indicator
    const executionMessage = {
      id: `execution-starting-${Date.now()}`,
      content: ` Executing DAG with ${data.dagBlocks.length} blocks...`,
      messageType: 'ExecutionStatus',
      type: 'system',
      scenarioId: data.scenarioId,
      timestamp: new Date().toISOString(),
      isExecuting: true
    };
    
    this.messageStore.addMessage(executionMessage);
    this.updateChatDisplay();
    
    // Show thinking indicator for execution
    this.thinkingManager.showThinking(`execution-${data.scenarioId}`, 'Executing model...');
  }
  
  /**
   * Handle execution completed event
   */
  handleExecutionCompleted(message) {
    const result = message.executionResult;
    
    // Stop thinking indicator
    this.thinkingManager.stopThinking(`execution-${message.scenarioId}`);
    
    // Create result display message
    const resultMessage = {
      id: `execution-result-${Date.now()}`,
      content: this.formatExecutionResult(result),
      messageType: 'ExecutionResult',
      type: 'system',
      scenarioId: message.scenarioId,
      executionResult: result,
      timestamp: new Date().toISOString()
    };
    
    this.messageStore.addMessage(resultMessage);
    this.updateChatDisplay();
    
    console.log(' Execution completed and displayed:', {
      success: result.Success,
      records: result.TotalRecords,
      scenarioId: result.ScenarioId
    });
  }
  
  /**
   * Handle execution error event  
   */
  handleExecutionError(message) {
    // Stop thinking indicator
    this.thinkingManager.stopThinking(`execution-${message.scenarioId}`);
    
    // Create error display message
    const errorMessage = {
      id: `execution-error-${Date.now()}`,
      content: ` Execution failed: ${message.error}`,
      messageType: 'ExecutionError',
      type: 'system',
      scenarioId: message.scenarioId,
      error: message.error,
      timestamp: new Date().toISOString()
    };
    
    this.messageStore.addMessage(errorMessage);
    this.updateChatDisplay();
    
    console.error(' Execution failed and displayed:', message.error);
  }
  
  /**
   * Retry execution for a failed scenario
   */
  async retryExecution(scenarioId) {
    console.log(' Retrying execution for scenario:', scenarioId);
    
    try {
      // Check if we have stored scenario data for this scenario
      const storedData = this.dagProcessor.getStoredDagData(scenarioId);
      if (!storedData) {
        throw new Error('No scenario data found for retry. Please regenerate the model first.');
      }
      
      // Show thinking indicator
      const requestId = `retry-exec-${scenarioId}`;
      this.thinkingManager.startThinking(requestId, ' Retrying execution...');
      
      // Re-trigger evaluation using the stored scenario data
      // This will use the same DAG structure and parameters as the original execution
      await this.dagProcessor.triggerEvaluation(scenarioId, `retry-${Date.now()}`, storedData);
      
      console.log(' Retry execution initiated successfully');
      
    } catch (error) {
      console.error(' Retry execution failed:', error);
      
      // Stop thinking indicator
      this.thinkingManager.stopThinking(`retry-exec-${scenarioId}`);
      
      // Add retry error message
      this.messageStore.addMessage({
        id: `retry-error-${Date.now()}`,
        type: 'system',
        messageType: 'Error',
        content: ` **Retry Failed**\n\n${error.message}\n\nYou may need to regenerate the model or check the scenario parameters.`,
        timestamp: Date.now(),
        scenarioId: scenarioId
      });
      
      throw error; // Re-throw for the UI to handle
    }
  }
  
  /**
   * Format execution result for display
   */
  formatExecutionResult(result) {
    if (!result.Success) {
      return ` Execution failed: ${result.ErrorMessage || 'Unknown error'}`;
    }
    
    if (result.TotalRecords === 0) {
      return ` Execution completed but returned no data. Check your strategy parameters.`;
    }
    
    const dateRange = result.FirstDate && result.LastDate 
      ? `from ${result.FirstDate} to ${result.LastDate}`
      : '';
      
    return ` Execution completed successfully! 
 Found ${result.TotalRecords} trading opportunities ${dateRange}
 Columns: ${result.Columns.join(', ')}`;
  }
  
  /**
   * Update action buttons based on current state
   */
  updateActionButtons() {
    const actions = this.actionManager.getAvailableActions();
    const actionsContainer = this.element.querySelector('.tlw-actions-container');
    
    if (!actionsContainer) {
      console.warn('Actions container not found');
      return;
    }
    
    // Clear existing buttons
    actionsContainer.innerHTML = '';
    
    // Add available action buttons
    actions.forEach(action => {
      if (!action.visible) return;
      
      const button = document.createElement('button');
      button.className = `tlw-action-btn ${action.primary ? 'primary' : 'secondary'}`;
      button.innerHTML = action.label;
      button.title = action.description;
      button.disabled = action.disabled || this.actionManager.areActionsDisabled();
      
      button.addEventListener('click', async () => {
        try {
          console.log(' Executing action:', action.id);
          await action.action();
        } catch (error) {
          console.error(' Action failed:', error);
          this.handleActionError(action, error);
        }
      });
      
      actionsContainer.appendChild(button);
    });
    
    console.log(' Action buttons updated:', actions.map(a => a.id));
  }
  
  /**
   * Handle action execution errors
   */
  handleActionError(action, error) {
    // Add error message to chat
    this.messageStore.addMessage({
      id: `error-${Date.now()}`,
      type: 'system',
      messageType: 'Error',
      content: ` **${action.label} Failed**\n\n${error.message}\n\nPlease try again or contact support if the problem persists.`,
      timestamp: Date.now()
    });
    
    // Update UI
    this.updateMainUI();
  }
  
  /**
   * Update main UI based on current state
   */
  updateMainUI() {
    if (this.showLandingPage && !this.hasConversation) {
      this.showLandingView();
    } else {
      this.showChatView();
    }
  }
  
  /**
   * Show landing page view
   */
  showLandingView() {
    console.log(' Showing landing page');
    
    if (!this.landingPage) {
      this.landingPage = new LandingPage(
        this.element,
        this.config,
        this.apiClient,
        this.messageStore,
        this.thinkingManager
      );
    }
    
    // Hide chat container
    if (this.chatContainer && this.chatContainer.element) {
      this.chatContainer.element.style.display = 'none';
    }
    
    // Show landing page
    this.landingPage.render();
  }
  
  /**
   * Show chat view
   */
  showChatView() {
    console.log(' Showing chat view');
    
    if (!this.chatContainer) {
      this.chatContainer = new ChatContainer(
        this.element.querySelector('#chat-container'),
        this.config,
        this.messageStore,
        this.apiClient,
        this.signalRClient
      );
    }
    
    // Hide landing page
    if (this.landingPage && this.landingPage.element) {
      this.landingPage.element.style.display = 'none';
    }
    
    // Show chat container
    this.chatContainer.render();
    
    // Attach event listeners if not already attached
    this.attachChatEventListeners();
  }
  
  /**
   * Show analysis input for questions
   */
  showAnalysisInput(scenario) {
    // This could be a modal or inline input
    const question = prompt('What would you like to analyze?');
    
    if (question && question.trim()) {
      this.actionManager.sendAnalysisQuestion(question.trim())
        .catch(error => {
          console.error('Analysis question failed:', error);
        });
    }
  }
  
  /**
   * SignalR connection established
   */
  onSignalRConnected() {
    // Update connection status in UI if needed
    this.currentState = WIDGET_STATES.READY;
  }
  
  /**
   * SignalR connection lost
   */
  onSignalRDisconnected() {
    // Show reconnection indicator
    this.currentState = WIDGET_STATES.RECONNECTING;
  }
  
  /**
   * Initialize user (same as base widget)
   */
  async initializeUser() {
    const externalUserId = this.config.get('externalUserId');
    
    if (!externalUserId) {
      throw new Error('EnhancedChatWidget: External user ID is required');
    }
    
    try {
      // Get JWT token for SignalR authentication
      const tokenResponse = await this.apiClient.getJwtToken();
      
      this.config.set('userId', tokenResponse.userId);
      this.config.set('jwtToken', tokenResponse.jwtToken || tokenResponse.token);
      this.config.set('tokenExpires', tokenResponse.tokenExpires || tokenResponse.expiresAt);
      
      if (!this.config.get('signalRUrl')) {
        const apiBaseUrl = this.config.get('apiBaseUrl');
        this.config.set('signalRUrl', `${apiBaseUrl}/hubs/message`);
      }
      
    } catch (error) {
      console.error('EnhancedChatWidget: Failed to initialize user', error);
      throw error;
    }
  }
  
  /**
   * Create all components
   */
  createComponents() {
    // Create main container structure
    this.element.innerHTML = `
      <div class="tlw-widget-container">
        <div class="tlw-milestone-container" id="milestone-container"></div>
        <div class="tlw-landing-container" id="landing-container"></div>
        <div class="tlw-chat-container" id="chat-container"></div>
      </div>
    `;
    
    // Create milestone tracker
    this.createMilestoneTracker();
    
    // Create landing page
    this.createLandingPage();
    
    // Create chat container
    this.createChatContainer();
    
    // Create modals
    this.createModals();
  }
  
  /**
   * Create milestone tracker
   */
  createMilestoneTracker() {
    const container = this.element.querySelector('#milestone-container');
    this.milestoneTracker = new MilestoneTracker(container, {
      onMilestoneClick: (milestone) => this.handleMilestoneClick(milestone)
    });
  }
  
  /**
   * Create landing page
   */
  createLandingPage() {
    const container = this.element.querySelector('#landing-container');
    this.landingPage = new LandingPage(container, {
      onSelection: (selection) => this.handleLandingSelection(selection),
      onTemplateRequest: () => this.showTemplateModal(),
      onWizardRequest: () => this.showScenarioWizard(),
      showBranding: this.config.get('showBranding', true)
    });
  }
  
  /**
   * Create enhanced chat container
   */
  createChatContainer() {
    const container = this.element.querySelector('#chat-container');
    
    // Enhanced chat container with Generate Scenario button
    container.innerHTML = this.renderChatInterface();
    
    this.attachChatEventListeners();
  }
  
  /**
   * Render chat interface
   */
  renderChatInterface() {
    return `
      <div class="tlw-chat-header">
        <button class="tlw-back-btn" id="back-to-landing" ${!this.hasConversation ? 'style="display: none;"' : ''}>
           Back
        </button>
        <div class="tlw-chat-title">TradrLab Assistant</div>
      </div>
      
      <div class="tlw-messages" id="messages-container">
        ${this.renderMessages()}
      </div>
      
      <div class="tlw-chat-controls">
        ${this.renderGenerateButton()}
        <div class="tlw-input-container">
          <textarea 
            class="tlw-message-input" 
            id="message-input"
            placeholder="Ask a question or describe your trading idea..."
            rows="1"
          ></textarea>
          <button class="tlw-send-btn" id="send-button" ${this.waitingForResponse ? 'disabled' : ''}>
            ${this.waitingForResponse ? '' : 'Send'}
          </button>
        </div>
        ${this.waitingForResponse ? '<div class="tlw-thinking">AI is thinking...</div>' : ''}
      </div>
    `;
  }
  
  /**
   * Render Generate Scenario button
   */
  renderGenerateButton() {
    const canGenerate = this.hasConversation && !this.waitingForResponse && 
                       this.messageStore.journeyType === 'interactive';
    
    if (!canGenerate) {
      return '';
    }
    
    return `
      <div class="tlw-generate-container">
        <button class="tlw-generate-btn" id="generate-scenario">
           Generate Scenario
        </button>
        <span class="tlw-generate-hint">Ready to create your trading model?</span>
      </div>
    `;
  }
  
  /**
   * Render messages
   */
  renderMessages() {
    const messages = this.messageStore.getMessages();
    
    if (messages.length === 0) {
      return `
        <div class="tlw-welcome-message">
          <div class="tlw-welcome-icon"></div>
          <h3>Welcome to TradrLab!</h3>
          <p>I'm here to help you create and test trading scenarios. What would you like to build today?</p>
        </div>
      `;
    }
    
    return messages.map(message => this.renderMessage(message)).join('');
  }
  
  /**
   * Render individual message
   */
  renderMessage(message) {
    const renderer = MessageRenderer.create(message);
    return renderer.render();
  }
  
  /**
   * Attach chat event listeners
   */
  attachChatEventListeners() {
    // Back button
    const backBtn = this.element.querySelector('#back-to-landing');
    if (backBtn) {
      backBtn.addEventListener('click', () => this.showLandingPage());
    }
    
    // Generate scenario button
    const generateBtn = this.element.querySelector('#generate-scenario');
    if (generateBtn) {
      generateBtn.addEventListener('click', () => this.generateScenario());
    }
    
    // Message input
    const messageInput = this.element.querySelector('#message-input');
    const sendButton = this.element.querySelector('#send-button');
    
    if (messageInput && sendButton) {
      // Auto-resize textarea
      messageInput.addEventListener('input', () => {
        messageInput.style.height = 'auto';
        messageInput.style.height = Math.min(messageInput.scrollHeight, 120) + 'px';
      });
      
      // Send on Enter (but allow Shift+Enter for new lines)
      messageInput.addEventListener('keydown', (e) => {
        if (e.key === 'Enter' && !e.shiftKey) {
          e.preventDefault();
          this.sendMessage();
        }
      });
      
      // Send button click
      sendButton.addEventListener('click', () => this.sendMessage());
    }
  }
  
  /**
   * Create modal components
   */
  createModals() {
    // Template modal
    this.templateModal = new TemplateModal({
      apiClient: this.apiClient,
      onSelect: (template) => this.handleTemplateSelection(template),
      onClose: () => console.log('Template modal closed')
    });
    
    // Scenario wizard
    this.scenarioWizard = new ScenarioWizard({
      onComplete: (result) => this.handleWizardComplete(result),
      onClose: () => console.log('Wizard closed')
    });
  }
  
  /**
   * Connect to SignalR
   */
  async connectSignalR() {
    console.log(' EnhancedChatWidget: Calling connectSignalR()...');
    try {
      console.log(' EnhancedChatWidget: About to call signalRClient.connect()');
      await this.signalRClient.connect();
      console.log(' EnhancedChatWidget: SignalR connection completed');
    } catch (error) {
      console.error(' EnhancedChatWidget: SignalR connection failed', error);
    }
  }
  
  /**
   * Handle landing page selection
   */
  handleLandingSelection(selection) {
    this.showChatInterface();
    this.messageStore.setJourneyType('interactive');
    
    // Send the initial message
    setTimeout(() => {
      this.sendMessageToApi(selection.content);
    }, 300);
  }
  
  /**
   * Handle template selection
   */
  async handleTemplateSelection(template) {
    // Prevent duplicate template processing
    if (this.processingTemplate) {
      console.log(' Template processing already in progress, ignoring duplicate selection');
      return;
    }
    
    this.processingTemplate = true;
    this.showChatInterface();
    this.messageStore.setJourneyType('template');
    
    // Add template selection message to chat
    this.messageStore.addMessage({
      id: `template-${template.id}-${Date.now()}`,
      type: 'user',
      content: `Selected template: ${template.name}`,
      messageType: 'TemplateSelection',
      templateId: template.id,
      templateName: template.name
    });
    
    try {
      // Send template to API for processing
      await this.sendTemplateToApi(template);
    } catch (error) {
      console.error(' Template processing failed:', error);
    } finally {
      // Reset flag to allow new template selections
      this.processingTemplate = false;
    }
  }
  
  /**
   * Handle wizard completion
   */
  handleWizardComplete(result) {
    this.showChatInterface();
    this.messageStore.setJourneyType('interactive');
    
    // Add wizard result to chat
    this.messageStore.addMessage({
      id: `wizard-${Date.now()}`,
      type: 'user',
      content: result.prompt,
      messageType: 'WizardResult',
      wizardAnswers: result.answers
    });
    
    // Send to API
    setTimeout(() => {
      this.sendMessageToApi(result.prompt);
    }, 300);
  }
  
  /**
   * Send message
   */
  async sendMessage(content) {
    // If content is provided, send it directly (public API)
    if (content) {
      if (this.waitingForResponse) {
        return;
      }
      
      // Add user message
      this.messageStore.addMessage({
        id: `user-${Date.now()}`,
        type: 'user',
        content: content,
        messageType: 'Text'
      });
      
      this.hasConversation = true;
      await this.sendMessageToApi(content);
      return;
    }
    
    // Otherwise, get content from input field (internal UI)
    const input = this.element.querySelector('#message-input');
    if (!input) return;
    
    const inputContent = input.value.trim();
    
    if (!inputContent || this.waitingForResponse) {
      return;
    }
    
    // Clear input
    input.value = '';
    input.style.height = 'auto';
    
    // Add user message
    this.messageStore.addMessage({
      id: `user-${Date.now()}`,
      type: 'user',
      content: inputContent,
      messageType: 'Text'
    });
    
    this.hasConversation = true;
    await this.sendMessageToApi(inputContent);
  }
  
  /**
   * Send message to API
   */
  async sendMessageToApi(content) {
    try {
      this.setWaitingState(true);
      
      // Start thinking indicator for chat message with timeout handling
      const requestId = `chat-${Date.now()}`;
      
      // Define timeout handler for chat messages
      const handleChatTimeout = (reqId, shouldRetry) => {
        console.warn(` EnhancedChatWidget: Chat request ${reqId} timed out`);
        
        // Reset waiting state
        this.setWaitingState(false);
        
        // Add timeout error message
        this.messageStore.addMessage({
          id: `chat-timeout-${Date.now()}`,
          type: 'system',
          messageType: 'Error',
          content: ` **Request Timed Out**\n\nThe AI engine did not respond within the expected time.\n\n${shouldRetry ? 'Retrying...' : 'Please try sending your message again.'}`,
          timestamp: Date.now()
        });
        
        // If retry was requested, resend the message
        if (shouldRetry) {
          console.log(' EnhancedChatWidget: Retrying chat message after timeout');
          setTimeout(() => {
            this.sendMessageToApi(content);
          }, 1000);
        }
      };
      
      // Start thinking with 60 second timeout for chat
      this.thinkingManager.startThinking(requestId, '/chat', ' Thinking...', handleChatTimeout, 60000);
      
      // Ensure we have a ScenarioId before sending messages
      // Create scenario if this is the first message
      if (!this.config.get('scenarioId')) {
        console.log('EnhancedChatWidget: Creating scenario for first message');
        await this.apiClient.createScenario('New Scenario');
      }
      
      const socketId = this.signalRClient.getConnectionId();
      
      // Use ApiClient.sendMessage to handle ScenarioId generation and storage
      await this.apiClient.sendMessage(content, {
        messageType: 'Text',
        socketId: socketId
      });
      
      // Note: Thinking indicator will be stopped when we receive the AI response via SignalR
      
    } catch (error) {
      console.error('EnhancedChatWidget: Failed to send message', error);
      this.setWaitingState(false);
      // Stop thinking indicator on error
      this.thinkingManager.stopAll();
      this.handleError(error);
    }
  }
  
  /**
   * Send template to API
   */
  async sendTemplateToApi(template) {
    try {
      this.setWaitingState(true);
      
      // Start thinking indicator with template-specific message
      const requestId = `template-${Date.now()}`;
      const templateMessage = ` Processing template: ${template.name}...`;
      console.log(' EnhancedChatWidget: Starting template thinking indicator', { templateName: template.name });
      
      // Define timeout handler for template processing
      const handleTemplateTimeout = (reqId, shouldRetry) => {
        console.warn(` EnhancedChatWidget: Template request ${reqId} timed out`);
        
        // Reset waiting state
        this.setWaitingState(false);
        
        // Add timeout error message
        this.messageStore.addMessage({
          id: `template-timeout-${Date.now()}`,
          type: 'system',
          messageType: 'Error',
          content: ` **Template Processing Timed Out**\n\nThe AI engine did not respond to template "${template.name}" within the expected time.\n\n${shouldRetry ? 'Retrying...' : 'Please try selecting the template again.'}`,
          timestamp: Date.now()
        });
        
        // If retry was requested, resend the template
        if (shouldRetry) {
          console.log(' EnhancedChatWidget: Retrying template after timeout');
          setTimeout(() => {
            this.sendTemplateToApi(template);
          }, 1000);
        }
      };
      
      // Start thinking with template message and 60 second timeout
      this.thinkingManager.startThinking(requestId, '/chat', templateMessage, handleTemplateTimeout, 60000);
      
      // Ensure we have a ScenarioId before sending messages
      // Create scenario if this is the first message
      if (!this.config.get('scenarioId')) {
        console.log('EnhancedChatWidget: Creating scenario for template');
        await this.apiClient.createScenario(template.name);
      }
      
      const socketId = this.signalRClient.getConnectionId();
      
      // Send only the template description to the backend for DAG generation
      // This ensures the AI engine gets clean trading logic without template metadata
      await this.apiClient.sendMessage(template.description, {
        messageType: 'TemplateSelection',
        socketId: socketId,
        templateId: template.id,
        templateName: template.name
      });
      
    } catch (error) {
      console.error('EnhancedChatWidget: Failed to send template', error);
      this.setWaitingState(false);
      this.handleError(error);
    }
  }
  
  /**
   * Generate scenario from conversation
   */
  async generateScenario() {
    try {
      console.log(' GENERATE SCENARIO: Starting DAG generation process');
      
      this.setWaitingState(true);
      this.messageStore.updateMilestone('model');
      
      const conversationSummary = this.messageStore.getConversationSummary();
      const socketId = this.signalRClient.getConnectionId();
      const scenarioId = this.config.get('scenarioId');
      const sessionId = this.config.get('sessionId');
      const externalUserId = this.config.get('externalUserId');
      
      console.log(' GENERATE SCENARIO: Current IDs:', {
        socketId: socketId,
        scenarioId: scenarioId,
        sessionId: sessionId,
        externalUserId: externalUserId,
        conversationLength: conversationSummary?.length
      });
      
      // Use the dedicated generateScenario method instead of generic post()
      // This ensures ExternalUserId is included properly
      const response = await this.apiClient.generateScenario({
        conversationContext: conversationSummary,
        socketId: socketId,
        sessionId: sessionId,
        scenarioId: scenarioId
      });
      
      console.log(' GENERATE SCENARIO: Response received from backend:', response);
      console.log(' GENERATE SCENARIO: Now waiting for DAG via SignalR...');
      
    } catch (error) {
      console.error(' GENERATE SCENARIO ERROR:', error);
      this.setWaitingState(false);
      this.handleError(error);
    }
  }
  
  /**
   * Handle message store events
   */
  handleMessageStoreEvent(event) {
    switch(event.action) {
      case 'message:add':
        this.updateChatDisplay();
        break;
        
      case 'milestone:update':
        this.milestoneTracker.updateMilestone(event.data.milestone);
        break;
        
      case 'session:start':
      case 'session:end':
        this.updateChatDisplay();
        break;
    }
  }
  
  /**
   * Handle different message types from SignalR
   */
  handleChatMessage(message) {
    this.updateChatDisplay();
  }
  
  handleTemplateMessage(message) {
    this.updateChatDisplay();
  }
  
  handleModelMessage(message) {
    this.generatedScenarios.push(message);
    this.updateChatDisplay();
  }
  
  handleAnalysisMessage(message) {
    this.updateChatDisplay();
  }
  
  handleTemplateReady(message) {
    // Template is ready for processing
    console.log('Template ready for processing:', message);
  }
  
  handleDagGenerated(message) {
    // DAG has been generated
    console.log('DAG generated:', message);
    this.messageStore.updateMilestone('evaluate');
  }
  
  /**
   * Set waiting state
   */
  setWaitingState(waiting) {
    this.waitingForResponse = waiting;
    this.signalRClient.setWaitingForResponse(waiting);
    
    if (this.currentState === WIDGET_STATES.CHATTING) {
      this.updateChatControls();
    }
  }
  
  /**
   * Update chat display
   */
  updateChatDisplay() {
    const container = this.element.querySelector('#chat-container');
    if (container) {
      container.innerHTML = this.renderChatInterface();
      this.attachChatEventListeners();
      
      // Scroll to bottom
      const messagesContainer = container.querySelector('#messages-container');
      if (messagesContainer) {
        messagesContainer.scrollTop = messagesContainer.scrollHeight;
      }
    }
  }
  
  /**
   * Update only chat controls (more efficient)
   */
  updateChatControls() {
    const controls = this.element.querySelector('.tlw-chat-controls');
    if (controls) {
      const generateContainer = controls.querySelector('.tlw-generate-container');
      const sendButton = controls.querySelector('#send-button');
      const thinkingDiv = controls.querySelector('.tlw-thinking');
      
      // Update generate button
      const shouldShowGenerate = this.hasConversation && !this.waitingForResponse && 
                                this.messageStore.journeyType === 'interactive';
      
      if (generateContainer) {
        generateContainer.style.display = shouldShowGenerate ? 'block' : 'none';
      }
      
      // Update send button
      if (sendButton) {
        sendButton.disabled = this.waitingForResponse;
        sendButton.textContent = this.waitingForResponse ? '' : 'Send';
      }
      
      // Update thinking indicator
      if (thinkingDiv) {
        thinkingDiv.style.display = this.waitingForResponse ? 'block' : 'none';
      }
    }
  }
  
  /**
   * Show initial view
   */
  showInitialView() {
    if (this.showLandingPage) {
      this.showLandingView();
    } else {
      this.showChatView();
    }
  }
  
  /**
   * Show landing page
   */
  showLandingPageView() {
    this.currentState = WIDGET_STATES.LANDING;
    
    const landingContainer = this.element.querySelector('#landing-container');
    const chatContainer = this.element.querySelector('#chat-container');
    
    if (landingContainer) {
      landingContainer.style.display = 'block';
      this.landingPage.show();
    }
    
    if (chatContainer) {
      chatContainer.style.display = 'none';
    }
  }
  
  /**
   * Show chat interface
   */
  showChatInterface() {
    this.currentState = WIDGET_STATES.CHATTING;
    
    const landingContainer = this.element.querySelector('#landing-container');
    const chatContainer = this.element.querySelector('#chat-container');
    
    if (landingContainer) {
      landingContainer.style.display = 'none';
      this.landingPage.hide();
    }
    
    if (chatContainer) {
      chatContainer.style.display = 'block';
    }
    
    this.updateChatDisplay();
    
    // Focus input
    setTimeout(() => {
      const input = this.element.querySelector('#message-input');
      if (input) {
        input.focus();
      }
    }, 100);
  }
  
  /**
   * Show template modal
   */
  showTemplateModal() {
    this.templateModal.show();
  }
  
  /**
   * Show scenario wizard
   */
  showScenarioWizard() {
    this.scenarioWizard.show();
  }
  
  /**
   * Handle milestone click
   */
  handleMilestoneClick(milestone) {
    console.log('Milestone clicked:', milestone);
    // Could show milestone-specific information or navigation
  }
  
  /**
   * Handle errors
   */
  handleError(error) {
    const onError = this.config.get('onError');
    if (onError) {
      onError(error);
    } else {
      this.messageStore.addMessage({
        id: `error-${Date.now()}`,
        type: 'error',
        content: 'An error occurred. Please try again.',
        messageType: 'Error'
      });
    }
  }
  
  /**
   * Handle message callbacks (for external callbacks like onMessageReceived)
   */
  handleMessageCallback(message) {
    console.log(' Processing message callback:', message.messageType);
    
    // Call onMessageReceived callback for AI messages (system messages, execution results, etc.)
    if (message.type === 'ai' || message.type === 'system') {
      const callback = this.config.get('onMessageReceived');
      if (callback && typeof callback === 'function') {
        console.log(' Calling onMessageReceived for:', message.messageType);
        callback(message);
      }
    }
  }
  
  /**
   * Handle milestone updates
   */
  handleMilestoneUpdate(data) {
    console.log(' Milestone updated:', data.milestone);
    // Milestone updates are handled internally by the widget
  }
  
  /**
   * Handle DAG generation events
   */
  handleDagGenerated(dagMetadata) {
    console.log(' DAG generated:', dagMetadata.scenarioId);
    // DAG generation is handled by the DagProcessor
  }
  
  /**
   * Destroy the widget
   */
  destroy() {
    try {
      if (this.signalRClient) {
        this.signalRClient.disconnect();
      }
      
      if (this.templateModal) {
        this.templateModal.destroy();
      }
      
      if (this.scenarioWizard) {
        this.scenarioWizard.destroy();
      }
      
      this.element.innerHTML = '';
      this.element.classList.remove('tradr-widget', 'tradr-widget--enhanced', 
                                   `tradr-widget-${this.config.get('theme')}`);
      
      this.initialized = false;
      this.currentState = WIDGET_STATES.DISCONNECTED;
      
    } catch (error) {
      console.error('EnhancedChatWidget: Error during destruction', error);
    }
  }
  
  /**
   * Get widget state
   */
  getState() {
    return {
      id: this.id,
      initialized: this.initialized,
      currentState: this.currentState,
      messages: this.messageStore ? this.messageStore.getMessages() : [],
      milestone: this.messageStore ? this.messageStore.currentMilestone : 0,
      journeyType: this.messageStore ? this.messageStore.journeyType : null,
      hasConversation: this.hasConversation,
      waitingForResponse: this.waitingForResponse,
      generatedScenarios: this.generatedScenarios
    };
  }
}

export default EnhancedChatWidget;