.tradr-widget-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 16px;
  border-bottom: 1px solid var(--tradr-border-color);
  background: var(--tradr-background-color);
}

.tradr-widget-header h3 {
  margin: 0;
  font-size: 16px;
  font-weight: 600;
