  border-bottom-left-radius: 4px;
  /* DEBUG: Force visibility */
  border: 2px solid red !important;
  min-height: 20px !important;
  display: block !important;
}

.tradr-message-error .tradr-message-bubble {
  background: #ffebee;
  color: var(--tradr-error-color);
  border: 1px solid var(--tradr-error-color);
}

/* Loading Animation */
.tradr-loading-dots {
  display: flex;
  gap: 4px;
}

.tradr-loading-dots span {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--tradr-primary-color);
  animation: bounce 1.4s infinite ease-in-out;
}

.tradr-loading-dots span:nth-child(1) {
  animation-delay: -0.32s;
}

.tradr-loading-dots span:nth-child(2) {
  animation-delay: -0.16s;
