  font-family: var(--tradr-font-family);
  font-size: 14px;
  resize: none;
  outline: none;
  transition: border-color 0.2s;
}

.tradr-widget-input-field:focus {
  border-color: var(--tradr-primary-color);
}

.tradr-send-btn,
.tradr-attach-btn {
  width: 36px;
  height: 36px;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  border-radius: 50%;
  background: var(--tradr-primary-color);
  color: white;
  cursor: pointer;
  transition: background-color 0.2s;
  position: relative;
}

.tradr-send-btn:hover,
.tradr-attach-btn:hover {
