/**
 * Landing Page Component
 * Entry point for both Interactive and Template journeys
 */

export class LandingPage {
  constructor(container, options = {}) {
    this.container = container;
    this.options = {
      onSelection: options.onSelection || (() => {}),
      onTemplateRequest: options.onTemplateRequest || (() => {}),
      onWizardRequest: options.onWizardRequest || (() => {}),
      showBranding: options.showBranding !== false,
      ...options
    };
    
    this.isVisible = false;
    this.templates = [];
    
    this.init();
  }
  
  /**
   * Initialize the landing page
   */
  init() {
    this.container.innerHTML = this.render();
    this.attachEventListeners();
  }
  
  /**
   * Render the landing page HTML
   */
  render() {
    return `
      <div class="tlw-landing" data-testid="landing-page">
        ${this.renderHeader()}
        ${this.renderCards()}
        ${this.renderFooter()}
      </div>
    `;
  }
  
  /**
   * Render header section
   */
  renderHeader() {
    return `
      <div class="tlw-landing-header">
        ${this.options.showBranding ? `
          <div class="tlw-branding">
            <span class="tlw-logo"></span>
            <h1 class="tlw-title">TradrLab</h1>
          </div>
        ` : ''}
        <h2 class="tlw-subtitle">What would you like to build today?</h2>
        <p class="tlw-description">
          Create and test trading scenarios using AI-powered analysis
        </p>
      </div>
    `;
  }
  
  /**
   * Render journey selection cards
   */
  renderCards() {
    return `
      <div class="tlw-cards">
        ${this.renderCard({
          action: 'help',
          icon: '',
          title: 'Help me get started',
          description: 'AI-guided scenario creation',
          subtitle: 'Interactive journey',
          color: 'blue'
        })}
        
        ${this.renderCard({
          action: 'templates',
          icon: '',
          title: 'View Templates',
          description: 'Pre-built trading scenarios',
          subtitle: 'Quick start',
          color: 'green'
        })}
        
        ${this.renderCard({
          action: 'scratch',
          icon: '',
          title: 'Build from Scratch',
          description: 'Custom scenario creation',
          subtitle: 'Advanced users',
          color: 'purple'
        })}
        
        ${this.renderCard({
          action: 'wizard',
          icon: '',
          title: 'Scenario Builder',
          description: 'Step-by-step guided creation',
          subtitle: 'Structured approach',
          color: 'orange'
        })}
      </div>
    `;
  }
  
  /**
   * Render individual card
   */
  renderCard({ action, icon, title, description, subtitle, color }) {
    return `
      <div class="tlw-card tlw-card--${color}" data-action="${action}" data-testid="card-${action}">
        <div class="tlw-card-icon">${icon}</div>
        <div class="tlw-card-content">
          <h3 class="tlw-card-title">${title}</h3>
          <p class="tlw-card-description">${description}</p>
          <span class="tlw-card-subtitle">${subtitle}</span>
        </div>
        <div class="tlw-card-arrow"></div>
      </div>
    `;
  }
  
  /**
   * Render footer section
   */
  renderFooter() {
    return `
      <div class="tlw-landing-footer">
        <p class="tlw-help-text">
          Need help? <a href="#" class="tlw-help-link" data-action="help-docs">View documentation</a>
        </p>
        <div class="tlw-version">
          Widget v2.0.0
        </div>
      </div>
    `;
  }
  
  /**
   * Attach event listeners
   */
  attachEventListeners() {
    // Card click handlers
    this.container.querySelectorAll('.tlw-card').forEach(card => {
      card.addEventListener('click', (e) => {
        const action = card.dataset.action;
        this.handleCardClick(action, e);
      });
      
      // Add hover effects
      card.addEventListener('mouseenter', () => {
        card.classList.add('tlw-card--hover');
      });
      
      card.addEventListener('mouseleave', () => {
        card.classList.remove('tlw-card--hover');
      });
    });
    
    // Help link
    const helpLink = this.container.querySelector('.tlw-help-link');
    if (helpLink) {
      helpLink.addEventListener('click', (e) => {
        e.preventDefault();
        this.handleHelpRequest();
      });
    }
    
    // Keyboard navigation
    this.container.addEventListener('keydown', (e) => {
      this.handleKeyNavigation(e);
    });
  }
  
  /**
   * Handle card click events
   */
  handleCardClick(action, event) {
    // Add click animation
    const card = event.currentTarget;
    card.classList.add('tlw-card--clicked');
    setTimeout(() => card.classList.remove('tlw-card--clicked'), 150);
    
    switch(action) {
      case 'help':
        this.handleHelpSelection();
        break;
        
      case 'templates':
        this.handleTemplatesSelection();
        break;
        
      case 'scratch':
        this.handleScratchSelection();
        break;
        
      case 'wizard':
        this.handleWizardSelection();
        break;
        
      default:
        console.warn('Unknown action:', action);
    }
  }
  
  /**
   * Handle "Help me get started" selection
   */
  handleHelpSelection() {
    const message = {
      type: 'message',
      content: 'I need help expressing my idea as a testable trading scenario. Can you guide me through the process?',
      journeyType: 'interactive'
    };
    
    this.options.onSelection(message);
    this.hide();
  }
  
  /**
   * Handle "View Templates" selection
   */
  handleTemplatesSelection() {
    this.options.onTemplateRequest();
  }
  
  /**
   * Handle "Build from Scratch" selection
   */
  handleScratchSelection() {
    const message = {
      type: 'message',
      content: 'I want to build a custom trading scenario from scratch.',
      journeyType: 'interactive'
    };
    
    this.options.onSelection(message);
    this.hide();
  }
  
  /**
   * Handle "Scenario Builder" selection
   */
  handleWizardSelection() {
    this.options.onWizardRequest();
  }
  
  /**
   * Handle help documentation request
   */
  handleHelpRequest() {
    const message = {
      type: 'message',
      content: 'Can you explain how to use this trading scenario builder?',
      journeyType: 'interactive'
    };
    
    this.options.onSelection(message);
    this.hide();
  }
  
  /**
   * Handle keyboard navigation
   */
  handleKeyNavigation(event) {
    const cards = Array.from(this.container.querySelectorAll('.tlw-card'));
    const currentFocus = this.container.querySelector('.tlw-card:focus');
    let currentIndex = currentFocus ? cards.indexOf(currentFocus) : -1;
    
    switch(event.key) {
      case 'ArrowRight':
      case 'ArrowDown':
        event.preventDefault();
        currentIndex = (currentIndex + 1) % cards.length;
        cards[currentIndex].focus();
        break;
        
      case 'ArrowLeft':
      case 'ArrowUp':
        event.preventDefault();
        currentIndex = currentIndex <= 0 ? cards.length - 1 : currentIndex - 1;
        cards[currentIndex].focus();
        break;
        
      case 'Enter':
      case ' ':
        if (currentFocus) {
          event.preventDefault();
          currentFocus.click();
        }
        break;
        
      case 'Escape':
        // Allow escape to close if there's a parent handler
        break;
    }
  }
  
  /**
   * Show the landing page
   */
  show() {
    if (!this.isVisible) {
      this.container.style.display = 'block';
      this.container.classList.add('tlw-landing--visible');
      this.isVisible = true;
      
      // Focus first card for keyboard navigation
      const firstCard = this.container.querySelector('.tlw-card');
      if (firstCard) {
        firstCard.setAttribute('tabindex', '0');
        firstCard.focus();
      }
      
      // Animate in
      setTimeout(() => {
        this.container.classList.add('tlw-landing--animated');
      }, 10);
    }
  }
  
  /**
   * Hide the landing page
   */
  hide() {
    if (this.isVisible) {
      this.container.classList.remove('tlw-landing--visible', 'tlw-landing--animated');
      
      setTimeout(() => {
        this.container.style.display = 'none';
        this.isVisible = false;
      }, 300);
    }
  }
  
  /**
   * Update templates data
   */
  updateTemplates(templates) {
    this.templates = templates || [];
    
    // Update template card if needed
    const templatesCard = this.container.querySelector('[data-action="templates"]');
    if (templatesCard) {
      const description = templatesCard.querySelector('.tlw-card-description');
      if (description) {
        description.textContent = `${this.templates.length} pre-built scenarios available`;
      }
    }
  }
  
  /**
   * Destroy the landing page
   */
  destroy() {
    if (this.container) {
      this.container.innerHTML = '';
    }
    this.isVisible = false;
    this.templates = [];
  }
  
  /**
   * Check if landing page is visible
   */
  isShown() {
    return this.isVisible;
  }
}

// CSS styles for the landing page
export const LANDING_PAGE_STYLES = `
  .tlw-landing {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 2rem 1rem;
    max-width: 600px;
    margin: 0 auto;
    min-height: 400px;
    opacity: 0;
    transform: translateY(20px);
    transition: opacity 0.3s ease, transform 0.3s ease;
  }
  
  .tlw-landing--visible {
    opacity: 1;
    transform: translateY(0);
  }
  
  .tlw-landing--animated .tlw-card {
    animation: tlw-card-enter 0.4s ease forwards;
  }
  
  .tlw-landing-header {
    text-align: center;
    margin-bottom: 2rem;
  }
  
  .tlw-branding {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 1rem;
    gap: 0.5rem;
  }
  
  .tlw-logo {
    font-size: 2rem;
  }
  
  .tlw-title {
    font-size: 2rem;
    font-weight: 700;
    color: #1a1a1a;
    margin: 0;
  }
  
  .tlw-subtitle {
    font-size: 1.5rem;
    font-weight: 600;
    color: #333;
    margin: 0 0 0.5rem 0;
  }
  
  .tlw-description {
    color: #666;
    font-size: 1rem;
    margin: 0;
  }
  
  .tlw-cards {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 1rem;
    width: 100%;
    margin-bottom: 2rem;
  }
  
  .tlw-card {
    display: flex;
    align-items: center;
    padding: 1.5rem;
    border-radius: 12px;
    background: #fff;
    border: 2px solid #e5e5e5;
    cursor: pointer;
    transition: all 0.3s ease;
    position: relative;
    opacity: 0;
    transform: translateY(20px);
  }
  
  .tlw-card:hover,
  .tlw-card:focus {
    border-color: #007bff;
    box-shadow: 0 4px 12px rgba(0, 123, 255, 0.15);
    transform: translateY(-2px);
  }
  
  .tlw-card--clicked {
    transform: translateY(0) scale(0.98);
  }
  
  .tlw-card--blue:hover {
    border-color: #007bff;
    box-shadow: 0 4px 12px rgba(0, 123, 255, 0.15);
  }
  
  .tlw-card--green:hover {
    border-color: #28a745;
    box-shadow: 0 4px 12px rgba(40, 167, 69, 0.15);
  }
  
  .tlw-card--purple:hover {
    border-color: #6f42c1;
    box-shadow: 0 4px 12px rgba(111, 66, 193, 0.15);
  }
  
  .tlw-card--orange:hover {
    border-color: #fd7e14;
    box-shadow: 0 4px 12px rgba(253, 126, 20, 0.15);
  }
  
  .tlw-card-icon {
    font-size: 2rem;
    margin-right: 1rem;
    flex-shrink: 0;
  }
  
  .tlw-card-content {
    flex: 1;
  }
  
  .tlw-card-title {
    font-size: 1.1rem;
    font-weight: 600;
    color: #1a1a1a;
    margin: 0 0 0.5rem 0;
  }
  
  .tlw-card-description {
    color: #666;
    font-size: 0.9rem;
    margin: 0 0 0.25rem 0;
    line-height: 1.4;
  }
  
  .tlw-card-subtitle {
    color: #999;
    font-size: 0.8rem;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.5px;
  }
  
  .tlw-card-arrow {
    font-size: 1.2rem;
    color: #999;
    margin-left: 1rem;
    transition: transform 0.3s ease;
  }
  
  .tlw-card:hover .tlw-card-arrow {
    transform: translateX(4px);
    color: #666;
  }
  
  .tlw-landing-footer {
    text-align: center;
    color: #999;
    font-size: 0.9rem;
  }
  
  .tlw-help-text {
    margin: 0 0 1rem 0;
  }
  
  .tlw-help-link {
    color: #007bff;
    text-decoration: none;
  }
  
  .tlw-help-link:hover {
    text-decoration: underline;
  }
  
  .tlw-version {
    font-size: 0.8rem;
    color: #bbb;
  }
  
  @keyframes tlw-card-enter {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  .tlw-card:nth-child(1) { animation-delay: 0.1s; }
  .tlw-card:nth-child(2) { animation-delay: 0.2s; }
  .tlw-card:nth-child(3) { animation-delay: 0.3s; }
  .tlw-card:nth-child(4) { animation-delay: 0.4s; }
  
  /* Mobile responsiveness */
  @media (max-width: 640px) {
    .tlw-landing {
      padding: 1rem 0.5rem;
    }
    
    .tlw-cards {
      grid-template-columns: 1fr;
      gap: 0.75rem;
    }
    
    .tlw-card {
      padding: 1.25rem;
    }
    
    .tlw-title {
      font-size: 1.5rem;
    }
    
    .tlw-subtitle {
      font-size: 1.25rem;
    }
  }
`;

export default LandingPage;