/**
 * Scenario Wizard Modal
 * Step-by-step guided scenario creation
 */

export class ScenarioWizard {
  constructor(options = {}) {
    this.options = {
      onComplete: options.onComplete || (() => {}),
      onClose: options.onClose || (() => {}),
      showProgress: options.showProgress !== false,
      ...options
    };
    
    this.currentStep = 0;
    this.answers = {};
    this.isVisible = false;
    
    this.modal = null;
    this.backdrop = null;
    
    this.steps = this.getWizardSteps();
    
    this.init();
  }
  
  /**
   * Initialize the wizard
   */
  init() {
    this.createModal();
  }
  
  /**
   * Get wizard step definitions
   */
  getWizardSteps() {
    return [
      {
        id: 'pattern',
        title: 'Trading Pattern',
        subtitle: 'What pattern are you looking for?',
        type: 'choice',
        options: [
          { value: 'breakout', label: 'Breakout', description: 'Price breaking above resistance' },
          { value: 'reversal', label: 'Reversal', description: 'Price changing direction' },
          { value: 'momentum', label: 'Momentum', description: 'Continued price movement' },
          { value: 'consolidation', label: 'Consolidation', description: 'Sideways price action' },
          { value: 'gap', label: 'Gap', description: 'Price gaps up or down' },
          { value: 'custom', label: 'Custom Pattern', description: 'I\'ll describe it myself' }
        ]
      },
      {
        id: 'direction',
        title: 'Direction',
        subtitle: 'What direction are you interested in?',
        type: 'choice',
        options: [
          { value: 'bullish', label: 'Bullish', description: 'Looking for upward movement' },
          { value: 'bearish', label: 'Bearish', description: 'Looking for downward movement' },
          { value: 'both', label: 'Both Directions', description: 'Either up or down' }
        ]
      },
      {
        id: 'symbols',
        title: 'Assets',
        subtitle: 'Which symbols/assets do you want to analyze?',
        type: 'input',
        inputType: 'text',
        placeholder: 'e.g., AAPL, MSFT, SPY or "tech stocks" or "S&P 500"',
        validation: {
          required: true,
          minLength: 1
        }
      },
      {
        id: 'timeframe',
        title: 'Timeframe',
        subtitle: 'What timeframe should we analyze?',
        type: 'choice',
        options: [
          { value: '1min', label: '1 Minute', description: 'Intraday scalping' },
          { value: '5min', label: '5 Minutes', description: 'Short-term trading' },
          { value: '15min', label: '15 Minutes', description: 'Day trading' },
          { value: '1hour', label: '1 Hour', description: 'Swing trading setup' },
          { value: '4hour', label: '4 Hours', description: 'Position trading' },
          { value: '1day', label: 'Daily', description: 'Long-term analysis' },
          { value: 'weekly', label: 'Weekly', description: 'Major trend analysis' }
        ]
      },
      {
        id: 'indicators',
        title: 'Technical Indicators',
        subtitle: 'Which indicators would you like to include?',
        type: 'multi-choice',
        optional: true,
        options: [
          { value: 'volume', label: 'Volume', description: 'Trading volume analysis' },
          { value: 'rsi', label: 'RSI', description: 'Relative Strength Index' },
          { value: 'macd', label: 'MACD', description: 'Moving Average Convergence Divergence' },
          { value: 'bollinger', label: 'Bollinger Bands', description: 'Volatility bands' },
          { value: 'sma', label: 'Simple MA', description: 'Simple Moving Averages' },
          { value: 'ema', label: 'Exponential MA', description: 'Exponential Moving Averages' },
          { value: 'stochastic', label: 'Stochastic', description: 'Momentum oscillator' },
          { value: 'atr', label: 'ATR', description: 'Average True Range' }
        ]
      },
      {
        id: 'conditions',
        title: 'Additional Conditions',
        subtitle: 'Any specific conditions or requirements?',
        type: 'input',
        inputType: 'textarea',
        placeholder: 'e.g., "minimum volume of 1M shares", "price above $50", "earnings within 30 days"...',
        optional: true
      }
    ];
  }
  
  /**
   * Create modal DOM structure
   */
  createModal() {
    this.backdrop = document.createElement('div');
    this.backdrop.className = 'tlw-modal-backdrop tlw-wizard-backdrop';
    this.backdrop.addEventListener('click', (e) => {
      if (e.target === this.backdrop) {
        this.hide();
      }
    });
    
    this.modal = document.createElement('div');
    this.modal.className = 'tlw-wizard-modal';
    this.modal.innerHTML = this.render();
    
    this.backdrop.appendChild(this.modal);
    document.body.appendChild(this.backdrop);
    
    this.attachEventListeners();
  }
  
  /**
   * Render modal HTML
   */
  render() {
    return `
      <div class="tlw-wizard-header">
        <div class="tlw-wizard-title">
          <span class="tlw-wizard-icon"></span>
          <h2>Scenario Builder</h2>
        </div>
        <button class="tlw-modal-close" data-action="close" aria-label="Close">
          <span>&times;</span>
        </button>
      </div>
      
      ${this.options.showProgress ? this.renderProgress() : ''}
      
      <div class="tlw-wizard-body">
        ${this.renderStep()}
      </div>
      
      <div class="tlw-wizard-footer">
        ${this.renderNavigation()}
      </div>
    `;
  }
  
  /**
   * Render progress indicator
   */
  renderProgress() {
    const progress = ((this.currentStep + 1) / this.steps.length) * 100;
    
    return `
      <div class="tlw-wizard-progress">
        <div class="tlw-progress-bar">
          <div class="tlw-progress-fill" style="width: ${progress}%"></div>
        </div>
        <div class="tlw-progress-text">
          Step ${this.currentStep + 1} of ${this.steps.length}
        </div>
      </div>
    `;
  }
  
  /**
   * Render current step
   */
  renderStep() {
    const step = this.steps[this.currentStep];
    
    if (!step) return '';
    
    return `
      <div class="tlw-wizard-step" data-step="${step.id}">
        <div class="tlw-step-header">
          <h3 class="tlw-step-title">${step.title}</h3>
          <p class="tlw-step-subtitle">${step.subtitle}</p>
          ${step.optional ? '<span class="tlw-optional-badge">Optional</span>' : ''}
        </div>
        
        <div class="tlw-step-content">
          ${this.renderStepInput(step)}
        </div>
      </div>
    `;
  }
  
  /**
   * Render step input based on type
   */
  renderStepInput(step) {
    switch(step.type) {
      case 'choice':
        return this.renderChoiceInput(step);
      case 'multi-choice':
        return this.renderMultiChoiceInput(step);
      case 'input':
        return this.renderTextInput(step);
      default:
        return '';
    }
  }
  
  /**
   * Render single choice input
   */
  renderChoiceInput(step) {
    return `
      <div class="tlw-choice-grid">
        ${step.options.map(option => `
          <div class="tlw-choice-option ${this.answers[step.id] === option.value ? 'selected' : ''}" 
               data-value="${option.value}" 
               data-action="select-choice">
            <div class="tlw-choice-label">${option.label}</div>
            <div class="tlw-choice-description">${option.description}</div>
          </div>
        `).join('')}
      </div>
    `;
  }
  
  /**
   * Render multi-choice input
   */
  renderMultiChoiceInput(step) {
    const selectedValues = this.answers[step.id] || [];
    
    return `
      <div class="tlw-multi-choice-grid">
        ${step.options.map(option => `
          <label class="tlw-checkbox-option ${selectedValues.includes(option.value) ? 'selected' : ''}">
            <input type="checkbox" 
                   value="${option.value}" 
                   data-action="toggle-choice"
                   ${selectedValues.includes(option.value) ? 'checked' : ''}>
            <div class="tlw-checkbox-content">
              <div class="tlw-checkbox-label">${option.label}</div>
              <div class="tlw-checkbox-description">${option.description}</div>
            </div>
            <div class="tlw-checkbox-indicator"></div>
          </label>
        `).join('')}
      </div>
    `;
  }
  
  /**
   * Render text input
   */
  renderTextInput(step) {
    const value = this.answers[step.id] || '';
    
    if (step.inputType === 'textarea') {
      return `
        <textarea 
          class="tlw-wizard-textarea" 
          placeholder="${step.placeholder || ''}"
          data-action="input-change"
          rows="4"
        >${value}</textarea>
      `;
    }
    
    return `
      <input 
        type="text" 
        class="tlw-wizard-input" 
        placeholder="${step.placeholder || ''}"
        value="${value}"
        data-action="input-change"
      >
    `;
  }
  
  /**
   * Render navigation buttons
   */
  renderNavigation() {
    const isFirstStep = this.currentStep === 0;
    const isLastStep = this.currentStep === this.steps.length - 1;
    const canProceed = this.canProceedFromCurrentStep();
    
    return `
      <div class="tlw-wizard-nav">
        <div class="tlw-nav-left">
          ${!isFirstStep ? `
            <button class="tlw-btn tlw-btn--secondary" data-action="prev-step">
               Previous
            </button>
          ` : ''}
        </div>
        
        <div class="tlw-nav-right">
          <button class="tlw-btn tlw-btn--ghost" data-action="close">
            Cancel
          </button>
          
          ${isLastStep ? `
            <button class="tlw-btn tlw-btn--success ${canProceed ? '' : 'disabled'}" 
                    data-action="complete" 
                    ${canProceed ? '' : 'disabled'}>
              Create Scenario
            </button>
          ` : `
            <button class="tlw-btn tlw-btn--primary ${canProceed ? '' : 'disabled'}" 
                    data-action="next-step"
                    ${canProceed ? '' : 'disabled'}>
              Next 
            </button>
          `}
        </div>
      </div>
    `;
  }
  
  /**
   * Attach event listeners
   */
  attachEventListeners() {
    // Close button
    this.modal.querySelectorAll('[data-action="close"]').forEach(btn => {
      btn.addEventListener('click', () => this.hide());
    });
    
    // Navigation
    this.modal.querySelector('[data-action="next-step"]')?.addEventListener('click', () => {
      if (this.canProceedFromCurrentStep()) {
        this.nextStep();
      }
    });
    
    this.modal.querySelector('[data-action="prev-step"]')?.addEventListener('click', () => {
      this.prevStep();
    });
    
    this.modal.querySelector('[data-action="complete"]')?.addEventListener('click', () => {
      if (this.canProceedFromCurrentStep()) {
        this.complete();
      }
    });
    
    // Choice selection
    this.modal.querySelectorAll('[data-action="select-choice"]').forEach(option => {
      option.addEventListener('click', (e) => {
        const value = e.currentTarget.dataset.value;
        this.handleChoiceSelection(value);
      });
    });
    
    // Multi-choice toggles
    this.modal.querySelectorAll('[data-action="toggle-choice"]').forEach(checkbox => {
      checkbox.addEventListener('change', (e) => {
        this.handleMultiChoiceToggle(e.target.value, e.target.checked);
      });
    });
    
    // Text inputs
    this.modal.querySelectorAll('[data-action="input-change"]').forEach(input => {
      input.addEventListener('input', (e) => {
        this.handleInputChange(e.target.value);
      });
    });
    
    // Keyboard navigation
    this.modal.addEventListener('keydown', (e) => this.handleKeyNavigation(e));
    
    // Prevent modal from closing when clicking inside
    this.modal.addEventListener('click', (e) => {
      e.stopPropagation();
    });
  }
  
  /**
   * Handle choice selection
   */
  handleChoiceSelection(value) {
    const step = this.steps[this.currentStep];
    this.answers[step.id] = value;
    
    // Update UI
    this.modal.querySelectorAll('.tlw-choice-option').forEach(option => {
      option.classList.toggle('selected', option.dataset.value === value);
    });
    
    this.updateNavigation();
    
    // Auto-advance for single choices (with slight delay for UX)
    if (step.type === 'choice') {
      setTimeout(() => {
        if (this.currentStep < this.steps.length - 1) {
          this.nextStep();
        }
      }, 500);
    }
  }
  
  /**
   * Handle multi-choice toggle
   */
  handleMultiChoiceToggle(value, checked) {
    const step = this.steps[this.currentStep];
    
    if (!this.answers[step.id]) {
      this.answers[step.id] = [];
    }
    
    if (checked) {
      if (!this.answers[step.id].includes(value)) {
        this.answers[step.id].push(value);
      }
    } else {
      this.answers[step.id] = this.answers[step.id].filter(v => v !== value);
    }
    
    // Update UI
    const option = this.modal.querySelector(`input[value="${value}"]`).closest('.tlw-checkbox-option');
    option.classList.toggle('selected', checked);
    
    this.updateNavigation();
  }
  
  /**
   * Handle text input change
   */
  handleInputChange(value) {
    const step = this.steps[this.currentStep];
    this.answers[step.id] = value.trim();
    
    this.updateNavigation();
  }
  
  /**
   * Handle keyboard navigation
   */
  handleKeyNavigation(e) {
    switch(e.key) {
      case 'Escape':
        this.hide();
        break;
        
      case 'Enter':
        if (!e.target.matches('textarea')) {
          e.preventDefault();
          if (this.canProceedFromCurrentStep()) {
            if (this.currentStep === this.steps.length - 1) {
              this.complete();
            } else {
              this.nextStep();
            }
          }
        }
        break;
    }
  }
  
  /**
   * Go to next step
   */
  nextStep() {
    if (this.currentStep < this.steps.length - 1) {
      this.currentStep++;
      this.updateModal();
    }
  }
  
  /**
   * Go to previous step
   */
  prevStep() {
    if (this.currentStep > 0) {
      this.currentStep--;
      this.updateModal();
    }
  }
  
  /**
   * Complete the wizard
   */
  complete() {
    const prompt = this.generatePrompt();
    
    this.options.onComplete({
      type: 'wizard',
      answers: this.answers,
      prompt: prompt,
      journeyType: 'interactive'
    });
    
    this.hide();
  }
  
  /**
   * Generate natural language prompt from answers
   */
  generatePrompt() {
    const parts = [];
    
    // Pattern and direction
    if (this.answers.pattern && this.answers.direction) {
      const patternMap = {
        breakout: 'breakout patterns',
        reversal: 'reversal patterns', 
        momentum: 'momentum continuation',
        consolidation: 'consolidation patterns',
        gap: 'gap patterns',
        custom: 'specific patterns'
      };
      
      const directionMap = {
        bullish: 'bullish',
        bearish: 'bearish',
        both: 'both bullish and bearish'
      };
      
      parts.push(`I'm looking for ${directionMap[this.answers.direction]} ${patternMap[this.answers.pattern] || this.answers.pattern}`);
    }
    
    // Symbols
    if (this.answers.symbols) {
      parts.push(`in ${this.answers.symbols}`);
    }
    
    // Timeframe
    if (this.answers.timeframe) {
      parts.push(`using ${this.answers.timeframe} timeframe`);
    }
    
    // Indicators
    if (this.answers.indicators && this.answers.indicators.length > 0) {
      const indicatorNames = {
        volume: 'volume',
        rsi: 'RSI',
        macd: 'MACD',
        bollinger: 'Bollinger Bands',
        sma: 'simple moving averages',
        ema: 'exponential moving averages',
        stochastic: 'stochastic oscillator',
        atr: 'ATR'
      };
      
      const indicators = this.answers.indicators.map(ind => indicatorNames[ind] || ind).join(', ');
      parts.push(`with ${indicators} confirmation`);
    }
    
    // Additional conditions
    if (this.answers.conditions) {
      parts.push(`Additional requirements: ${this.answers.conditions}`);
    }
    
    let prompt = parts.join(' ');
    
    // Make it more natural
    prompt = prompt.replace(/^I'm looking for/, 'I want to find');
    prompt += '. Please help me create a testable trading scenario for this.';
    
    return prompt;
  }
  
  /**
   * Check if we can proceed from current step
   */
  canProceedFromCurrentStep() {
    const step = this.steps[this.currentStep];
    
    if (step.optional) {
      return true;
    }
    
    const answer = this.answers[step.id];
    
    if (!answer) {
      return false;
    }
    
    // Validate based on step type
    switch(step.type) {
      case 'choice':
        return !!answer;
        
      case 'multi-choice':
        return true; // Multi-choice is always valid (can be empty)
        
      case 'input':
        if (step.validation?.required && (!answer || answer.length === 0)) {
          return false;
        }
        if (step.validation?.minLength && answer.length < step.validation.minLength) {
          return false;
        }
        return true;
        
      default:
        return !!answer;
    }
  }
  
  /**
   * Update the entire modal
   */
  updateModal() {
    this.modal.innerHTML = this.render();
    this.attachEventListeners();
    
    // Focus appropriate element
    setTimeout(() => {
      const firstInput = this.modal.querySelector('input, textarea, .tlw-choice-option');
      if (firstInput) {
        firstInput.focus();
      }
    }, 100);
  }
  
  /**
   * Update navigation buttons only
   */
  updateNavigation() {
    const footer = this.modal.querySelector('.tlw-wizard-footer');
    if (footer) {
      footer.innerHTML = this.renderNavigation();
      
      // Re-attach navigation event listeners
      footer.querySelector('[data-action="next-step"]')?.addEventListener('click', () => {
        if (this.canProceedFromCurrentStep()) {
          this.nextStep();
        }
      });
      
      footer.querySelector('[data-action="prev-step"]')?.addEventListener('click', () => {
        this.prevStep();
      });
      
      footer.querySelector('[data-action="complete"]')?.addEventListener('click', () => {
        if (this.canProceedFromCurrentStep()) {
          this.complete();
        }
      });
      
      footer.querySelector('[data-action="close"]')?.addEventListener('click', () => {
        this.hide();
      });
    }
  }
  
  /**
   * Show the wizard
   */
  show() {
    if (!this.isVisible) {
      this.backdrop.style.display = 'flex';
      document.body.classList.add('tlw-modal-open');
      
      setTimeout(() => {
        this.backdrop.classList.add('tlw-modal-backdrop--visible');
        this.modal.classList.add('tlw-wizard-modal--visible');
      }, 10);
      
      this.isVisible = true;
      
      // Focus first interactive element
      setTimeout(() => {
        const firstInput = this.modal.querySelector('input, textarea, .tlw-choice-option');
        if (firstInput) {
          firstInput.focus();
        }
      }, 100);
    }
  }
  
  /**
   * Hide the wizard
   */
  hide() {
    if (this.isVisible) {
      this.backdrop.classList.remove('tlw-modal-backdrop--visible');
      this.modal.classList.remove('tlw-wizard-modal--visible');
      
      setTimeout(() => {
        this.backdrop.style.display = 'none';
        document.body.classList.remove('tlw-modal-open');
        this.isVisible = false;
        
        this.options.onClose();
      }, 300);
    }
  }
  
  /**
   * Reset wizard to beginning
   */
  reset() {
    this.currentStep = 0;
    this.answers = {};
    
    if (this.isVisible) {
      this.updateModal();
    }
  }
  
  /**
   * Destroy the wizard
   */
  destroy() {
    if (this.backdrop && this.backdrop.parentNode) {
      this.backdrop.parentNode.removeChild(this.backdrop);
    }
    
    document.body.classList.remove('tlw-modal-open');
    this.isVisible = false;
  }
}

export default ScenarioWizard;