/**
 * Widget Factory
 * Creates different types of widgets based on configuration
 */

import { EnhancedChatWidget } from '../components/enhanced-chat-widget';

export class WidgetFactory {
  static widgetTypes = {
    'chat': EnhancedChatWidget,
    'tradrlab-chat': EnhancedChatWidget
  };
  
  /**
   * Create a widget instance
   * @param {string} type - Widget type
   * @param {HTMLElement} targetElement - Target DOM element
   * @param {ConfigManager} configManager - Configuration manager instance
   * @returns {Object} Widget instance
   */
  static create(type, targetElement, configManager) {
    // Get widget class, falling back to EnhancedChatWidget for custom types
    const WidgetClass = this.widgetTypes[type] || this.widgetTypes['chat'];
    
    if (!WidgetClass) {
      throw new Error(`WidgetFactory: No widget implementation available`);
    }
    
    // For custom types, pass the type in config so widget can identify itself
    if (!this.widgetTypes[type]) {
      configManager.set('customWidgetType', type);
    }
    
    return new WidgetClass(targetElement, configManager);
  }
  
  /**
   * Register a custom widget type
   * @param {string} type - Widget type name
   * @param {Class} WidgetClass - Widget class constructor
   */
  static registerType(type, WidgetClass) {
    if (this.widgetTypes[type]) {
      console.warn(`WidgetFactory: Widget type "${type}" already exists. Overwriting.`);
    }
    
    this.widgetTypes[type] = WidgetClass;
  }
  
  /**
   * Get available widget types
   * @returns {Array} List of widget type names
   */
  static getAvailableTypes() {
    return Object.keys(this.widgetTypes);
  }
}