/**
 * Widget state machine states
 */
export const WIDGET_STATES = {
  INITIALIZING: 'initializing',
  LANDING: 'landing',
  CHATTING: 'chatting',
  GENERATING: 'generating',
  EVALUATING: 'evaluating',
  ANALYZING: 'analyzing',
  ERROR: 'error',
  DISCONNECTED: 'disconnected'
};

/**
 * Get configuration value with fallback
 */
export function getConfig(key, fallback = null) {
  const configs = {
    capabilities: WIDGET_CAPABILITIES,
    apiFlags: API_FLAGS,
    milestones: MILESTONES,
    categories: TEMPLATE_CATEGORIES,
    settings: DEFAULT_SETTINGS,
    endpoints: ENDPOINTS,
    errors: ERROR_MESSAGES,
    success: SUCCESS_MESSAGES,
    states: WIDGET_STATES
  };
  
  const [section, property] = key.split('.');
  
  if (section && property && configs[section]) {
    return configs[section][property] || fallback;
  }
  
  return fallback;
}

/**
 * Merge API flags with request
 */
export function withApiFlags(request = {}) {
  return {
    ...request,
    ...API_FLAGS
  };
}

/**
 * Check if a message type is supported
 */
export function isMessageTypeSupported(messageType) {
  return WIDGET_CAPABILITIES.supportedMessageTypes.includes(messageType);
}

/**
 * Get milestone by ID or name
 */
export function getMilestone(identifier) {
  if (typeof identifier === 'number') {
    return MILESTONES.find(m => m.id === identifier);
  }
  
  return MILESTONES.find(m => 
    m.name.toLowerCase() === identifier.toLowerCase()
  );
}

/**
 * Export all configurations
 */
export default {
  WIDGET_CAPABILITIES,
  API_FLAGS,
  MILESTONES,
  TEMPLATE_CATEGORIES,
  DEFAULT_SETTINGS,
  ENDPOINTS,
  ERROR_MESSAGES,
  SUCCESS_MESSAGES,
  WIDGET_STATES,
  getConfig,
  withApiFlags,
  isMessageTypeSupported,
  getMilestone
};